import React, { useContext } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, InputAdornment, OutlinedInput, Stack, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function HistoryKeepDays() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.keepDays" });
    const { updateSetting, enableHistory, historyKeepDays } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,
        historyKeepDays: state.settings.historyKeepDays,
    }));

    const handleHistoryKeepDaysChange = async (event) => {
        await updateSetting('historyKeepDays', parseInt(event.target.value))
        await sendRuntimeMessage("update_setting", { key: "historyKeepDays", value: parseInt(event.target.value) })
    }


    return (

        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '100px' }} variant="outlined">
                    <OutlinedInput
                        disabled={!enableHistory}
                        value={historyKeepDays}
                        inputProps={{ itemType: 'number', max: 365, min: 1, step: 1 }}
                        endAdornment={<InputAdornment position="end">{t('days')}</InputAdornment>}
                        aria-describedby="outlined-weight-helper-text"
                        onChange={handleHistoryKeepDaysChange}
                    />
                </FormControl>
            </Grid>
        </Grid>

    )
}