import React from 'react';
import Box from "@mui/material/Box";
import { Divider, Stack, Typography } from "@mui/material";
import Grid from "@mui/material/Grid";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import AutoDiscardable from "./AutoDiscardable";
import PlayingProgressBar from "./PlayingProgressBar";
import ReadingProgressBar from "./ReadingProgressBar";
import TabSwitchPip from "./TabSwitchPip";
import { useParams, Link } from "react-router-dom";
import IconButton from "@mui/material/IconButton";
import { ArrowBack } from "@mui/icons-material";
import BrowserSettings from './BrowserSettings';
import { useEffect } from 'react';
import { siteStore } from '../../../../core/stores/siteStore';
import LinkSettings from './LinkSettings';
import BackgroundColor from './BackgroundColor';
import FontColor from './FontColor';
import PipRestrictionAction from './PipRestrictionAction';
import AutoEnterPictureInPicture from './AutoEnterPictureInPicture';
import CaptionSetting from './CaptionSetting';
import PageKeepAlive from './PageKeepAlive';
import AutoJoinGroup from './AutoJoinGroup';
import Incognito from './Incognito';
import LinkOpenMethod from './LinkOpenMethod';
import RefreshInterval from './RefreshInterval';


export default function SiteConfig({ hostname }) {

    const params = useParams();

    hostname = hostname || params.hostname
    useTranslationContext('options', { keyPrefix: 'settings.site' });

    const { siteConfig, setSiteConfig } = siteStore((state) => ({
        siteConfig: state.sites.find(site => site.hostname === hostname),
        setSiteConfig: state.setSiteConfig
    }))

    if (hostname && !siteConfig) {
        setSiteConfig(hostname, { hostname })
    }

    useEffect(() => {


    }, [hostname])
    const { t } = useTranslationContext('options', { keyPrefix: 'settings.site' });

    return (
        <Box>
            <Grid container mb={3}>

                <Stack direction="row" justifyContent="center" alignItems="flex-start" spacing={1}>
                    <Link to={`/sites`}><IconButton><ArrowBack /></IconButton></Link>
                    <Typography variant="h1">{hostname}</Typography>
                </Stack>
                {/*<Grid item xs={2}>*/}
                {/*    /!* <Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings')}</Button> *!/*/}
                {/*</Grid>*/}
            </Grid>
            {/*<Divider />*/}
            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                <AutoDiscardable hostname={hostname} />
                <PageKeepAlive hostname={hostname} />
                <Incognito hostname={hostname} />
                <AutoJoinGroup hostname={hostname} />
                <ReadingProgressBar hostname={hostname} />
                <PlayingProgressBar hostname={hostname} />
                <TabSwitchPip hostname={hostname} />
                {/* <ClearHistoyAfterCLose hostname={hostname} /> */}
                <BrowserSettings hostname={hostname} />
                <LinkOpenMethod hostname={hostname} />
                <RefreshInterval hostname={hostname} />
                <BackgroundColor hostname={hostname} />
                <FontColor hostname={hostname} />

            </Stack>
            <Stack spacing={2}>
                <Divider textAlign="center" sx={{ color: "error.main", padding: "10px", my: 3 }}>{t('premiumEditionOnly', { keyPrefix: "settings" })}</Divider>

                <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <PipRestrictionAction hostname={hostname} />
                    <Divider sx={{ my: 1 }} />
                    <AutoEnterPictureInPicture hostname={hostname} />
                    <Divider sx={{ my: 1 }} />
                    <CaptionSetting hostname={hostname} />
                </Box>
            </Stack>

        </Box>
    )
}