import React, { useState, useEffect } from "react";
import userStore from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { colord, getFormat, getGradientColor, random } from "../../../../utils/colord";
import { Grid, Input, Stack, Typography, Box, Popover, IconButton, Tooltip } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { RgbaStringColorPicker } from "react-colorful";
import ClearIcon from '@mui/icons-material/Clear';
import Cached from '@mui/icons-material/Cached';

const commonColors = [
    "#1abc9c", "#2ecc71", "#3498db", "#9b59b6", "#34495e",
    "#16a085", "#27ae60", "#2980b9", "#8e44ad", "#2c3e50",
    "#f1c40f", "#e67e22", "#e74c3c", "#ecf0f1", "#95a5a6"
];

export default function BackgroundColor() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.beautify.backgroundColor" });

    const { updateSetting, backgroundColor, backgroundGradient, backgroundGradientDeg, backgroundImageUrl } = userStore((state) => ({
        updateSetting: state.updateSetting,
        backgroundColor: state.settings.backgroundColor,
        backgroundGradient: state.settings.backgroundGradient,
        backgroundGradientDeg: state.settings.backgroundGradientDeg,
        backgroundImageUrl: state.settings.backgroundImageUrl,
    }));

    const [anchorEl, setAnchorEl] = useState(null);
    const [localBackgroundColor, setLocalBackgroundColor] = useState(backgroundColor);

    useEffect(() => {
        setLocalBackgroundColor(backgroundColor);
    }, [backgroundColor]);

    const setFontColor = async (bgColor) => {
        let fontColor = colord(bgColor).isLight() ? "#333" : "#fff";
        if (bgColor === 'transparent') {
            fontColor = 'inherit';
        }
        await updateSetting("fontColor", fontColor);
        await sendRuntimeMessage("update_setting", { key: "fontColor", value: fontColor });
    };

    const handleInputChange = async (color) => {
        setLocalBackgroundColor(color);
        if (!getFormat(color)) return;
        await updateSetting("backgroundColor", color);
        await sendRuntimeMessage("update_setting", { key: "backgroundColor", value: color });
        const finalColor = backgroundGradient ? getGradientColor(color, backgroundGradientDeg) : color;
        await updateSetting("background", finalColor);
        await sendRuntimeMessage("update_setting", { key: "background", value: finalColor });
        setFontColor(color);
    };

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleClear = async () => {
        const color = "transparent";
        setLocalBackgroundColor(color);
        await updateSetting("backgroundColor", color);
        await sendRuntimeMessage("update_setting", { key: "backgroundColor", value: color });
        await updateSetting("background", color);
        await sendRuntimeMessage("update_setting", { key: "background", value: color });
        await setFontColor(color);
    };

    const handleCommonColorClick = async (color) => {
        setLocalBackgroundColor(color);
        await updateSetting("backgroundColor", color);
        await sendRuntimeMessage("update_setting", { key: "backgroundColor", value: color });
        const finalColor = backgroundGradient ? getGradientColor(color, backgroundGradientDeg) : color;
        await updateSetting("background", finalColor);
        await sendRuntimeMessage("update_setting", { key: "background", value: finalColor });
        await setFontColor(color);
    };

    const getRandomColor = () => {
        const k = Math.random();
        if (k > 0.75) return random().toHslString();
        if (k > 0.5) return random().toRgbString();
        return random().toHex();
    };

    const handleRandomClick = async () => {
        const color = getRandomColor();
        handleInputChange(color);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'color-picker-popover' : undefined;

    const isDisabled = backgroundImageUrl !== "";

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4} sx={{ cursor: isDisabled ? 'not-allowed' : 'pointer', }}>
                <Tooltip title={isDisabled ? t("backgroundImageConfigured") : ""}>
                    <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">

                        <Box
                            aria-describedby={id}
                            sx={{
                                width: 24,
                                height: 24,
                                backgroundColor: backgroundColor,
                                border: '1px solid #000',
                                pointerEvents: isDisabled ? 'none' : 'auto'
                            }}
                            onClick={handleClick}
                        />

                        <Popover
                            id={id}
                            open={open}
                            anchorEl={anchorEl}
                            onClose={handleClose}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            slotProps={{
                                paper: {
                                    sx: {
                                        padding: 2,
                                        overflow: 'hidden'
                                    }
                                }
                            }}
                        >
                            <RgbaStringColorPicker color={colord(localBackgroundColor).toRgbString()} onChange={handleInputChange} />
                            <Box sx={{ display: 'flex', flexWrap: 'wrap', mt: 2 }}>
                                {commonColors.map((color) => (
                                    <Box
                                        key={color}
                                        sx={{
                                            width: 10,
                                            height: 24,
                                            backgroundColor: color,
                                            border: '1px solid #000',
                                            cursor: 'pointer',
                                            margin: '2px',
                                            flex: '0 0 calc(20% - 4px)' // 5 items per row
                                        }}
                                        onClick={() => handleCommonColorClick(color)}
                                    />
                                ))}
                            </Box>
                        </Popover>
                        <Input
                            type="text"
                            value={localBackgroundColor}
                            onChange={(e) => handleInputChange(e.target.value)}
                            autoFocus
                            disabled={isDisabled}
                            endAdornment={
                                <>
                                    <Tooltip title={t("random")} >
                                        <span>
                                            <IconButton onClick={handleRandomClick} disabled={isDisabled}>
                                                <Cached />
                                            </IconButton>
                                        </span>
                                    </Tooltip>
                                    <Tooltip title={t("clear")}>
                                        <span>
                                            <IconButton onClick={handleClear} disabled={isDisabled}>
                                                <ClearIcon />
                                            </IconButton>
                                        </span>
                                    </Tooltip>
                                </>
                            }
                        />
                    </Stack>
                </Tooltip>
            </Grid>
        </Grid>
    );
}