import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Typography, ToggleButton, ToggleButtonGroup } from "@mui/material";
import { VerticalAlignBottom } from "@mui/icons-material";
import { VerticalAlignTop } from "@mui/icons-material";
import { VisibilityOff } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function SearchBoxPosition() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.searchBoxPosition" });

    const { updateSetting, searchBoxPosition } = userStore((state) => ({
        updateSetting: state.updateSetting,
        searchBoxPosition: state.settings.searchBoxPosition
    }));


    const handleSearchBoxPositionChange = async (e, v) => {
        await updateSetting("searchBoxPosition", v)
        await sendRuntimeMessage("update_setting", { key: "searchBoxPosition", value: v })
    }

    return (
        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={searchBoxPosition} exclusive>
                    <ToggleButton value="top" onClick={handleSearchBoxPositionChange}>
                        <VerticalAlignTop /> <Typography variant="body1">{t("top")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="bottom" onClick={handleSearchBoxPositionChange}>
                        <VerticalAlignBottom />
                        <Typography variant="body1">{t("bottom")}</Typography>

                    </ToggleButton>
                    <ToggleButton value="hide" onClick={handleSearchBoxPositionChange}>
                        <VisibilityOff />
                        <Typography variant="body1">{t("hide")}</Typography>
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
        </Grid>

    )
}