import React, { useContext } from "react";
import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function EnableHistory() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.saveRecentlyTabs" });
    const { updateSetting, enableHistory } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,

    }));


    const handleEnableHistoryChange = async (event) => {
        await updateSetting('enableHistory', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "enableHistory", value: event.target.checked })
        if (!event.target.checked) {
            await updateSetting('activeTabIndex', 0)
            await sendRuntimeMessage("update_setting", { key: "activeTabIndex", value: 0 })
        }
    }
    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={enableHistory}
                    onChange={handleEnableHistoryChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>
    )
}