import React from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Slider, Stack, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function BackgroundImageFade() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.beautify.backgroundImageTransparency" });

    const { updateSetting, backgroundImageFade, backgroundImageUrl } = userStore((state) => ({
        updateSetting: state.updateSetting,
        backgroundImageFade: state.settings.backgroundImageFade,
        backgroundImageUrl: state.settings.backgroundImageUrl
    }));

    const handleOnChange = async (e, v) => {
        await updateSetting("backgroundImageFade", v);
    };

    const handleOnChangeCommitted = async (e, v) => {
        await sendRuntimeMessage("update_setting", { key: "backgroundImageFade", value: v });
    };

    const valueLabelFormat = (v) => `${Math.round(v * 100)}%`;

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
                    <Slider
                        size="small"
                        sx={{ width: "100%" }}
                        step={0.01}
                        min={0}
                        max={1}
                        value={backgroundImageFade}
                        color="error"
                        valueLabelFormat={valueLabelFormat}
                        valueLabelDisplay="auto"
                        onChange={handleOnChange}
                        onChangeCommitted={handleOnChangeCommitted}
                    />
                </Stack>
            </Grid>
        </Grid>
    );
}