import React, { useState } from 'react';
import {
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    TextField,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    Checkbox,
    Avatar,
    Typography,
    ListItemButton
} from '@mui/material';
import { v4 as uuidv4 } from 'uuid';
import tabStore from '../../../../core/stores/tabStore';
import { faviconURL } from '../../../../utils/helpers';
import { toast } from 'react-toastify';
import { useTranslationContext } from "../../../Providers/TranslationProvider"; import { sendRuntimeMessage } from '../../../../core/browers/runtime';
import { useEffect } from 'react';
import { ArrowRight } from '@mui/icons-material';

await tabStore.getState().initQuickAccess();

const AddQuickAccess = () => {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.quickAccess.selectFromHistory" });

    const { storedQuickAccess } = tabStore((state) => ({ storedQuickAccess: state.quickAccess }));
    const [open, setOpen] = useState(false);
    const [days, setDays] = useState(90);
    const [limit, setLimit] = useState(30);
    const [urlList, setUrlList] = useState([]);
    const [selectedUrls, setSelectedUrls] = useState([]);
    const [showList, setShowList] = useState(false);


    const handleOpen = () => setOpen(true);
    const handleClose = () => {
        setOpen(false);
        setShowList(false);
        setUrlList([]);
        setSelectedUrls([]);
    };

    const requestHistoryPermission = () => {
        return new Promise((resolve, reject) => {
            chrome.permissions.request({
                permissions: ['history']
            }, (granted) => {
                if (granted) {
                    resolve();
                } else {

                    reject(new Error(t('deniedMessage')));
                }
            });
        });
    };

    const handleSubmit = async () => {
        try {
            await requestHistoryPermission();
            const results = await getMostVisitedUrls(days, limit);
            setUrlList(results);
            setSelectedUrls(results.filter(result => storedQuickAccess.map(item => item.url).includes(result.url)));
            setShowList(true);

        } catch (error) {
            console.error('Error:', error);
            toast.error(error.message);
        }
    };

    const handleToggle = (value) => () => {
        const newChecked = selectedUrls.includes(value)
            ? selectedUrls.filter(url => url !== value)
            : [...selectedUrls, value];
        setSelectedUrls(newChecked);
    };

    const handleSelectAll = (event) => {
        setSelectedUrls(event.target.checked ? urlList : []);
    };

    const handleAddSelected = async () => {
        await sendRuntimeMessage("addQuickAccess", { items: selectedUrls });
        toast.success(t('addedSuccess', { count: selectedUrls.length }))
        handleClose();
    };

    return (
        <>
            <Button size='small' color='error' variant="text" endIcon={<ArrowRight />} onClick={handleOpen}>
                {t('label')}
            </Button>
            <Dialog open={open} fullWidth maxWidth="xs" onClose={(event, reason) => { if (reason !== 'backdropClick') { handleClose } }}>
                <DialogTitle variant='h3'>{t('label')}</DialogTitle>
                <DialogContent>
                    {!showList ? (
                        <>
                            <TextField
                                color='error'
                                autoFocus
                                margin="dense"
                                id="days"
                                label={t('numberOfDays')}
                                type="text"
                                fullWidth
                                variant="standard"
                                value={days}
                                onChange={(e) => setDays(Number(e.target.value))}
                            />
                            <TextField
                                color='error'
                                margin="dense"
                                id="limit"
                                label={t('numberOfUrls')}
                                type="text"
                                fullWidth
                                variant="standard"
                                value={limit}
                                onChange={(e) => setLimit(Number(e.target.value))}
                            />
                        </>
                    ) : (
                        <>
                            <Typography variant="body1" gutterBottom>
                                {t('selectItemsToAddToQuickAccess')}
                            </Typography>
                            <List sx={{ width: '100%', bgcolor: 'background.paper', flexWrap: 'wrap' }}>
                                <ListItem>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            checked={selectedUrls.length === urlList.length}
                                            indeterminate={selectedUrls.length > 0 && selectedUrls.length < urlList.length}
                                            onChange={handleSelectAll}
                                        />
                                    </ListItemIcon>
                                    <ListItemText primary={t('selectAll')} />
                                </ListItem>
                                {urlList.map((value) => {
                                    const labelId = `checkbox-list-label-${value.url}`;

                                    return (
                                        <ListItemButton disabled={Boolean(storedQuickAccess.find(s => s.url === value.url))} key={value.url} dense onClick={handleToggle(value)}>
                                            <ListItemIcon>
                                                <Checkbox
                                                    edge="start"
                                                    color='error'
                                                    checked={selectedUrls.includes(value)}
                                                    tabIndex={-1}
                                                    disableRipple
                                                    inputProps={{ 'aria-labelledby': labelId }}
                                                />
                                            </ListItemIcon>
                                            <ListItemIcon>
                                                <Avatar sx={{ width: 24, height: 24 }} src={value.favIconUrl} alt={value.title} />
                                            </ListItemIcon>
                                            <ListItemText

                                                id={labelId}
                                                primary={value.title}
                                                secondary={value.url}
                                                secondaryTypographyProps={{ noWrap: true }}
                                            />
                                        </ListItemButton>
                                    );
                                })}
                            </List>
                        </>
                    )}
                </DialogContent>
                <DialogActions>
                    <Button color='error' variant='text' onClick={handleClose}>{t('cancel')}</Button>
                    {!showList ? (
                        <Button color='error' variant='contained' onClick={handleSubmit}>{t('query')}</Button>
                    ) : (
                        <Button color='error' variant='contained' onClick={handleAddSelected} disabled={selectedUrls.length === 0}>
                            {t('addSelected')} ({selectedUrls.length})
                        </Button>
                    )}
                </DialogActions>
            </Dialog>
        </>
    );
};

export default AddQuickAccess;

function getMostVisitedUrls(days, limit) {
    return new Promise((resolve, reject) => {
        const millisecondsPerDay = 1000 * 60 * 60 * 24;
        const startTime = new Date().getTime() - (days * millisecondsPerDay);

        chrome.history?.search({
            'text': '',
            'startTime': startTime,
            'maxResults': 100000
        }, function (historyItems) {
            const urlInfo = {};

            historyItems.forEach(item => {
                if (item.url in urlInfo) {
                    urlInfo[item.url].count += item.visitCount;
                } else {
                    urlInfo[item.url] = {
                        count: item.visitCount,
                        title: item.title || item.url
                    };
                }
            });

            const sortedUrls = Object.entries(urlInfo)
                .sort((a, b) => b[1].count - a[1].count)
                .slice(0, limit)
                .map(([url, info]) => ({
                    title: info.title,
                    url: url,
                    favIconUrl: `${faviconURL(url, 32)}`
                }));

            resolve(sortedUrls);
        });
    });
}
