import React from "react";
import { Box, Button, Divider, Grid, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { resetBeautifySettings } from "../../../../core/startup";
import userStore from "../../../../core/stores/userStore";

import ColorfulBackground from "./ColorfulBackground";
import ListBottomBorder from "./ListBottomBorder";
import BackgroundImageFade from "./BackgroundImageFade";
import BackgroundImageUrl from "./BackgroundImageUrl";
import ListBackgroundAlpha from "./ListBackgroundAlpha";
import ListBackgroundStyle from "./ListBackgroundStyle";
import OverlayScrollbars from "./OverlayScrollbars";
import BackgroundImageBlur from "./BackgroundImageBlur";
import BackgroundImageBrightness from "./BackgroundImageBrightness";
import FontScale from './FontScale';
import FontWeight from "./FontWeight";
import FontFamily from "./FontFamily";
import FontColor from "./FontColor";
import BackgroundColor from "./BackgroundColor";
import BackgroundGradient from "./BackgroundGradient";
import ListVerticalSpacing from "./ListVerticalSpacing";

export default function Beautify() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings" });

    const { colorfulBackground } = userStore((state) => ({
        colorfulBackground: state.settings.colorfulBackground
    }));

    const handleResetSettings = async () => {
        await resetBeautifySettings();
    };

    return (
        <Box>
            <Grid container alignItems="center" sx={{ marginBottom: "30px" }}>
                <Grid item xs={8}>
                    <Typography variant="h1">{t("modules.beautify")}</Typography>
                </Grid>
                <Grid item xs={4}>
                    <Button variant="outlined" color="error" onClick={handleResetSettings}>
                        {t('resetCurrentPageSettings')}
                    </Button>
                </Grid>
            </Grid>

            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                <FontFamily />
                <FontScale />
                <FontWeight />
                <FontColor />
                <ColorfulBackground />
                {colorfulBackground && <BackgroundColor />}
                {colorfulBackground && <BackgroundGradient />}
                {colorfulBackground && <BackgroundImageUrl />}
                {colorfulBackground && <BackgroundImageFade />}
                {colorfulBackground && <BackgroundImageBlur />}
                {colorfulBackground && <BackgroundImageBrightness />}
                <ListVerticalSpacing />
                <ListBackgroundAlpha />
                <ListBackgroundStyle />
                <ListBottomBorder />
                <OverlayScrollbars />
            </Stack>
        </Box>
    );
}