import React from "react";
import { Box, Card, CardContent, CardMedia, Divider, FormControl, FormControlLabel, FormLabel, Paper, Radio, RadioGroup, Switch, Typography, Alert, Grid, Button, Stack } from "@mui/material";
import { useContext } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import UserActivationAlert from "../../alerts/UserActivationAlert";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { sendMessageToAllTabs } from "../../../../core/browers/tabs";
import TabSwitchPip from "./TabSwitchPip";
import TabSwitchPipBackground from "./TabSwitchPipBackground";
import ScrollSwitchPip from "./ScrollSwitchPip";
import AdvancedPipEnable from "./advancedPipEnable";
import { resetPictureInPictureSettings } from "../../../../core/startup";
import SubtitleDetection from "./subtitleDetection";
import ContinuousPlayback from "./ContinuousPlayback";



export default function PictureInPicture() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.picture_in_picture' });

    const { updateSetting, tabSwitchPip, scrollSwitchPip } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabSwitchPip: state.settings.tabSwitchPip,
        scrollSwitchPip: state.settings.scrollSwitchPip,
    }));


    const handleResetSettings = async () => {
        await resetPictureInPictureSettings();
    };


    const handleTabSwitchPipChange = async (event) => {
        await updateSetting('tabSwitchPip', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "tabSwitchPip", value: event.target.checked })
        await sendMessageToAllTabs("update_setting", { key: "tabSwitchPip", value: event.target.checked })

    }

    const handleScrollSwitchPipChange = async (event) => {
        await updateSetting('scrollSwitchPip', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "scrollSwitchPip", value: event.target.checked })
        await sendMessageToAllTabs("update_setting", { key: "scrollSwitchPip", value: event.target.checked })

    }

    return (
        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("Picture-in-picture")}</Typography></Grid>
                <Grid item xs={4}>
                    <Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings', { keyPrefix: "settings" })}</Button>
                </Grid>

            </Grid>
            <Stack spacing={2}>
                <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <TabSwitchPip />
                    <Divider sx={{ my: 1 }} />
                    <TabSwitchPipBackground />
                    <Divider sx={{ my: 1 }} />
                    <ScrollSwitchPip />
                </Box>
                <Box sx={{ p: 2 }}>
                    <UserActivationAlert />
                </Box>

                <Divider textAlign="center" sx={{ color: "error.main", padding: "10px", my: 3 }}>{t('premiumEditionOnly', { keyPrefix: "settings" })}</Divider>

                <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <AdvancedPipEnable />
                    <Divider sx={{ my: 1 }} />
                    <SubtitleDetection />
                    <Divider sx={{ my: 1 }} />
                    <ContinuousPlayback />
                </Box>
                {/* <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <UseBookmarkFolderNameAsGroupName />
                    <Divider sx={{ my: 1 }} />
                    <GroupSelectBookmarkFolderLevel />
                </Box> */}

            </Stack>


        </Box>
    )
}