import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function GroupFilterOutPinnedTabs() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.chromeTabGroupAdvanced.groupFilterOutPinnedTabs' });

    const { updateSetting, groupFilterOutPinnedTabs } = userStore((state) => ({
        updateSetting: state.updateSetting,
        groupFilterOutPinnedTabs: state.settings.groupFilterOutPinnedTabs,
    }));


    const handleChange = async (event) => {
        await updateSetting('groupFilterOutPinnedTabs', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "groupFilterOutPinnedTabs", value: event.target.checked })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={groupFilterOutPinnedTabs}
                    onChange={handleChange} />}
                    label={t(groupFilterOutPinnedTabs ? 'enabled' : 'disabled')}
                    labelPlacement="end"
                />
            </Grid>
        </Grid>

    )

}