import { Close } from "@mui/icons-material";
import { IconButton, ListItemButton, Stack, Typography } from "@mui/material";
import React, { useState } from "react";
import snapshotStore from "../../../../core/stores/snapshotStore";
import { formatDateYmdHis } from "../../../../utils/helpers";
import { toast } from "react-toastify";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function SnapshotItem({ summary }) {

    const { t } = useTranslationContext('options', { keyPrefix: 'snapshots' });


    const selectdSnapshotId = snapshotStore(state => state.selectdSnapshotId);

    const handleClick = async (event) => {
        event.stopPropagation();
        snapshotStore.setState({ selectdSnapshotId: summary.id, selectedTabs: [] });
    }

    const handleDelete = async (event) => {
        event.stopPropagation();
        try {
            await snapshotStore.getState().removeSnapshot(summary.id);
            toast.success(t('snapshotDeletedSuccess'));
        } catch (error) {
            toast.error(t('snapshotDeletedFailed'));
        }
    }

    return (

        <ListItemButton onClick={handleClick} sx={{ flexGrow: 1, flexDirection: 'column', alignItems: 'flex-start', borderRadius: "6px" }} selected={summary.id === selectdSnapshotId}>
            <Stack direction="row" alignItems="center" justifyContent="space-between" sx={{ width: '100%' }} spacing={1}>
                <Stack direction="column" alignItems="flex-start" spacing={1}>
                    <Typography variant="h3">{formatDateYmdHis(summary.createdAt)}</Typography>
                    <Typography variant="body2">{t('windowCount', { count: summary.windowCount })} / {t('tabCount', { count: summary.tabCount })}</Typography>
                </Stack>
                <IconButton onClick={handleDelete}>
                    <Close color="error" />
                </IconButton>
            </Stack>
        </ListItemButton>

    )
}