import { Box, Checkbox, ListItem, ListItemButton, Stack, Typography } from "@mui/material";
import React from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import LinkAvatar from "../../../component/links/LinkAvatar";
import { decodedUrl, faviconURL } from "../../../../utils/helpers";
import snapshotStore from "../../../../core/stores/snapshotStore";

const WindowTabItem = ({ item }) => {
    const { t } = useTranslationContext('options', { keyPrefix: 'snapshots' });
    const { selectedTabs, addSelectdTabId, removeSelectTabId } = snapshotStore((state) => ({
        selectedTabs: state.selectedTabs,
        addSelectdTabId: state.addSelectdTabId,
        removeSelectTabId: state.removeSelectTabId,
    }));
    const selected = selectedTabs[item.windowId]?.includes(item.id) || false;

    const handleOpenLink = () => {
        chrome.tabs.create({ url: item.url, active: false });
    };

    const handleCheckboxChange = (event) => {
        const check = event.target.checked;
        if (check) {
            addSelectdTabId(item.id, item.windowId);
        } else {
            removeSelectTabId(item.id, item.windowId);
        }
    };

    const handleCheckboxClick = (event) => {
        event.stopPropagation();
    };

    return (
        <ListItem disablePadding>
            <ListItemButton
                selected={selected}
                component="div"
                disableGutters
                onClick={handleOpenLink}
            >
                <Stack
                    direction="row"
                    sx={{
                        width: "100%",
                        paddingX: "10px",
                    }}
                    alignItems={"center"}
                    justifyContent={"space-between"}
                    spacing={1}
                >
                    <LinkAvatar avatarUrl={item.favIconUrl || faviconURL(item.url)} />
                    <Box sx={{ minWidth: 0, flex: 1 }}>
                        <Typography
                            variant="h4"
                            noWrap
                            sx={{
                                paddingTop: "4px",
                                paddingBottom: "4px",
                            }}
                        >
                            {item.title}
                        </Typography>
                        <Typography variant="h5" color="text.secondary" sx={{
                            whiteSpace: "pre-wrap",
                            wordWrap: "break-word",
                            wordBreak: "break-all",
                            textWrap: "break-word",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            display: "-webkit-box",
                            WebkitLineClamp: "2",
                            WebkitBoxOrient: "vertical",
                            opacity: 0.75
                        }}>
                            {decodedUrl(item.url)}
                        </Typography>
                    </Box>
                    <Checkbox
                        checked={selected}
                        onChange={handleCheckboxChange}
                        onClick={handleCheckboxClick}
                    />
                </Stack>
            </ListItemButton>
        </ListItem>
    );
};

export default WindowTabItem;
