import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function PinnedTabsAsIcons() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.pinedTabsAsIcons" });

    const { updateSetting, PinnedTabsAsIcons } = userStore((state) => ({
        updateSetting: state.updateSetting,
        PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons
    }));



    const handlePinnedTabsAsIconsChange = async (event) => {
        await updateSetting('PinnedTabsAsIcons', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "PinnedTabsAsIcons", value: event.target.checked })
    }


    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={PinnedTabsAsIcons}
                    onChange={handlePinnedTabsAsIconsChange} />}
                    labelPlacement="start"

                />
            </Grid>
        </Grid>

    )

}
