import React, { useContext } from "react";
import { Chip, Divider, List, ListItem, ListItemButton, Stack, Typography } from "@mui/material";
import { NavLink } from "react-router-dom";
import { useTheme } from '@emotion/react';
import { useTranslationContext } from "../Providers/TranslationProvider";
import PremiumIcon from "../component/PremiumIcon";


export default function Sidebar() {
    // console.log("Sidebar render")

    const { t } = useTranslationContext();
    const theme = useTheme();

    const [selectMenu, setSelectMenu] = React.useState(location.hash.replace("#", ""));

    const links = [
        { name: "home", label: t("settings.modules.appearance"), url: "/" },
        { name: "beautify", label: t("settings.modules.beautify"), url: "/beautify" },
        { name: "tabs", label: t("settings.modules.tabs"), url: "/tabs" },
        { name: "history", label: t("settings.modules.history"), url: "/history" },
        { name: "quickAccess", label: t("settings.quickAccess.title"), url: "/quickAccess" },
        { name: "tabSidebarOnPages", label: t("settings.tabSidebarOnPages.title"), url: "/tabSidebarOnPages" },
        { name: "shortcuts", label: t("settings.modules.shortcuts"), url: "/shortcuts" },
        { name: "performance", label: t("settings.performance.label"), url: "/performance" },
        { name: "site", label: t("settings.site.label"), url: "/sites" },
        { name: "import_export", label: t("settings.importAndExport.title"), url: "/import_export" },
    ]
    const snapshots = [
        { name: "premium", label: t("premiumEdition", { ns: "translation" }), url: "/premium", icon: <PremiumIcon /> },
        { name: "snapshots", label: t("snapshots.title", { ns: "options", keyPrefix: "" }), url: "/snapshots" },

    ]

    const experiments = [

        { name: "pip", label: t("settings.modules.pip"), url: "/pip" },
        { name: "chromeTabGroup", label: t("settings.modules.chromeTabGroupAdvanced"), url: "/chromeTabGroup" },
        { name: "progressBar", label: t("settings.modules.progressBar"), url: "/progressBar" },
        // { name: "floatBall", label: t("settings.modules.floatBall"), url: "/floatball" },
    ]

    return (
        <Stack direction="column" sx={{ height: "100%", backgroundColor: theme.palette.background.default, padding: 0, borderRadius: 2 }} spacing={2} alignItems="flex-start" justifyContent="space-between">
            <List sx={{ width: "100%" }}>
                <List>

                    {links.map((link) => (
                        <ListItemButton
                            key={link.name}
                            selected={selectMenu === link.url}
                            onClick={() => setSelectMenu(link.url)}
                            sx={{
                                width: "100%",
                                padding: 0,
                                "&.Mui-selected": {
                                    color: "#e11d48",
                                    backgroundColor: theme.palette.action.selected,
                                    borderRadius: 1,
                                },
                            }}
                        >
                            <NavLink
                                style={{
                                    textDecoration: "none",
                                    color: "inherit",
                                    width: "100%",
                                    padding: "8px",
                                }}
                                to={link.url}
                                replace={true}
                            >
                                <ListItem key={link.name}>
                                    <Typography variant="h3" sx={{ fontWeight: 600 }}>
                                        {link.label}
                                    </Typography>
                                </ListItem>
                            </NavLink>
                        </ListItemButton>
                    ))}
                </List>


                <Divider sx={{ my: 2 }}> <Chip label={t('settings.modules.experimentalFeatures')} /></Divider>
                {experiments.map((link) => (
                    <ListItemButton key={link.name} selected={selectMenu === link.url} onClick={() => setSelectMenu(link.url)} sx={{ width: "100%", padding: 0, "&.Mui-selected": { color: "#e11d48", backgroundColor: theme.palette.action.selected, borderRadius: 1 } }}>
                        <NavLink style={{ textDecoration: 'none', color: 'inherit', width: "100%", padding: "8px" }} to={link.url} replace={true}>
                            <ListItem key={link.name} >
                                <Typography variant="h3" sx={{ fontWeight: 600 }}>{link.label}</Typography>
                            </ListItem>
                        </NavLink>
                    </ListItemButton>
                ))}

                <Divider sx={{ my: 2 }} />
                {snapshots.map((link) => (
                    <ListItemButton key={link.name} selected={selectMenu === link.url} onClick={() => setSelectMenu(link.url)} sx={{ width: "100%", padding: 0, "&.Mui-selected": { color: "#e11d48", backgroundColor: theme.palette.action.selected, borderRadius: 1 } }}>
                        <NavLink style={{ textDecoration: 'none', color: 'inherit', width: "100%", padding: "8px" }} to={link.url} replace={true}>
                            <ListItem key={link.name}>
                                {link.icon}
                                <Typography variant="h3" sx={{ fontWeight: 600, pl: 1 }}>{link.label}</Typography>
                            </ListItem>
                        </NavLink>
                    </ListItemButton>
                ))}
            </List>
        </Stack>
    );
}