import React, { useState, useEffect } from "react";
import userStore from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { colord, getFormat, getGradientColor, random } from "../../../../utils/colord";
import { Grid, Input, Stack, Typography, Box, Popover, IconButton, Tooltip } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { RgbaStringColorPicker } from "react-colorful";
import ClearIcon from '@mui/icons-material/Clear';
import Cached from '@mui/icons-material/Cached';
import { siteStore } from "../../../../core/stores/siteStore";

const commonColors = [
    "#1abc9c", "#2ecc71", "#3498db", "#9b59b6", "#34495e",
    "#16a085", "#27ae60", "#2980b9", "#8e44ad", "#2c3e50",
    "#f1c40f", "#e67e22", "#e74c3c", "#ecf0f1", "#95a5a6"
];

export default function BackgroundColor({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.backgroundColor" });

    const { backgroundColor, setSiteConfig } = siteStore((state) => ({
        backgroundColor: state.sites.find(site => site.hostname === hostname)?.backgroundColor,
        setSiteConfig: state.setSiteConfig
    }));

    const [anchorEl, setAnchorEl] = useState(null);
    const [localBackgroundColor, setLocalBackgroundColor] = useState(backgroundColor);

    useEffect(() => {
        setLocalBackgroundColor(backgroundColor);
    }, [backgroundColor]);

    const setFontColor = async (bgColor) => {
        let fontColor = colord(bgColor).isLight() ? "#333" : "#fff";
        if (bgColor === 'transparent') {
            fontColor = 'inherit';
        }
        await setSiteConfig(hostname, { fontColor: fontColor });
        await sendRuntimeMessage('update_site_config', { hostname, config: { fontColor: fontColor } });
    };

    const handleInputChange = async (color) => {
        setLocalBackgroundColor(color);
        if (!getFormat(color)) return;
        await setSiteConfig(hostname, { backgroundColor: color });
        await sendRuntimeMessage('update_site_config', { hostname, config: { backgroundColor: color } });
        setFontColor(color);
    };

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleClear = async () => {
        const color = "transparent";
        setLocalBackgroundColor(color);

        await setSiteConfig(hostname, { backgroundColor: null });
        await sendRuntimeMessage('update_site_config', { hostname, config: { backgroundColor: null } });

        await setFontColor(color);
    };

    const handleCommonColorClick = async (color) => {
        setLocalBackgroundColor(color);

        await setSiteConfig(hostname, { backgroundColor: color });
        await sendRuntimeMessage('update_site_config', { hostname, config: { backgroundColor: color } });
        await setFontColor(color);
    };

    const getRandomColor = () => {
        const k = Math.random();
        if (k > 0.75) return random().toHslString();
        if (k > 0.5) return random().toRgbString();
        return random().toHex();
    };

    const handleRandomClick = async () => {
        const color = getRandomColor();
        handleInputChange(color);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'color-picker-popover' : undefined;


    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>

                <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">

                    <Box
                        aria-describedby={id}
                        sx={{
                            width: 24,
                            height: 24,
                            backgroundColor: backgroundColor,
                            border: '1px solid #000',
                        }}
                        onClick={handleClick}
                    />

                    <Popover
                        id={id}
                        open={open}
                        anchorEl={anchorEl}
                        onClose={handleClose}
                        anchorOrigin={{
                            vertical: 'bottom',
                            horizontal: 'left',
                        }}
                        slotProps={{
                            paper: {
                                sx: {
                                    padding: 2,
                                    overflow: 'hidden'
                                }
                            }
                        }}
                    >
                        <RgbaStringColorPicker color={colord(localBackgroundColor).toRgbString()} onChange={handleInputChange} />
                        <Box sx={{ display: 'flex', flexWrap: 'wrap', mt: 2 }}>
                            {commonColors.map((color) => (
                                <Box
                                    key={color}
                                    sx={{
                                        width: 10,
                                        height: 24,
                                        backgroundColor: color,
                                        border: '1px solid #000',
                                        cursor: 'pointer',
                                        margin: '2px',
                                        flex: '0 0 calc(20% - 4px)' // 5 items per row
                                    }}
                                    onClick={() => handleCommonColorClick(color)}
                                />
                            ))}
                        </Box>
                    </Popover>
                    <Input
                        type="text"
                        value={localBackgroundColor}
                        onChange={(e) => handleInputChange(e.target.value)}
                        autoFocus

                        endAdornment={
                            <>
                                <Tooltip title={t("random")} >
                                    <span>
                                        <IconButton onClick={handleRandomClick}  >
                                            <Cached />
                                        </IconButton>
                                    </span>
                                </Tooltip>
                                <Tooltip title={t("clear")}>
                                    <span>
                                        <IconButton onClick={handleClear} >
                                            <ClearIcon />
                                        </IconButton>
                                    </span>
                                </Tooltip>
                            </>
                        }
                    />
                </Stack>
            </Grid>
        </Grid>
    );
}