import React from "react";
import { Stack, Typography } from "@mui/material";
import { siteStore } from "../../../../core/stores/siteStore";
import Avatar from "@mui/material/Avatar";
import { ArrowForward, Clear, Public, Remove } from "@mui/icons-material";
import { faviconURL } from "../../../../utils/helpers";
import IconButton from "@mui/material/IconButton";
import SiteConfig from "./SiteConfig";
import { useParams, Link } from "react-router-dom";


export default function SiteItem({ hostname }) {

    const params = useParams();

    hostname = hostname || params.hostname


    const { deleteSiteConfig, } = siteStore((state) => ({
        deleteSiteConfig: state.deleteSiteConfig
    }))


    const handleDelete = (event) => {
        event.stopPropagation();
        event.preventDefault();
        deleteSiteConfig(hostname)
    }

    const avatarUrl = faviconURL(`https://${hostname}`) || `https://via.placeholder.com/60x60.png?text=${hostname}`
    return (
        <Link to={`/sites/${hostname}`} >
            <Stack direction="row" justifyContent="space-between" alignItems="center" spacing={2} sx={{}}>

                <Avatar variant="square" src={avatarUrl} sx={{ width: "20px", height: "20px", alignContent: "center" }} >
                    <Public sx={{ fontSize: "20px" }} color="primary" />
                </Avatar>

                <Typography variant="h3" component="div" sx={{ flexGrow: 1 }}>
                    {hostname}
                </Typography>

                <IconButton>
                    <ArrowForward />
                </IconButton>

                <IconButton onClick={handleDelete}>
                    <Clear />
                </IconButton>

            </Stack>
        </Link>
    )
}