import { Add } from "@mui/icons-material";
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, TextField } from "@mui/material";
import React from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { toast } from "react-toastify";

export default function AddNewSite() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.addNewSite" });

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    return (
        <React.Fragment>
            <Button variant="outlined" color="error" startIcon={<Add />} onClick={handleClickOpen}  > {t("label")}</Button>

            <Dialog
                open={open}
                onClose={handleClose}
                PaperProps={{
                    component: 'form',
                    onSubmit: (event) => {
                        event.preventDefault();
                        var regex = /^(?!-)[A-Za-z0-9-]{1,63}(?<!-)(\.[A-Za-z0-9]+)*(\.[A-Za-z]{2,6})$/;
                        const formData = new FormData(event.currentTarget);
                        const formJson = Object.fromEntries(formData.entries());
                        const hostname = formJson.hostname;
                        if (!regex.test(hostname)) {
                            toast.error(t("invalidDomain"));
                            return;
                        }

                        handleClose();
                        window.location.href = `chrome-extension://${chrome.runtime.id}/options.html#/sites/${hostname}`
                    },
                }}
            >
                <DialogTitle variant="h3">{t("label")}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {t("description")}
                    </DialogContentText>
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        id="name"
                        name="hostname"
                        autoComplete="off"
                        type="text"
                        fullWidth
                        variant="standard"
                        placeholder={t("placeholder")}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>{t("cancel")}</Button>
                    <Button type="submit">{t("add")}</Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}