import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import AddQuickAccess from "./AddQuickAccess";



export default function EnableQuickAccess() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.quickAccess.enableQuickAccess" });

    const { updateSetting, pinToQuickAccess } = userStore((state) => ({
        updateSetting: state.updateSetting,
        pinToQuickAccess: state.settings.pinToQuickAccess
    }));




    const handlePinnedTabsAsIconsChange = async (event) => {
        await updateSetting('pinToQuickAccess', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "pinToQuickAccess", value: event.target.checked })
    }


    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={2}>
                <FormControlLabel control={<Switch color="error" checked={pinToQuickAccess}
                    onChange={handlePinnedTabsAsIconsChange} />}
                    labelPlacement="start"
                />

            </Grid>
            {pinToQuickAccess && <Grid item xs={2}>
                <AddQuickAccess />
            </Grid>
            }
        </Grid>

    )

}
