import React, { useState } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Typography, FormControlLabel, Switch, Button, Dialog, DialogTitle, DialogContent, DialogActions, FormGroup, FormControl, FormLabel, Checkbox, Divider } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { ArrowRight } from "@mui/icons-material";

export default function ShowToolbar() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.toolbar" });

    const { updateSetting, showToolbar, toolbar_hide_label } = userStore((state) => ({
        updateSetting: state.updateSetting,
        showToolbar: state.settings.showToolbar,
        toolbar_hide_label: state.settings.toolbar_hide_label
    }));

    const [open, setOpen] = useState(false);
    const [options, setOptions] = useState([
        { label: t("options.toolbar_newtab"), key: "toolbar_newtab", value: true },
        { label: t("options.toolbar_orderby"), key: "toolbar_orderby", value: true },
        { label: t("options.toolbar_showby"), key: "toolbar_showby", value: true },
        { label: t("options.toolbar_view"), key: "toolbar_view", value: true },
        { label: t("options.toolbar_colorMode"), key: "toolbar_colorMode", value: true },
        { label: t("options.toolbar_muteAllTabs"), key: "toolbar_muteAllTabs", value: true },
        { label: t("options.toolbar_scrollToActiveTab"), key: "toolbar_scrollToActiveTab", value: true }
    ]);

    const handleShowToolbarChange = async (event) => {
        await updateSetting('showToolbar', event.target.checked);
        await sendRuntimeMessage("update_setting", { key: "showToolbar", value: event.target.checked });
    };

    const handleToolbarIconLabelChange = async (event) => {
        await updateSetting('toolbar_hide_label', event.target.checked);
        await sendRuntimeMessage("update_setting", { key: "toolbar_hide_label", value: event.target.checked });
    };

    const handleMoreOptionsClick = () => {
        setOpen(true);
    };

    const handleDialogClose = () => {
        setOpen(false);
    };

    const handleOptionChange = async (event) => {
        const newOptions = options.map(option =>
            option.key === event.target.name ? { ...option, value: event.target.checked } : option
        );
        setOptions(newOptions);
        await updateSetting(event.target.name, event.target.checked);
        await sendRuntimeMessage("update_setting", { key: event.target.name, value: event.target.checked });
    };

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={2}>
                <FormControlLabel control={<Switch color="error" checked={showToolbar}
                    onChange={handleShowToolbarChange} />}
                    labelPlacement="start"
                />

            </Grid>
            {showToolbar &&
                <Grid item xs={2}>
                    <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={handleMoreOptionsClick}>
                        {t('moreOptions')}
                    </Button>
                </Grid>
            }

            <Dialog open={open} onClose={(event, reason) => { if (reason !== 'backdropClick') { handleDialogClose } }}>
                <DialogTitle variant="h3"> {t('moreOptions')}</DialogTitle>
                <DialogContent>

                    <FormControlLabel

                        control={
                            <Switch
                                color="error"
                                checked={toolbar_hide_label}
                                onChange={handleToolbarIconLabelChange}
                                name='toolbar_hide_label'
                            />
                        }
                        label={t('moreOptionsHideLabel')}
                    />
                    <Divider sx={{ my: 2 }} />
                    <FormLabel component="h4" >{t("moreOptionsDescription")}</FormLabel>

                    <FormGroup>
                        {options.map(option => (
                            <FormControlLabel
                                key={option.key}
                                control={
                                    <Checkbox
                                        color="error"
                                        checked={option.value}
                                        onChange={handleOptionChange}
                                        name={option.key}
                                    />
                                }
                                label={option.label}
                            />
                        ))}
                    </FormGroup>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose} color="error">
                        {t('close')}
                    </Button>
                </DialogActions>
            </Dialog>
        </Grid>
    );
}