import React, { useContext } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, Radio, RadioGroup, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";


export default function PipRestrictionAction({ hostname }) {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.site.pipRestrictionAction' });

    const { pipRestrictionAction, setSiteConfig } = siteStore((state) => ({
        pipRestrictionAction: state.sites.find(site => site.hostname === hostname)?.pipRestrictionAction || "default",
        setSiteConfig: state.setSiteConfig
    }));

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { pipRestrictionAction: event.target.value });
        await sendRuntimeMessage('update_site_config', { hostname, config: { pipRestrictionAction: event.target.value } });
    }


    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl>
                    {/* <FormLabel id="demo-row-radio-buttons-group-label">Gender</FormLabel> */}
                    <RadioGroup
                        // row
                        name="gender"
                        value={pipRestrictionAction}
                        onChange={handleChange}
                    >
                        <FormControlLabel value="default" control={<Radio />} label={t('default')} />
                        <FormControlLabel value="use_standard_pip" control={<Radio />} label={t('use_standard_pip')} />
                        <FormControlLabel value="open_in_new_tab" control={<Radio />} label={t('open_in_new_tab')} />


                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>

    )

}