import React, { useContext } from "react";
import { Box, Button, Divider, Grid, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import View from "./View";
import PinnedTabsAsIcons from "./PinnedTabsAsIcons";
import GroupCollapsedShowIcon from "./GroupCollapsedShowIcon";
import TabPreviewEnable from "./TabPreviewEnable";
import TabPreviewImage from "./TabPreviewImage";
import TabPreviewWaitSeconds from "./TabPreviewWaitSeconds";
import TabShowBy from "./TabShowBy";
import TabSortBy from "./TabSortBy";
import { resetTabsSettings } from "../../../../core/startup";
import OnlyCurrentWindowTabs from "./OnlyCurrentWindowTabs";
import ShowFullUrl from "./ShowFullUrl";
import PinToQuickAccess from "./PinToQuickAccess";
import ShowTabIndex from "./ShowTabIndex";
import TabHistoryEnable from "./TabHistoryEnable";
import TabHistoryOnlyOnActive from "./TabHistoryOnlyOnActive";
import TabHistoryExpand from "./TabHistoryExpand";
import TabHistoryShowExpandBtn from "./TabHistoryShowExpandBtn";
import TabHistoryShowUrl from "./TabHistoryShowUrl";
import TabHistoryShowIconWhenCollapsed from "./TabHistoryShowIconWhenCollapsed";
import TabHistorySortBy from "./TabHistorySortBy";
import TabHistoryKeepCount from "./TabHistoryKeepCount";
import GroupTitleMulitLines from "./GroupTitleMulitLines";
import TabTitleMulitLines from "./TabTitleMulitLines";
import GroupCollapsedTitleWidth from "./GroupCollapsedTitleWidth";
import ShowGroupTabCount from "./ShowGroupTabCount";



export default function Tabs() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings" });
    const handleResetSettings = () => {
        resetTabsSettings();

    }

    return (

        <Box>
            <Grid container alignItems="center" mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("modules.tabs")}</Typography></Grid>
                <Grid item xs={4}><Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings')}</Button></Grid>
            </Grid>

            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>

                <View />
                <TabShowBy />
                <TabSortBy />
                <ShowFullUrl />
                <OnlyCurrentWindowTabs />

                <PinnedTabsAsIcons />
                <PinToQuickAccess />

                <GroupCollapsedShowIcon />
                <TabPreviewEnable />
                <TabPreviewImage />
                <TabPreviewWaitSeconds />
                <ShowTabIndex />
                <ShowGroupTabCount />
                <TabTitleMulitLines />
                <GroupTitleMulitLines />
                <GroupCollapsedTitleWidth />



            </Stack>
            <Divider textAlign="center" sx={{ color: "error.main", padding: "10px", my: 3 }}>{t('premiumEditionOnly')}</Divider>

            <Stack spacing={2} sx={{ border: "1px solid #eee", mt: "15px", padding: "15px", borderRadius: "10px" }}>
                <TabHistoryEnable />
                <TabHistoryOnlyOnActive />
                <TabHistoryExpand />
                <TabHistoryShowExpandBtn />
                <TabHistoryShowUrl />
                <TabHistoryShowIconWhenCollapsed />
                <TabHistorySortBy />
                <TabHistoryKeepCount />
            </Stack>
        </Box >

    )

}
