import React, { useContext } from "react";

import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { sendRuntimeMessage, sendTabMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import { useParams } from "react-router-dom";



export default function PageKeepAlive({ hostname }) {

    const params = useParams();
    hostname = hostname || params.hostname
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.pageKeepAlive" });
    const { pageKeepAlive, setSiteConfig } = siteStore((state) => ({
        pageKeepAlive: state.sites.find(site => site.hostname === hostname)?.pageKeepAlive,
        setSiteConfig: state.setSiteConfig
    }));

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { pageKeepAlive: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { pageKeepAlive: event.target.checked } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            await sendRuntimeMessage(event.target.checked ? 'page_keep_alive' : 'page_keep_alive_cancel', { tabId: tab.id });
        }
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <FormControlLabel control={<Switch color="error" checked={pageKeepAlive === true}
                    onChange={handleChange} />}
                />
            </Grid>
        </Grid>

    )

}
