import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Typography, ToggleButton, ToggleButtonGroup, Alert, AlertTitle } from "@mui/material";
import { KeyboardArrowRight } from "@mui/icons-material";
import { KeyboardArrowLeft } from "@mui/icons-material";
import { History } from "@mui/icons-material";
import { RemoveFromQueue } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { toast } from "react-toastify";
import { Close } from "@mui/icons-material";
import { Pause } from "@mui/icons-material";
import { NotInterested } from "@mui/icons-material";
export default function SaveMemoryAction() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.performance.saveMemoryAction" });
    const { updateSetting, saveMemoryAction } = userStore((state) => ({
        updateSetting: state.updateSetting,

        saveMemoryAction: state.settings.saveMemoryAction

    }));


    const handleChange = async (e, v) => {
        await updateSetting("saveMemoryAction", v)
        await sendRuntimeMessage("update_setting", { key: "saveMemoryAction", value: v })


    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={saveMemoryAction} exclusive>
                    <ToggleButton value="disabled" onClick={handleChange}>
                        <NotInterested sx={{ mr: 1 }} /> <Typography variant="body1">{t("disabled")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="discard" onClick={handleChange}>
                        <Pause sx={{ mr: 1 }} /> <Typography variant="body1">{t("discardTabs")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="close" onClick={handleChange}>
                        <Close sx={{ mr: 1 }} /> <Typography variant="body1">{t("closeTabs")}</Typography>
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
            {saveMemoryAction === "discard" &&
                <Grid item xs={12} mt={1}>
                    <Alert icon={false} severity="info">
                        <Typography >{t('discardTips')} </Typography>
                    </Alert>
                </Grid>
            }
        </Grid>

    )
}