import React, { useContext, useState } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, IconButton, InputAdornment, OutlinedInput, Stack, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { Save } from "@mui/icons-material";
import { Check } from "@mui/icons-material";



export default function TabHistoryKeepCount() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.tabHistoryKeepCount" });
    const { updateSetting, tabHistoryEnable, tabHistoryKeepCount } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabHistoryEnable: state.settings.tabHistoryEnable,
        tabHistoryKeepCount: state.settings.tabHistoryKeepCount,
    }));

    const [inputValue, setInputValue] = useState(tabHistoryKeepCount);

    const handleSave = async () => {
        const value = parseInt(inputValue);
        if (!isNaN(value)) {
            await updateSetting('tabHistoryKeepCount', value);
            await sendRuntimeMessage("update_setting", { key: "tabHistoryKeepCount", value: value });
        }
    }

    return (

        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '100px' }} variant="outlined">
                    <OutlinedInput
                        disabled={!tabHistoryEnable}
                        value={inputValue}
                        inputProps={{ itemType: 'number', max: 999, min: 1, step: 1 }}
                        onChange={(event) => setInputValue(event.target.value)}
                        endAdornment={<IconButton onClick={handleSave}><Check color="success" /></IconButton>}
                    />
                </FormControl>
            </Grid>
        </Grid>

    )
}