import React from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { FormControl, Autocomplete, Grid, Typography, TextField } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function FontFamily() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.fontFamily" });

    const { updateSetting, fontFamily } = userStore((state) => ({
        updateSetting: state.updateSetting,
        fontFamily: state.settings.fontFamily,
    }));

    const fontFamilies = [
        { label: 'System', value: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif' },
        { label: 'Roboto', value: 'Roboto, Arial, sans-serif' },
        { label: 'Open Sans', value: '"Open Sans", Arial, sans-serif' },
        { label: 'Noto Sans', value: '"Noto Sans", "Noto Sans JP", "Noto Sans KR", "Noto Sans SC", "Noto Sans TC", "Noto Sans Arabic", sans-serif' },
        { label: 'Montserrat', value: 'Montserrat, Arial, sans-serif' },
        { label: 'Lato', value: 'Lato, Arial, sans-serif' },
        { label: 'Inter', value: 'Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Arial, sans-serif' },
        { label: 'Source Sans Pro', value: '"Source Sans Pro", Arial, sans-serif' },
        { label: 'Poppins', value: 'Poppins, "Segoe UI", Tahoma, Geneva, sans-serif' },
        { label: 'PT Sans', value: '"PT Sans", Arial, sans-serif' },
        { label: 'Merriweather', value: 'Merriweather, Georgia, "Times New Roman", Times, serif' },
        { label: 'Raleway', value: 'Raleway, Arial, sans-serif' },
        { label: 'Playfair Display', value: '"Playfair Display", Georgia, serif' },
        { label: 'Nunito', value: 'Nunito, Arial, sans-serif' },
        { label: 'Fira Sans', value: '"Fira Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Arial, sans-serif' },
        { label: 'Work Sans', value: '"Work Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Arial, sans-serif' },
        { label: 'Georgia', value: 'Georgia, "Times New Roman", Times, serif' },
        { label: 'Arial', value: 'Arial, sans-serif' },
        { label: 'Helvetica', value: '"Helvetica Neue", Helvetica, Arial, sans-serif' },
        { label: 'Courier New', value: '"Courier New", Courier, monospace' },
        { label: 'Microsoft YaHei', value: '"Microsoft YaHei", 微软雅黑, sans-serif' },
        { label: 'STXihei', value: 'STXihei, "华文细黑", sans-serif' },
        { label: 'SimSun', value: 'SimSun, 宋体, sans-serif' },
        { label: 'SimHei', value: 'SimHei, 黑体, sans-serif' },
        { label: 'Noto Sans CJK SC', value: '"Noto Sans CJK SC", sans-serif' },
        { label: 'Yu Gothic', value: '"Yu Gothic", "Hiragino Kaku Gothic ProN", "Meiryo", sans-serif' },
        { label: 'MS Mincho', value: '"MS Mincho", "Hiragino Mincho ProN", serif' },
        { label: 'Malgun Gothic', value: '"Malgun Gothic", "Apple SD Gothic Neo", sans-serif' },
        { label: 'Nanum Gothic', value: '"Nanum Gothic", "Apple SD Gothic Neo", sans-serif' },
        { label: 'Verdana', value: 'Verdana, Geneva, sans-serif' },
        { label: 'Dubai', value: '"Dubai", "Arial", sans-serif' },
        { label: 'Tahoma', value: 'Tahoma, Geneva, sans-serif' }
    ];

    const handleChange = async (event, value) => {
        await updateSetting("fontFamily", value?.value);
        await sendRuntimeMessage("update_setting", { key: "fontFamily", value: value?.value });
    }

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl variant="standard" fullWidth sx={{ m: 1, minWidth: 200 }}>
                    <Autocomplete
                        fullWidth
                        options={fontFamilies}
                        getOptionLabel={(option) => option?.label}
                        defaultValue={fontFamilies.find((v) => v.value === fontFamily)}
                        value={fontFamilies.find((v) => v.value === fontFamily)}
                        onChange={handleChange}
                        renderInput={(params) => <TextField {...params} />}
                    />
                </FormControl>
            </Grid>
        </Grid>
    );
}