import React, { useState, useEffect, useCallback } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Button, FormControl, Grid, IconButton, TextField, Typography, Paper, Box, Dialog, DialogContent, DialogTitle, DialogActions } from "@mui/material";
import { Edit as EditIcon, Delete as DeleteIcon, ArrowRight } from "@mui/icons-material";
import { sendRuntimeMessage, sendTabMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import { DndContext, closestCenter, MouseSensor, TouchSensor, useSensor, useSensors } from '@dnd-kit/core';
import { arrayMove, SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { useParams } from "react-router-dom";

export default function RefreshInterval({ hostname }) {
    const params = useParams();
    hostname = hostname || params.hostname;
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.linkRefreshOptions" });
    const { linkRefreshOptions, setSiteConfig } = siteStore((state) => ({
        linkRefreshOptions: state.sites.find(site => site.hostname === hostname)?.linkRefreshOptions || [],
        setSiteConfig: state.setSiteConfig
    }));

    const [open, setOpen] = useState(false);
    const [localRefreshOptions, setLocalRefreshOptions] = useState([]);
    const [regex, setRegex] = useState("");
    const [refreshInterval, setRefreshInterval] = useState("");
    const [editingId, setEditingId] = useState(null);

    useEffect(() => {
        const refreshOptions = siteStore.getState().sites.find(site => site.hostname === hostname)?.linkRefreshOptions || [];
        setLocalRefreshOptions(refreshOptions);
    }, [hostname]);

    const handleSave = useCallback(async () => {
        let newOptions;
        const parsedInterval = parseFloat(refreshInterval);
        const finalInterval = isNaN(parsedInterval) ? 0 : Number(parsedInterval.toFixed(3));
        if (editingId) {
            newOptions = localRefreshOptions.map(option =>
                option.id === editingId ? { ...option, regex, refreshInterval: finalInterval } : option
            );
        } else {
            newOptions = [...localRefreshOptions, { id: crypto.randomUUID(), regex, refreshInterval: finalInterval, order: localRefreshOptions.length }];
        }
        setLocalRefreshOptions(newOptions);
        await setSiteConfig(hostname, { linkRefreshOptions: newOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkRefreshOptions: newOptions } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        openTabs.forEach(tab => {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkRefreshOptions', value: newOptions });
        });
        setRegex("");
        setRefreshInterval("");
        setEditingId(null);
    }, [hostname, localRefreshOptions, regex, refreshInterval, editingId, setSiteConfig]);

    const handleDelete = useCallback(async (id) => {
        const newOptions = localRefreshOptions.filter(option => option.id !== id);
        setLocalRefreshOptions(newOptions);
        await setSiteConfig(hostname, { linkRefreshOptions: newOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkRefreshOptions: newOptions } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        openTabs.forEach(tab => {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkRefreshOptions', value: newOptions });
        });
    }, [hostname, localRefreshOptions, setSiteConfig]);

    const handleEdit = useCallback((id) => {
        const option = localRefreshOptions.find(option => option.id === id);
        setRegex(option.regex);
        setRefreshInterval(option.refreshInterval.toString());
        setEditingId(id);
    }, [localRefreshOptions]);

    const handleDragEnd = useCallback(async (event) => {
        const { active, over } = event;
        if (!active?.id || !over?.id || active.id === over.id) return;
        const oldIndex = localRefreshOptions.findIndex(option => option.id === active.id);
        const newIndex = localRefreshOptions.findIndex(option => option.id === over.id);
        const newOptions = arrayMove(localRefreshOptions, oldIndex, newIndex);
        setLocalRefreshOptions(newOptions);
        await setSiteConfig(hostname, { linkRefreshOptions: newOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkRefreshOptions: newOptions } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        openTabs.forEach(tab => {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkRefreshOptions', value: newOptions });
        });
    }, [hostname, localRefreshOptions, setSiteConfig]);

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    return (
        <Grid container alignItems="center">
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={() => setOpen(true)}>
                    {t('configure')}
                </Button>
                <Dialog
                    open={open}
                    onClose={(event, reason) => { if (reason !== 'backdropClick') { setOpen(false); } }}
                    maxWidth="md"
                    fullWidth
                >
                    <DialogTitle variant="h3">{t("label")}</DialogTitle>
                    <DialogContent>
                        <Box border={1} borderColor="grey.300" borderRadius={2} p={2} mb={2}>
                            <Typography variant="body2" color="textSecondary" gutterBottom>{t("listTips")}</Typography>
                            <Grid container alignItems="center" spacing={2} sx={{ borderBottom: '1px solid grey', paddingBottom: "10px" }}>
                                <Grid item xs>
                                    <Typography variant="h4">{t("linkMatchContent")}</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <Typography variant="h4">{t("refreshInterval")}</Typography>
                                </Grid>
                                <Grid item xs={1}>
                                    <Typography variant="h4">{t("edit")}</Typography>
                                </Grid>
                                <Grid item xs={1}>
                                    <Typography variant="h4">{t("delete")}</Typography>
                                </Grid>
                            </Grid>
                            {localRefreshOptions.length > 0 && (
                                <DndContext
                                    collisionDetection={closestCenter}
                                    onDragEnd={handleDragEnd}
                                    sensors={sensors}
                                >
                                    <SortableContext
                                        items={localRefreshOptions}
                                        strategy={verticalListSortingStrategy}
                                    >
                                        {localRefreshOptions.map((option) => (
                                            <SortableItem key={option.id} id={option.id}>
                                                <Grid container alignItems="center" spacing={1}>
                                                    <Grid item xs>
                                                        <Typography style={{ wordBreak: 'break-word' }}>{option.regex}</Typography>
                                                    </Grid>
                                                    <Grid item xs={3}>
                                                        <Typography>{option.refreshInterval === 0 ? t("disabled") : option.refreshInterval}</Typography>
                                                    </Grid>
                                                    <Grid item xs={1}>
                                                        <IconButton color="primary" onClick={() => handleEdit(option.id)}>
                                                            <EditIcon />
                                                        </IconButton>
                                                    </Grid>
                                                    <Grid item xs={1}>
                                                        <IconButton color="error" onClick={() => handleDelete(option.id)}>
                                                            <DeleteIcon />
                                                        </IconButton>
                                                    </Grid>
                                                </Grid>
                                            </SortableItem>
                                        ))}
                                    </SortableContext>
                                </DndContext>
                            )}
                        </Box>
                        <Paper variant="outlined" style={{ padding: '16px', marginTop: '16px' }}>
                            <Typography variant="h4" gutterBottom>{t("linkMatchContent")}</Typography>
                            <Typography variant="body2" color="textSecondary" gutterBottom>{t("linkMatchContentTips")}</Typography>
                            <FormControl fullWidth margin="normal">
                                <TextField
                                    value={regex}
                                    autoComplete="off"
                                    onChange={(e) => setRegex(e.target.value)}
                                    variant="standard"
                                />
                            </FormControl>
                            <Typography variant="h4">{t("refreshInterval")}</Typography>
                            <FormControl fullWidth margin="normal">
                                <TextField
                                    value={refreshInterval}
                                    onChange={(e) => setRefreshInterval(e.target.value)}
                                    variant="standard"
                                />
                            </FormControl>
                            <Button onClick={handleSave} color="error" variant="contained" style={{ marginTop: '16px' }} disabled={!regex}>
                                {t("save")}
                            </Button>
                        </Paper>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => setOpen(false)} color="error">
                            {t("close")}
                        </Button>
                    </DialogActions>
                </Dialog>
            </Grid>
        </Grid>
    );
}

function SortableItem({ id, children }) {
    const { attributes, listeners, setNodeRef, transform, transition } = useSortable({ id });

    const style = {
        transform: CSS.Transform.toString(transform),
        transition,
        cursor: 'move'
    };

    return (
        <div ref={setNodeRef} style={style} {...attributes} {...listeners}>
            {children}
        </div>
    );
}