import React, { useContext } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Typography, Button } from "@mui/material";

export default function Sidepanel() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.SidePanelPosition" });

    const handleClick = (event, val) => {
        chrome.tabs.create({ url: "chrome://settings/appearance#:~:text=side%20panel", active: true });
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <Button variant="contained" color="error" onClick={handleClick}>{t("goToBrowserSettings")}</Button>
            </Grid>
        </Grid>
    )
}