import React, { useContext } from "react";
import { Box, Button, Divider, Grid, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import EnableQuickAccess from "./EnableQuickAccess";
import QuickAccessVerticalSpacing from "./QuickAccessVerticalSpacing";
import QuickAccessHorizontalSpacing from "./QuickAccessHorizontalSpacing";
import { resetQuickAccessSettings } from "../../../../core/startup";
import QuickAccessGroupView from "./QuickAccessGroupView";
import QuickAccessGroupClickAction from "./QuickAccessGroupClickAction";



export default function QuickAccess() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings" });
    const handleResetSettings = async () => {
        await resetQuickAccessSettings();
    };


    return (

        <Box>
            <Grid container alignItems="center" mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("quickAccess.title")}</Typography></Grid>
                <Grid item xs={4}>
                    <Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings', { keyPrefix: "settings" })}</Button>

                </Grid>
            </Grid>

            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>


                <EnableQuickAccess />
                <QuickAccessVerticalSpacing />
                <QuickAccessHorizontalSpacing />

            </Stack>

            <Divider textAlign="center" sx={{ color: "error.main", padding: "10px", my: 3 }}>{t('premiumEditionOnly')}</Divider>

            <Stack spacing={2} sx={{ border: "1px solid #eee", mt: "15px", padding: "15px", borderRadius: "10px" }}>
                <QuickAccessGroupView />
                <QuickAccessGroupClickAction />
            </Stack>

        </Box >

    )

}
