import React, { useContext } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, InputAdornment, OutlinedInput, Stack, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function HistoryKeepCount() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.saveRecentlyTabsLimit" });
    const { updateSetting, enableHistory, historyKeepCount } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,
        historyKeepCount: state.settings.historyKeepCount,
    }));

    const handleChange = async (event) => {
        await updateSetting('historyKeepCount', parseInt(event.target.value))
        await sendRuntimeMessage("update_setting", { key: "historyKeepCount", value: parseInt(event.target.value) })
    }


    return (

        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '100px' }} variant="outlined">
                    <OutlinedInput
                        disabled={!enableHistory}
                        value={historyKeepCount}
                        inputProps={{ itemType: 'number', max: 999, min: 1, step: 1 }}

                        onChange={handleChange}
                    />
                </FormControl>
            </Grid>
        </Grid>

    )
}