import React from "react";
import { FormControl, Grid, OutlinedInput, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function Command({ command }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.shortcuts" });
    const commandName = command.name;

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t(commandName)}</Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '100%' }} variant="outlined">
                    <OutlinedInput
                        disabled={true}
                        value={command.shortcut}
                    />
                </FormControl>
            </Grid>
        </Grid>
    );
}