import { Search } from "@mui/icons-material";
import { Grid, IconButton, TextField } from "@mui/material";
import React, { useState, useEffect, useRef } from "react";
import { siteStore } from "../../../../core/stores/siteStore";
import { Clear } from "@mui/icons-material";

export default function SearchBox() {

    const [focused, setFocused] = useState(true);
    const searchBoxRef = useRef(null);
    let clearTimer = null;
    const { searchText } = siteStore((state) => ({
        searchText: state.searchText
    }))


    useEffect(() => {

        if (searchBoxRef.current) {
            searchBoxRef.current.focus();
            searchBoxRef.current.value = searchText;
        }

        return () => {
        }
    }, [searchText]);

    const handleClearClick = e => {
        siteStore.setState({ searchText: "" });
        setFocused(false);
    };
    const handleSearchInput = event => {
        event.preventDefault();
        const value = searchBoxRef.current.value;
        siteStore.setState({ searchText: value });
    };


    return (
        <Grid container justifyContent="center" alignItems={"center"} p={2}>
            <TextField
                inputRef={searchBoxRef}
                size="small"
                fullWidth
                placeholder="Search"
                variant="outlined"
                // value={searchText}
                autoComplete="off"
                focused={focused}

                InputProps={{

                    startAdornment: <Search sx={{ mr: 1, color: "#999", fontSize: "1rem" }} />,
                    endAdornment: searchText.length > 0 && (
                        <IconButton onClick={e => handleClearClick(e)}>
                            <Clear sx={{ fontSize: "1rem" }} />
                        </IconButton>
                    ),
                }}
                onChange={e => handleSearchInput(e)}
                onFocus={() => setFocused(true)}
                onMouseEnter={() => setFocused(true)}
                onMouseLeave={() => setFocused(false)}
            />
        </Grid>
    )
}