import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { Grid, Typography, FormControlLabel, Switch } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
export default function AlwaysShowCloseButton() {

    const { t } = useTranslationContext();

    const { updateSetting, AlwaysShowCloseButton } = userStore((state) => ({
        updateSetting: state.updateSetting,
        AlwaysShowCloseButton: state.settings.AlwaysShowCloseButton

    }));


    const handleChange = async (event) => {
        await updateSetting('AlwaysShowCloseButton', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "AlwaysShowCloseButton", value: event.target.checked })
    }


    return (
        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("settings.appearance.alwaysShowCloseButton.label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("settings.appearance.alwaysShowCloseButton.description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={AlwaysShowCloseButton}
                    onChange={handleChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>

    )
}