import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, RadioGroup, FormControl, FormLabel, Radio, Typography, Grid } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import userStore from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function QuickAccessGroupView() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.quickAccess.quickAccessGroupView' });

    const { updateSetting, pinToQuickAccess, quickAccessGroupView } = userStore((state) => ({
        updateSetting: state.updateSetting,
        pinToQuickAccess: state.settings.pinToQuickAccess,
        quickAccessGroupView: state.settings.quickAccessGroupView,
    }));


    const handleChange = async (event) => {
        await updateSetting('quickAccessGroupView', event.target.value)
        await sendRuntimeMessage("update_setting", { key: "quickAccessGroupView", value: event.target.value })
    }
    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl component="fieldset" disabled={!pinToQuickAccess}>
                    <FormLabel component="legend">{t('label')}</FormLabel>
                    <RadioGroup
                        name="quickAccessGroupView"
                        value={quickAccessGroupView}
                        onChange={handleChange}
                    >
                        <FormControlLabel value="short_name" control={<Radio disabled={!pinToQuickAccess} />} label={t('short_name')} />
                        <FormControlLabel value="icons" control={<Radio disabled={!pinToQuickAccess} />} label={t('icons')} />
                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>

    )
}