import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { Grid, Typography, ToggleButton, ToggleButtonGroup } from "@mui/material";
import { KeyboardArrowRight } from "@mui/icons-material";
import { KeyboardArrowLeft } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
export default function SidebarPosition() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabSidebarOnPages.sidebarPosition" });

    const { updateSetting, sidebarPosition } = userStore((state) => ({
        updateSetting: state.updateSetting,
        sidebarPosition: state.settings.sidebarPosition

    }));


    const handleChange = async (e, v) => {
        await updateSetting("sidebarPosition", v)
        await sendRuntimeMessage("update_setting", { key: "sidebarPosition", value: v })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={sidebarPosition} exclusive>
                    <ToggleButton value="left" onClick={handleChange}>
                        <KeyboardArrowLeft /> <Typography variant="body1">{t("left")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="right" onClick={handleChange}>
                        <Typography variant="body1">{t("right")}</Typography> <KeyboardArrowRight />
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
        </Grid>

    )
}