import React from "react";
import { createRoot } from "react-dom/client";
import App from "../App";
import "overlayscrollbars/overlayscrollbars.css";
import Options from "./Options";
import { createHashRouter } from "react-router-dom";
import { RouterProvider } from "react-router-dom";
import Appearance from "./settings/Appearance";
import Tabs from "./settings/Tabs";
import History from "./settings/History";
import PictureInPicture from "./settings/PictureInPicture";
import ChromeTabGroup from "./settings/ChromeTabGroup";
import Beautify from "./settings/Beautify";
import Shortcuts from "./settings/Shortcuts";
import ProgressBar from "./settings/ScrollProgressBar";
import FloatBall from "./settings/FloatBall";
import Performance from "./settings/Performance";

import SiteConfig from "./settings/Site/SiteConfig";
import SiteList from "./settings/Site";

import { initSentry, SentryErrorBoundary } from "../../services/sentry-config";
import { OverlayScrollbars } from "overlayscrollbars";
import ImportExport from "./settings/ImportExport";
import Snapshots from "./settings/Snapshots";
import { TranslationProvider } from "../Providers/TranslationProvider";
import TabSidebarOnPages from "./settings/TabSidebarOnPages";
import { SettingsProvider } from "../Providers/SettingsProvider";
import QuickAccess from "./settings/QuickAccess";
import Premium from "./premium";

initSentry();

const container = document.getElementById("root");
const root = createRoot(container);
OverlayScrollbars(document.body, { scrollbars: { autoHide: "scroll" } });

const router = createHashRouter([
    {
        path: "/",
        element: <Options><Appearance /></Options>
    },
    {
        path: "beautify",
        element: <Options><Beautify /></Options>
    },
    {
        path: "tabs",
        element: <Options><Tabs /></Options>
    },
    {
        path: "history",
        element: <Options><History /></Options>
    },
    {
        path: "quickAccess",
        element: <Options><QuickAccess /></Options>
    },
    {
        path: "shortcuts",
        element: <Options><Shortcuts /></Options>
    },
    {
        path: "pip",
        element: <Options><PictureInPicture /></Options>
    },
    {
        path: "chromeTabGroup",
        element: <Options><ChromeTabGroup /></Options>
    },
    {
        path: "progressBar",
        element: <Options><ProgressBar /></Options>
    },
    {
        path: "floatball",
        element: <Options><FloatBall /></Options>
    },
    {
        path: "performance",
        element: <Options><Performance /></Options>
    },
    {
        path: "tabSidebarOnPages",
        element: <Options><TabSidebarOnPages /></Options>
    },
    {
        path: "sites",
        element: <Options><SiteList /></Options>
    },
    {
        path: "sites/:hostname",
        element: <Options><SiteConfig /></Options>
    },
    {
        path: "import_export",
        element: <Options><ImportExport /></Options>
    },
    {
        path: "snapshots",
        element: <Options><Snapshots /></Options>
    },
    {
        path: "premium",
        element: <Options><Premium /></Options>
    },


])

root.render(
    <SettingsProvider>
        <App>
            <SentryErrorBoundary>
                <TranslationProvider defaultNS="options">
                    <RouterProvider router={router} />
                </TranslationProvider>
            </SentryErrorBoundary>
        </App>
    </SettingsProvider>

);
