import React, { useState } from "react";

import { Alert, AlertTitle, Link, Typography, Collapse, IconButton, Box } from "@mui/material";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export default function UserActivationAlert() {

    const { t } = useTranslationContext();
    const [showDetails, setShowDetails] = useState(false);

    const handleToggleDetails = () => {
        setShowDetails(!showDetails);
    };

    return (
        <Box>
            <Alert severity="warning" onClick={handleToggleDetails} style={{ cursor: 'pointer', width: 'auto' }}>
                <Typography variant="h4">
                    {t('settings.userActivitionAlert.title')}
                    <IconButton
                        onClick={handleToggleDetails}
                        aria-expanded={showDetails}
                        aria-label="show more"
                        size="small"
                        style={{ transform: showDetails ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.3s' }}
                    >
                        <ExpandMoreIcon />
                    </IconButton>
                </Typography>
                <Collapse in={showDetails}>
                    <Typography component="p">{t('settings.userActivitionAlert.tips_1')} </Typography>
                    <Typography component="li">{t('settings.userActivitionAlert.tips_2')} =&gt; {t('click')}/{t('contextmenu')}/{t('dblclick')} </Typography>
                    <Typography component="strong">{t('settings.userActivitionAlert.tips_3')} </Typography>
                    <Typography component="p">{t('settings.userActivitionAlert.tips_4')}<Link href="https://developer.mozilla.org/en-US/docs/Web/Security/User_activation" target="_blank" rel="noopener noreferrer">https://developer.mozilla.org/en-US/docs/Web/Security/User_activation</Link>  </Typography>
                </Collapse>
            </Alert>
        </Box>
    );
}