import React, { useEffect, useState } from 'react';
import { Alert, Box, IconButton, Typography, Collapse, Button } from '@mui/material';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import { Close, ExpandMore, ExpandLess } from '@mui/icons-material';

const ScreenSizeTips = () => {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.alerts.screenSizeTips" });
    const [showAlert, setShowAlert] = useState(false);
    const [showSidebarRelease, setShowSidebarRelease] = useState(false);
    const [devicePixelRatio, setDevicePixelRatio] = useState(window.devicePixelRatio);
    const [percent, setPercent] = useState(0);
    const [expanded, setExpanded] = useState(false);

    useEffect(() => {
        const handleResize = () => {
            const screenWidth = window.screen.width;
            const sidebarWidth = 360;
            const remainingWidth = screenWidth - sidebarWidth;
            const remainingWidthRatio = remainingWidth / screenWidth;

            setPercent(Math.floor((sidebarWidth / screenWidth) * 100));

            if (remainingWidthRatio <= 0.78) {
                setShowAlert(true);
            } else {
                setShowAlert(false);
            }
        };

        const checkStorage = async () => {
            try {
                const { screenSizeAlertClosed, sidebarReleaseClosed } = await chrome.storage.local.get(['screenSizeAlertClosed', 'sidebarReleaseClosed']);
                if (screenSizeAlertClosed) setShowAlert(false);
                if (!sidebarReleaseClosed) setShowSidebarRelease(true);
            } catch (error) {
                console.error("Error accessing storage:", error);
            }
        };

        window.addEventListener('resize', handleResize);
        handleResize();
        checkStorage();

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const handleClickSidebarRelease = async () => {
        try {
            setShowSidebarRelease(false);
            await chrome.storage.local.set({ sidebarReleaseClosed: true });
        } catch (error) {
            console.error("Error setting storage:", error);
        }
    };

    const handleClickScreenWarning = async () => {
        try {
            setShowAlert(false);
            await chrome.storage.local.set({ screenSizeAlertClosed: true });
        } catch (error) {
            console.error("Error setting storage:", error);
        }
    };

    const formatMessage = (message) => {
        try {
            return message
                .replace(/\*\*(.*?)\*\*/g, '<b>$1</b>') // bold
                .replace(/\[(.*?)\]\((.*?)\)/g, (match, p1, p2) => {
                    const isInternal = p2.startsWith('/options.html');
                    return `<a href="${p2}"${isInternal ? '' : ' target="_blank"'}>${p1}</a>`;
                }) // link
                .replace(/\s*-\s+(.*?)(?=\n|$)/gm, '<li>$1</li>') // list item
                .replace(/\n/g, '<br>'); // newline
        } catch (error) {
            console.error("Error formatting message:", error);
            return message;
        }
    };

    return (
        <Box sx={{ width: "100%", mb: 2 }}>

            {showAlert && (
                <Box sx={{ width: '100%', my: 1 }}>
                    <Alert severity="warning"
                        action={
                            <IconButton onClick={handleClickScreenWarning}><Close /></IconButton>
                        }
                        sx={{ width: '100%' }}
                    >
                        <Typography>
                            {t('screenSizeWarningMessage', { percent: percent }).split('\n')[0]}
                            <Button onClick={() => setExpanded(!expanded)}>
                                {expanded ? <ExpandLess /> : <ExpandMore />}
                                {expanded ? t('showLess') : t('showMore')}
                            </Button>
                        </Typography>
                        <Collapse in={expanded}>
                            <Typography sx={{ mt: 2 }} dangerouslySetInnerHTML={{ __html: formatMessage(t('screenSizeWarningMessage', { percent: percent }).split('\n').slice(1).join('\n')) }} />
                            {devicePixelRatio > 1 && (
                                <Typography sx={{ mt: 2 }} dangerouslySetInnerHTML={{ __html: formatMessage(t('scaleWarningMessage', { scale: devicePixelRatio * 100 })) }} />
                            )}
                        </Collapse>
                    </Alert>
                </Box>
            )}
            {showAlert && showSidebarRelease && (
                <Box sx={{ width: '100%', my: 1 }}>
                    <Alert severity="success"
                        action={
                            <IconButton onClick={handleClickSidebarRelease}><Close /></IconButton>
                        }
                        sx={{ width: '100%' }}
                    >
                        <Typography dangerouslySetInnerHTML={{ __html: formatMessage(t('pageSidebarRelase', { percent: percent })) }} />
                    </Alert>
                </Box>
            )}
        </Box>
    );
};

export default ScreenSizeTips;
