import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Typography, ToggleButton, ToggleButtonGroup, Alert } from "@mui/material";
import { KeyboardArrowRight } from "@mui/icons-material";
import { KeyboardArrowLeft } from "@mui/icons-material";
import { History } from "@mui/icons-material";
import { RemoveFromQueue } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { toast } from "react-toastify";
export default function HistoryDataSource() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.dataSource" });
    const { updateSetting, enableHistory, historyDataSource } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,
        historyDataSource: state.settings.historyDataSource

    }));


    const handleChange = async (e, v) => {
        if (v === 'chrome') {
            chrome.permissions.contains({ permissions: ["history"] }, async (granted) => {
                if (!granted) {
                    chrome.permissions.request({ permissions: ["history"] }, async (granted) => {
                        const val = granted ? "chrome" : "recentTabs"
                        if (!granted) {
                            toast.error(t("deniedMessage"));
                        }
                        await updateSetting("historyDataSource", val)
                        await sendRuntimeMessage("update_setting", { key: "historyDataSource", value: val });
                    })
                } else {
                    await updateSetting("historyDataSource", v)
                    await sendRuntimeMessage("update_setting", { key: "historyDataSource", value: v })
                }
            })

        } else {
            await updateSetting("historyDataSource", v)
            await sendRuntimeMessage("update_setting", { key: "historyDataSource", value: v })
        }


    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup disabled={!enableHistory} size="small" value={historyDataSource} exclusive>
                    <ToggleButton value="recentTabs" onClick={handleChange}>
                        <RemoveFromQueue sx={{ mr: 1 }} /> <Typography variant="body1">{t("recentlyClosedTabs")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="chrome" onClick={handleChange}>
                        <History sx={{ mr: 1 }} /> <Typography variant="body1">{t("chromeHistory")}</Typography>
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
            <Grid item xs={12} mt={1}>
                <Alert severity="warning">
                    <Typography component="p">{t('alert')} </Typography>
                </Alert>
            </Grid>
        </Grid>

    )
}