import React, { useContext } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, InputAdornment, OutlinedInput, Stack, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { sendMessageToAllTabs } from "../../../../core/browers/tabs";



export default function ScrollHeightRatio() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.progressBar.scrollHeightRatio' });

    const { updateSetting, enableProgressBar, scrollHeightRatio } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableProgressBar: state.settings.enableProgressBar,

        scrollHeightRatio: state.settings.scrollHeightRatio
    }));


    const handleChange = async (event) => {
        await updateSetting('scrollHeightRatio', (event.target.value))
        await sendRuntimeMessage("update_setting", { key: "scrollHeightRatio", value: (event.target.value) })
        await sendMessageToAllTabs("update_setting", { key: "scrollHeightRatio", value: event.target.value })
    }


    return (

        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '200px' }} variant="outlined">
                    <OutlinedInput
                        disabled={!enableProgressBar}
                        value={scrollHeightRatio}
                        inputProps={{ itemType: 'number', max: 100, min: 1, step: 0.1 }}
                        // endAdornment={<InputAdornment position="end">{t('seconds')}</InputAdornment>}
                        aria-describedby="outlined-weight-helper-text"
                        onChange={handleChange}
                    />
                </FormControl>
            </Grid>
        </Grid>

    )
}