import React, { useState, useEffect } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Typography, FormControlLabel, Switch, Button, Dialog, DialogActions, DialogContent, DialogTitle, Box, Paper, IconButton, ListItem, Stack, Divider, Card } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { DndContext, closestCenter, MouseSensor, TouchSensor, useSensor, useSensors } from '@dnd-kit/core';
import { arrayMove, SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import DeleteIcon from '@mui/icons-material/Delete';
import { ArrowRight } from "@mui/icons-material";
import { Close } from "@mui/icons-material";
import { Add } from "@mui/icons-material";
import { toast } from "react-toastify";

export default function ShowBottomNav() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.bottomNav" });

    const { updateSetting, showBottomNav, bottomNavOptions } = userStore((state) => ({
        updateSetting: state.updateSetting,
        showBottomNav: state.settings.showBottomNav,
        bottomNavOptions: state.settings.bottomNavOptions
    }));

    const [open, setOpen] = useState(false);

    const handleShowBottomNavChange = async (event) => {
        await updateSetting('showBottomNav', event.target.checked);
        await sendRuntimeMessage("update_setting", { key: "showBottomNav", value: event.target.checked });
    };

    const handleOptionShowLabelChange = async (event) => {
        const newShowBottomNav = { ...bottomNavOptions, showLabel: event.target.checked };
        await updateSetting('bottomNavOptions', newShowBottomNav);
        await sendRuntimeMessage("update_setting", { key: "bottomNavOptions", value: newShowBottomNav });
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleDragEnd = async (event) => {
        const { active, over } = event;
        if (!active?.id || !over?.id || active.id === over.id) return;
        const oldIndex = bottomNavOptions.navs.findIndex(item => item.id === active.id);
        const newIndex = bottomNavOptions.navs.findIndex(item => item.id === over.id);
        const newNavs = arrayMove(bottomNavOptions.navs, oldIndex, newIndex).map((item, index) => ({ ...item, sort: index + 1 }));
        const newOptions = { ...bottomNavOptions, navs: newNavs };
        await updateSetting('bottomNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "bottomNavOptions", value: newOptions });

    };

    const handleEnable = async (id) => {
        const newNavs = bottomNavOptions.navs.map((item) =>
            item.id === id ? { ...item, enable: true } : item
        );
        const newOptions = { ...bottomNavOptions, navs: newNavs };
        await updateSetting('bottomNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "bottomNavOptions", value: newOptions });
    };

    const handleSwitchLabel = async (id) => {
        const newNavs = bottomNavOptions.navs.map((item) =>
            item.id === id ? { ...item, showLabel: !item.showLabel } : item
        );
        const newOptions = { ...bottomNavOptions, navs: newNavs };
        await updateSetting('bottomNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "bottomNavOptions", value: newOptions });
    };

    const handleDelete = async (id) => {

        const curNav = bottomNavOptions.navs.find((item) => item.id === id);
        if (curNav.name === 'version') {
            const { isOnToolbar } = await chrome.action.getUserSettings();
            if (!isOnToolbar) {
                toast(t('pinToToolbarReminder'));
                return;
            }
        }

        const newNavs = bottomNavOptions.navs.map((item) =>
            item.id === id ? { ...item, enable: false } : item
        );
        const newOptions = { ...bottomNavOptions, navs: newNavs };
        await updateSetting('bottomNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "bottomNavOptions", value: newOptions });
    };

    const SortableItem = (props) => {
        const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({ id: props.id });
        const style = {
            transform: CSS.Transform.toString(transform),
            transition,
            zIndex: isDragging ? 1000 : 'auto',
            opacity: isDragging ? 0.8 : 1,
            display: 'flex',
            alignItems: 'center'
        };
        return (
            <Card
                sx={{ border: "1px solid #e0e0e0", borderRadius: 2, p: 2, display: 'flex', alignItems: 'center', cursor: 'move', boxShadow: 3, position: "relative" }}
                ref={setNodeRef}
                style={style}
                {...attributes}
                {...listeners}
                component="div"
            >
                <IconButton
                    size="small"
                    onClick={() => handleDelete(props.id)}
                    sx={{ position: 'absolute', top: 0, right: 0 }}
                >
                    <Close color="error" fontSize="small" />
                </IconButton>
                <Stack direction="column" spacing={2} useFlexGap sx={{ padding: '5px' }} >
                    <Typography
                        variant="h4"
                        color="error"
                        size="small"
                        sx={{ cursor: 'move' }}
                    >
                        {t(props.name)}
                    </Typography>
                    <FormControlLabel
                        control={
                            <Switch
                                size="small"
                                color="error"
                                disabled={!bottomNavOptions.showLabel}
                                checked={props.showLabel && bottomNavOptions.showLabel}
                                onChange={() => handleSwitchLabel(props.id)}
                                name='showLabel'
                            />
                        }
                        label={t('showLabel')}
                    />


                </Stack>
            </Card>
        );
    };

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    return (
        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={2}>
                <FormControlLabel control={<Switch color="error" checked={showBottomNav}
                    onChange={handleShowBottomNavChange} />}
                    labelPlacement="start"
                />
            </Grid>
            {showBottomNav &&
                <Grid item xs={2}>
                    <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={handleOpen}>
                        {t('configure')}
                    </Button>
                </Grid>
            }
            <Dialog fullWidth={true} maxWidth="lg" open={open}
                onClose={(event, reason) => { if (reason !== 'backdropClick') { handleClose } }}
            >
                <DialogTitle>{t("configureBottomNav")}</DialogTitle>
                <DialogContent>
                    <Box mb={3}>
                        <FormControlLabel

                            control={
                                <Switch
                                    color="error"
                                    checked={bottomNavOptions.showLabel}
                                    onChange={handleOptionShowLabelChange}
                                    name={"showLabel"}
                                />
                            }
                            label={t('showLabel')}
                        />
                        <Divider sx={{ my: 2 }} />
                        <Typography variant="h3" gutterBottom>{t("enabledNavs")}</Typography>
                        <Paper variant="outlined" style={{ padding: '16px', marginBottom: '16px' }}>
                            <DndContext
                                onDragEnd={handleDragEnd}
                                sensors={sensors}
                            >
                                <SortableContext
                                    items={bottomNavOptions.navs.filter(option => option.enable).map(option => option.id).sort((a, b) => a.sort - b.sort)}
                                // strategy={verticalListSortingStrategy}
                                >
                                    <Box display="flex" flexWrap="wrap" gap={1}>
                                        {bottomNavOptions.navs.filter(option => option.enable).map((option) => (
                                            <SortableItem key={option.id} id={option.id} name={option.name} showLabel={option.showLabel} />
                                        ))}
                                    </Box>
                                </SortableContext>
                            </DndContext>
                        </Paper>
                    </Box>
                    <Box mb={3}>
                        <Typography variant="h3" gutterBottom>{t("disabledNavs")}</Typography>
                        <Paper variant="outlined" style={{ padding: '16px' }}>
                            <Box display="grid" gridTemplateColumns="repeat(auto-fill, minmax(200px, 1fr))" gap={1}>
                                {bottomNavOptions.navs.filter(option => !option.enable).sort((a, b) => a.id - b.id).map((option) => (
                                    <Button key={option.id} endIcon={<Add />} variant="outlined" size="small" onClick={() => handleEnable(option.id)}>
                                        {t(option.name)}
                                    </Button>
                                ))}
                            </Box>
                        </Paper>
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="error">
                        {t("close")}
                    </Button>
                </DialogActions>
            </Dialog>
        </Grid>
    );
}