
import React, { useEffect } from "react";
import Box from "@mui/material/Box";
import { siteStore } from "../../../../core/stores/siteStore";
import SiteItem from "./SiteItem";
import { Divider, Grid, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import SearchBox from "./SearchBox";
import AddNewSite from "./AddNewSite";


export default function SiteList() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.site' });

    let { sites, searchText } = siteStore((state) => ({
        sites: state.sites,
        searchText: state.searchText
    }))

    if (searchText.trim().length > 0) {
        const keywords = searchText.trim().toLowerCase().split(" ").filter(Boolean);
        sites = sites.filter(site =>
            Object.values(site)
                .filter(value => typeof value === "string")
                .some(value => keywords.every(kw => value.toLowerCase().includes(kw)))
        );
    }


    console.log("sites", sites)

    useEffect(() => {

    }, [])


    return (
        <Box>
            <Grid container mb={1} alignItems="center" justifyContent="space-between">
                <Grid item xs={8}>
                    <Typography variant="h1">{t("label")}</Typography>
                </Grid>
                <Grid item xs={4}> <SearchBox /></Grid>

            </Grid>

            <Divider><AddNewSite /></Divider>
            <Box my={2}>


                {sites.map((site, index) => {
                    return <SiteItem key={index} hostname={site.hostname} />
                })
                }
            </Box>
        </Box>
    )
}