import React from "react";
import { Box, Button, Paper, Stack, Typography } from "@mui/material";
import WindowTabItem from "./WindowTabItem";
import snapshotStore from "../../../../core/stores/snapshotStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

const WindowItem = ({ windowIndex, window, restoreWindowSize }) => {
    const { t } = useTranslationContext('options', { keyPrefix: 'snapshots' });

    const { selectdWindowTabs, selectedTabsCount } = snapshotStore((state) => ({
        selectdWindowTabs: state.selectedTabs[window.id] || [],
        selectedTabsCount: (state.selectedTabs[window.id] || []).length
    }));

    const handleOpenWindow = () => {
        const tabsToOpen = selectdWindowTabs.length > 0 ? selectdWindowTabs.map(tabId => window.tabs.find(tab => tab.id === tabId)) : window.tabs;

        const firstTab = tabsToOpen.shift();
        const createWindowOptions = {
            url: firstTab.url,
            ...(restoreWindowSize && { left: window.left, top: window.top, width: window.width, height: window.height })
        };

        chrome.windows.create(createWindowOptions, (newWindow) => {
            const groupTabs = {};

            tabsToOpen.forEach((tab, index) => {
                setTimeout(() => {
                    chrome.tabs.create({ windowId: newWindow.id, url: tab.url, active: false }, (createdTab) => {
                        if (tab.groupId > 0) {
                            if (!groupTabs[tab.groupId]) {
                                groupTabs[tab.groupId] = [];
                            }
                            groupTabs[tab.groupId].push(createdTab.id);
                        }
                    });
                }, index * 250);
            });

            if (selectdWindowTabs.length === 0) {
                setTimeout(() => {
                    Object.keys(groupTabs).forEach(groupId => {
                        if (groupId && window.tabGroups) {
                            const group = window.tabGroups.find(group => group.id === parseInt(groupId));
                            if (group) {
                                chrome.tabs.group({ tabIds: groupTabs[groupId], createProperties: { windowId: newWindow.id } }, (newGroupId) => {
                                    chrome.tabGroups?.update(newGroupId, { color: group.color, title: group.title });
                                });
                            }
                        }
                    });
                }, tabsToOpen.length * 250 + 500);
            }
        });
    };

    const handleOpenInCurrentWindow = () => {
        const tabsToOpen = selectdWindowTabs.length > 0 ? selectdWindowTabs.map(tabId => window.tabs.find(tab => tab.id === tabId)) : window.tabs;
        const groupTabs = {};

        tabsToOpen.forEach((tab, index) => {
            setTimeout(() => {
                chrome.tabs.create({ url: tab.url, active: false }, (createdTab) => {
                    if (tab.groupId > 0) {
                        if (!groupTabs[tab.groupId]) {
                            groupTabs[tab.groupId] = [];
                        }
                        groupTabs[tab.groupId].push(createdTab.id);
                    }
                });
            }, index * 250);
        });

        if (selectdWindowTabs.length === 0) {
            setTimeout(() => {
                Object.keys(groupTabs).forEach(groupId => {
                    if (groupId && window.tabGroups) {
                        const group = window.tabGroups.find(group => group.id === parseInt(groupId));
                        if (group) {
                            chrome.tabs.group({ tabIds: groupTabs[groupId] }, (newGroupId) => {
                                chrome.tabGroups?.update(newGroupId, { color: group.color, title: group.title });
                            });
                        }
                    }
                });
            }, tabsToOpen.length * 250 + 500);
        }
    };

    const handleResetSelection = () => {
        window.tabs.forEach(tab => {
            snapshotStore.getState().removeSelectTabId(tab.id, window.id);
        });
    };

    return (
        <Paper elevation={3} style={{ margin: '16px 0', padding: '16px' }}>
            <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ marginBottom: "16px" }}>
                <Stack direction="row" spacing={2} alignItems="center" justifyContent="flex-start">
                    <Typography variant="h3">{t('Window')} {windowIndex + 1}</Typography>
                    <Typography variant="h5">({t('tabCount', { count: window.tabs.length })})</Typography>
                </Stack>
                <Stack direction="row" spacing={1} alignItems="center" justifyContent="flex-end">
                    <Button variant="outlined" color="primary" onClick={handleOpenInCurrentWindow} style={{ marginLeft: '8px' }}>
                        {t('OpenInCurrentWindow')} {selectedTabsCount ? ` (${selectedTabsCount})` : ""}
                    </Button>
                    <Button variant="outlined" color="error" onClick={handleOpenWindow}>
                        {t('OpenInNewWindow')} {selectedTabsCount ? ` (${selectedTabsCount})` : ""}
                    </Button>
                    {selectedTabsCount > 0 && (
                        <Button variant="outlined" color="secondary" onClick={handleResetSelection}>
                            {t('ResetSelection')}
                        </Button>
                    )}
                </Stack>
            </Stack>
            <Box>
                {window.tabs.map((tab, tabIndex) => (
                    <WindowTabItem
                        key={tab.id}
                        tabIndex={tabIndex}
                        item={tab}
                    />
                ))}
            </Box>
        </Paper>
    );
};

export default WindowItem;