import React from "react";
import { Box, CardMedia, Divider, Grid, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import EnableProgressBar from "./EnableProgressBar";
import ReadingProgressBar from "./ReadingProgressBar";
import PlayingProgressBar from "./PlayingProgressBar";
import ScrollHeightRatio from "./ScrollHeightRatio";


export default function ProgressBar() {
    const { t } = useTranslationContext('options', { keyPrefix: 'settings' });



    return (

        <Box>
            <Grid container alignItems="center" mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("modules.progressBar")}</Typography></Grid>
                {/* <Grid item xs={4}><Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings',)}</Button></Grid> */}
                {/* <Grid item xs={2}><Button color="error" onClick={handleResetAllSettings}>{t('Reset all settings')}</Button></Grid> */}
            </Grid>
            {/* <Typography variant="h1">{t('modules.progressBar')}</Typography> */}
            <EnableProgressBar />
            <Divider sx={{ my: 2 }} />
            <Grid container alignItems="space-around" >
                <Grid item xs={4}>
                    <CardMedia component='img' sx={{ width: "100%", padding: "8px" }} image={chrome.runtime.getURL('/images/helps/readingProgressBar.jpg')} />
                </Grid>
                <Grid item xs={8} sx={{ padding: "8px" }}>
                    <ReadingProgressBar />
                    <Divider sx={{ my: 2 }} />
                    <ScrollHeightRatio />

                </Grid>
            </Grid>
            <Divider sx={{ my: 2 }} />
            <Grid container alignItems="space-around" >
                <Grid item xs={4}>
                    <CardMedia component='img' sx={{ width: "100%", padding: "8px" }} image={chrome.runtime.getURL('/images/helps/playingProgressBar.jpg')} />
                </Grid>
                <Grid item xs={8} sx={{ padding: "8px" }}>

                    <PlayingProgressBar />

                </Grid>
            </Grid>
            <Divider sx={{ my: 2 }} />
        </Box>
    )
}