import * as React from 'react';
import Box from '@mui/material/Box';
import Alert from '@mui/material/Alert';
import Collapse from '@mui/material/Collapse';
import Button from '@mui/material/Button';
import CloseIcon from '@mui/icons-material/Close';

import { sendRuntimeMessage } from '../../../core/browers/runtime';
import { ArrowRightAlt } from '@mui/icons-material';
import { ButtonGroup } from '@mui/material';
import { useTranslationContext } from '../../Providers/TranslationProvider';

export default function PreviewTips() {
    const [open, setOpen] = React.useState(true);
    const { t } = useTranslationContext("options")
    const handleClick = async () => {
        if (('sidePanel' in chrome)) {
            await sendRuntimeMessage('open_sidepanel');
        } else {
            await sendRuntimeMessage('open_popup');
        }
    };

    return (
        <Box sx={{ width: '100%' }}>
            <Collapse in={open}>
                <Alert
                    action={
                        <ButtonGroup variant='text'>
                            <Button onClick={handleClick}><ArrowRightAlt />{t(('sidePanel' in chrome) ? 'settings.appearance.extAction.openSidePanel' : 'settings.appearance.extAction.openPopup')}</Button>
                            <Button
                                aria-label="close"
                                color="inherit"
                                size="small"

                            >
                                <CloseIcon fontSize="inherit" onClick={() => {
                                    setOpen(false);
                                }} />

                            </Button>
                        </ButtonGroup>

                    }
                    sx={{ mb: 2 }}
                >
                    {t('OpenSidePanelToPreview')}
                </Alert>
            </Collapse>
        </Box>
    );
}
