import React, { useContext } from "react";

import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography, Alert, Tooltip } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import userStore from "../../../../core/stores/userStore";
import { Link } from "react-router-dom";



export default function PlayingProgressBar({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.playingProgressBar" });

    const { globalPlayingProgressBar } = userStore((state) => ({
        globalPlayingProgressBar: state.settings.playingProgressBar && state.settings.enableProgressBar
    }))

    const { playingProgressBar, setSiteConfig } = siteStore((state) => ({
        playingProgressBar: state.sites.find(site => site.hostname === hostname)?.playingProgressBar,
        setSiteConfig: state.setSiteConfig
    }));

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { playingProgressBar: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { playingProgressBar: event.target.checked } });

    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>

                <FormControlLabel control={<Switch disabled={!globalPlayingProgressBar} color="error" checked={globalPlayingProgressBar && playingProgressBar !== false}
                    onChange={handleChange} />}
                />

            </Grid>

            {!globalPlayingProgressBar && <Link target="_blank" to={`/progressBar`}><Alert severity="warning">{t('globaldisabledTips')}</Alert></Link>}

        </Grid>

    )

}
