import React from "react";
import { Grid, ToggleButton, ToggleButtonGroup, Typography } from "@mui/material";
import { userStore } from "../../../../core/stores/userStore";

import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function TabHistorySortBy() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.tabHistorySortBy" });


    const { updateSetting, tabHistoryEnable, tabHistorySortBy } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabHistoryEnable: state.settings.tabHistoryEnable,
        tabHistorySortBy: state.settings.tabHistorySortBy

    }));



    const handleChange = async (e, v) => {
        if (v) {
            await updateSetting("tabHistorySortBy", v)
            await sendRuntimeMessage("update_setting", { key: "tabHistorySortBy", value: v })
        }
    }



    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup disabled={!tabHistoryEnable} size="small" value={tabHistorySortBy} exclusive>
                    <ToggleButton value="lastAccessed" onClick={handleChange}>
                        <Typography variant="body1">{t("lastAccessed")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="createdAt" onClick={handleChange}>
                        <Typography variant="body1">{t("createdAt")}</Typography>
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
        </Grid>

    )

}
