import React, { useContext } from "react";

import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography, Alert } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import userStore from "../../../../core/stores/userStore";
import { Link } from "react-router-dom";



export default function AutoEnterPictureInPicture({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.autoEnterPictureInPicture" });




    const { autoEnterPictureInPicture, setSiteConfig } = siteStore((state) => ({
        autoEnterPictureInPicture: state.sites.find(site => site.hostname === hostname)?.autoEnterPictureInPicture,
        setSiteConfig: state.setSiteConfig
    }));

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { autoEnterPictureInPicture: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { autoEnterPictureInPicture: event.target.checked } });
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <FormControlLabel control={<Switch color="error" checked={autoEnterPictureInPicture === true}
                    onChange={handleChange} />}
                />
            </Grid>
        </Grid>

    )

}
