import React from "react";
import { Grid, FormControlLabel, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function ReadingProgressBar() {
    const { t } = useTranslationContext('options', { keyPrefix: 'settings.progressBar.readingProgressBar' });

    const { enableProgressBar, readingProgressBar, updateSetting } = userStore((state) => ({
        enableProgressBar: state.settings.enableProgressBar,
        readingProgressBar: state.settings.readingProgressBar,
        updateSetting: state.updateSetting
    }));

    const handleChanged = async (event) => {
        const isChecked = event.target.checked;
        await updateSetting('readingProgressBar', isChecked);
        await sendRuntimeMessage("update_setting", { key: "readingProgressBar", value: isChecked });
    }

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel
                    disabled={!enableProgressBar}
                    control={<Switch color="error" checked={enableProgressBar && readingProgressBar} onChange={handleChanged} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>
    );
}