import React from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Typography, FormControlLabel, Switch } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { getDominantColor, getFontColor } from "../../../../utils/colord";

export default function ColorfulBackground() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.beautify.coloredbackground" });

    const { updateSetting, colorfulBackground, backgroundImageUrl, backgroundColor } = userStore((state) => ({
        updateSetting: state.updateSetting,
        colorfulBackground: state.settings.colorfulBackground,
        backgroundImageUrl: state.settings.backgroundImageUrl,
        backgroundColor: state.settings.backgroundColor,
    }));

    const handleColorfulBackgroundChange = async (event) => {
        const isChecked = event.target.checked;
        await updateSetting('colorfulBackground', isChecked);
        await sendRuntimeMessage("update_setting", { key: "colorfulBackground", value: isChecked });

        if (!isChecked) {
            await updateSetting("fontColor", "inherit");
            await sendRuntimeMessage("update_setting", { key: "fontColor", value: 'inherit' });
        } else {
            if (!backgroundImageUrl?.match(/(?:png|jpe?g|gif|unsplash)/) && !backgroundImageUrl.startsWith('data:image/')) {
                const fontColor = getFontColor(backgroundColor);
                await updateSetting('fontColor', fontColor);
                await sendRuntimeMessage("update_setting", { key: "fontColor", value: fontColor });
                return;
            }
            const image = new Image();
            image.crossOrigin = "Anonymous";
            image.src = backgroundImageUrl;
            image.onload = async () => {
                const dominantColor = getDominantColor(image);
                const fontColor = getFontColor(dominantColor);
                await updateSetting('fontColor', fontColor);
                await sendRuntimeMessage("update_setting", { key: "fontColor", value: fontColor });
            };
        }
    }

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel
                    control={<Switch color="error" checked={colorfulBackground} onChange={handleColorfulBackgroundChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>
    );
}