import React, { useState } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Typography, ToggleButton, ToggleButtonGroup, Tooltip, Stack, FormControlLabel, Switch } from "@mui/material";
import { customColors, getBackgroundByColor } from "../../../../utils/colors";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function ListBackgroundStyle() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.beautify.listBackgroundStyle" });

    const { updateSetting, colorfulBackground, listBackgroundStyle } = userStore((state) => ({
        updateSetting: state.updateSetting,
        colorfulBackground: state.settings.colorfulBackground,
        listBackgroundStyle: state.settings.listBackgroundStyle
    }));

    const [enabled, setEnabled] = useState(true);

    const handleChange = async (e, v) => {
        await updateSetting('listBackgroundStyle', v);
        await sendRuntimeMessage("update_setting", { key: "listBackgroundStyle", value: v });
    }

    const handleSwitchChange = async (event) => {
        const isChecked = event.target.checked;
        setEnabled(isChecked);
        const newValue = isChecked ? listBackgroundStyle : '';
        await updateSetting('listBackgroundStyle', newValue);
        await sendRuntimeMessage("update_setting", { key: "listBackgroundStyle", value: newValue });
    }

    const colors = colorfulBackground ? ['none', 'single', 'simple', 'complementary', 'analogous', 'triadic', 'tetradic'] : ['none'];

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel
                    control={<Switch color="primary" checked={enabled} onChange={handleSwitchChange} />}
                    labelPlacement="start"
                />
                {enabled && (
                    <Grid item xs={12}>
                        <ToggleButtonGroup size="small" value={listBackgroundStyle}>
                            {colors.map((item, index) => (
                                <Stack direction="row" useFlexGap flexWrap="wrap" spacing={1} key={index}>
                                    {[45, 225, 90, 270, 135, 315, 180, 0].map((deg) => (
                                        <Tooltip followCursor key={index + deg} arrow title={<Typography>{t(item) + " " + deg + "°"}</Typography>}>
                                            <ToggleButton
                                                sx={{
                                                    width: "32px", height: "32px", background: getBackgroundByColor(customColors[1], item, deg, 0.4), "&.Mui-selected": {
                                                        border: "2px solid #eb0028"
                                                    }
                                                }}
                                                value={item + "|" + deg}
                                                onClick={handleChange}
                                            />
                                        </Tooltip>
                                    ))}
                                </Stack>
                            ))}
                        </ToggleButtonGroup>
                    </Grid>
                )}
            </Grid>
        </Grid>
    );
}