import React, { useContext } from "react";
import { Box, Alert, FormControl, FormControlLabel, Grid, InputAdornment, OutlinedInput, Stack, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function HistorySearchLimitCount() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.limitSearchResults" });
    const { updateSetting, enableHistory, tabSearchShowHistory, historySearchLimitCount } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,
        tabSearchShowHistory: state.settings.tabSearchShowHistory,
        historySearchLimitCount: state.settings.historySearchLimitCount,
    }));

    const handleChange = async (event) => {
        await updateSetting('historySearchLimitCount', parseInt(event.target.value))
        await sendRuntimeMessage("update_setting", { key: "historySearchLimitCount", value: parseInt(event.target.value) })
    }


    return (

        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '100px' }} variant="outlined">
                    <OutlinedInput
                        disabled={!enableHistory || !tabSearchShowHistory}
                        value={historySearchLimitCount}
                        inputProps={{ itemType: 'number', max: 50, min: 1, step: 1 }}

                        onChange={handleChange}
                    />
                </FormControl>
            </Grid>
            <Grid item xs={12} mt={1}>
                <Alert severity="warning">
                    <Typography component="p">{t('alert')} </Typography>
                </Alert>
            </Grid>
        </Grid>

    )
}