import { Box, Button, Divider, Stack, Typography } from "@mui/material";
import React, { useEffect } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import snapshotStore from "../../../../core/stores/snapshotStore";
import SnapshotItem from "./SnapshotItem";
import { toast } from "react-toastify";
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";

snapshotStore.getState().fetchSummaries();

export default function SideList() {

    const allSummaries = snapshotStore(state => state.summaries)
        .sort((a, b) => b.createdAt - a.createdAt);
    const summaries = allSummaries.slice(0, 50);
    const snapshotsToRemove = allSummaries.slice(50);
    snapshotsToRemove.forEach(snapshot => snapshotStore.getState().removeSnapshot(snapshot.id));

    const { t } = useTranslationContext('options', { keyPrefix: 'snapshots' });

    useEffect(() => {
        const selectdSnapshotId = snapshotStore.getState().selectdSnapshotId;
        if (!selectdSnapshotId && summaries.length > 0) {
            snapshotStore.setState({ selectdSnapshotId: summaries[0].id });
        }
    }, [summaries]);

    const handleCreateSnapshot = async () => {
        console.log("createSnapshot");
        try {
            await snapshotStore.getState().addSnapshot();
            const latestSnapshot = snapshotStore.getState().summaries.sort((a, b) => b.createdAt - a.createdAt)[0];
            snapshotStore.setState({ selectdSnapshotId: latestSnapshot.id, selectedTabs: [] });

            // Keep only the latest 50 snapshots and remove snapshots older than 3 months
            const updatedSummaries = snapshotStore.getState().summaries.sort((a, b) => b.createdAt - a.createdAt);
            const latestFiftySummaries = updatedSummaries.slice(0, 50);
            const threeMonthsAgo = Date.now() - 3 * 30 * 24 * 60 * 60 * 1000;
            const snapshotsToRemove = updatedSummaries.filter(snapshot => !latestFiftySummaries.includes(snapshot) || snapshot.createdAt < threeMonthsAgo);
            for (const snapshot of snapshotsToRemove) {
                await snapshotStore.getState().removeSnapshot(snapshot.id);
            }
            snapshotStore.setState({ summaries: latestFiftySummaries.filter(snapshot => snapshot.createdAt >= threeMonthsAgo) });

            toast.success(t('snapshotCreatedSuccess'));
        } catch (error) {
            toast.error(t('snapshotCreatedFailed'));
        }
    }

    const handleImportSnapshot = async (event) => {
        const file = event.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onload = async (e) => {
                try {
                    const importedData = JSON.parse(e.target.result);
                    if (Array.isArray(importedData.snapshots)) {
                        let successCount = 0;
                        let failureCount = 0;
                        for (const snapshot of importedData.snapshots) {
                            const existingSnapshot = snapshotStore.getState().summaries.find(s => s.id === snapshot.id);
                            if (existingSnapshot) {
                                failureCount++;
                            } else {
                                try {
                                    await snapshotStore.getState().addImportedSnapshot(snapshot);
                                    successCount++;
                                } catch (error) {
                                    failureCount++;
                                }
                            }
                        }
                        if (successCount > 0) {
                            toast.success(t('snapshotImportedSuccess', { count: successCount, error: "" }));
                        }
                        if (failureCount > 0) {
                            toast.error(t('snapshotImportFailed', { count: failureCount, error: "" }));
                        }
                    } else {
                        const existingSnapshot = snapshotStore.getState().summaries.find(snapshot => snapshot.id === importedData.id);
                        if (existingSnapshot) {
                            toast.error(t('snapshotImportFailedDuplicate'));
                        } else {
                            await snapshotStore.getState().addImportedSnapshot(importedData);
                            toast.success(t('snapshotImportedSuccess', { count: 1 }));
                        }
                    }
                } catch (error) {
                    console.error("Error importing snapshot:", error);
                    toast.error(t('snapshotImportFailed', { count: 0, error: error.message }));
                }
            };
            reader.readAsText(file);
        }
    }

    return (
        <Box>
            <Box mb={2}>
                <Button size="large" variant="contained" color="error" onClick={handleCreateSnapshot}>{t('createSnapshot')}</Button>
            </Box>
            <Box mb={2}>
                <Button size="large" variant="contained" component="label">
                    {t('importSnapshot')}
                    <input type="file" accept=".json" hidden onChange={handleImportSnapshot} />
                </Button>
            </Box>
            <Divider >
                <Typography variant="h4" sx={{ py: 2 }}>{t('title')}:{summaries.length}</Typography>
            </Divider>
            <OverlayScrollbarsComponent defer={true} style={{ maxHeight: '60vh' }}>
                <Stack direction="column" spacing={1} alignItems="center" justifyContent="center">
                    {summaries.map(summary => <SnapshotItem key={summary.id} summary={summary} />)}
                </Stack>
            </OverlayScrollbarsComponent>
        </Box>
    )
}