import React from "react";
import { Box, Divider, Alert, Grid, Stack, Typography, Button } from "@mui/material";
import userStore from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import SaveMemoryAction from "./SaveMemoryAction";
import DiscardInactiveTabsMinutes from "./DiscardInactiveTabsMinutes";

import CloseInactiveTabsMinutes from "./CloseInactiveTabsMinutes";
import DisableAnimations from "./DisableAnimations";


export default function Performance() {
    const { t } = useTranslationContext('options', { keyPrefix: 'settings.performance' });

    const { updateSetting, saveMemoryAction } = userStore((state) => ({
        updateSetting: state.updateSetting,

        saveMemoryAction: state.settings.saveMemoryAction

    }));
    return (

        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("label")}</Typography></Grid>
                <Grid item xs={4}>
                    {/* <Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings')}</Button> */}
                </Grid>

            </Grid>
            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                <SaveMemoryAction />
                {saveMemoryAction === "discard" && <DiscardInactiveTabsMinutes />}
                {saveMemoryAction === "close" && <CloseInactiveTabsMinutes />}
                <DisableAnimations />
            </Stack>

        </Box>
    )
}