import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, RadioGroup, FormControl, FormLabel, Radio, Typography, Grid } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function AutoActivateTabGroupingName() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.chromeTabGroupAdvanced.autoActivateTabGroupingName' });

    const { updateSetting, autoActivateTabGrouping, autoActivateTabGroupingName } = userStore((state) => ({
        updateSetting: state.updateSetting,
        autoActivateTabGrouping: state.settings.autoActivateTabGrouping,
        autoActivateTabGroupingName: state.settings.autoActivateTabGroupingName,
    }));


    const handleChange = async (event) => {
        await updateSetting('autoActivateTabGroupingName', event.target.value)
        await sendRuntimeMessage("update_setting", { key: "autoActivateTabGroupingName", value: event.target.value })
    }
    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl component="fieldset" disabled={!autoActivateTabGrouping}>
                    <FormLabel component="legend">{t('label')}</FormLabel>
                    <RadioGroup
                        aria-label="autoActivateTabGroupingName"
                        name="autoActivateTabGroupingName"
                        value={autoActivateTabGroupingName}
                        onChange={handleChange}
                    >
                        <FormControlLabel value="none" control={<Radio />} label={t('none')} />
                        <FormControlLabel value="title" control={<Radio />} label={t('title')} />
                        <FormControlLabel value="domain" control={<Radio />} label={t('domain')} />
                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>

    )
}