import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";

import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, ToggleButton, ToggleButtonGroup, Typography } from "@mui/material";

import { LightMode } from "@mui/icons-material";
import { DarkMode } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function Theme() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.theme" });

    const { updateSetting, theme } = userStore((state) => ({
        updateSetting: state.updateSetting,
        theme: state.settings.theme,
    }));


    const handleThemeChange = async (e, v) => {
        await updateSetting("theme", v)
        await sendRuntimeMessage("update_setting", { key: "theme", value: v })
    }

    return (
        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={theme} exclusive>
                    <ToggleButton value="light" onClick={handleThemeChange}>
                        <LightMode />
                    </ToggleButton>
                    <ToggleButton value="dark" onClick={handleThemeChange}>
                        <DarkMode />
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
        </Grid>
    )
}