import React from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Slider, Stack, Typography, Button } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function GroupCollapsedTitleWidth() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.groupCollapsedTitleWidth" });

    const { updateSetting, groupCollapsedTitleWidth } = userStore((state) => ({
        updateSetting: state.updateSetting,
        groupCollapsedTitleWidth: state.settings.groupCollapsedTitleWidth,
    }));

    const handleOnChange = async (e, v) => {
        await updateSetting("groupCollapsedTitleWidth", v);
    }

    const handleOnChangeCommitted = async (e, v) => {
        await sendRuntimeMessage("update_setting", { key: "groupCollapsedTitleWidth", value: v });
    }

    const handleReset = async () => {
        await updateSetting("groupCollapsedTitleWidth", 0);
        await sendRuntimeMessage("update_setting", { key: "groupCollapsedTitleWidth", value: 0 });
    }

    const valueLabelFormat = (value) => {
        return `${Math.round(value * 100)}%`;
    }

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
                    <Slider
                        size="small"
                        sx={{ width: "100%" }}
                        step={0.01}
                        marks
                        min={0.3}
                        max={0.8}
                        value={groupCollapsedTitleWidth}
                        color="error"
                        valueLabelDisplay="auto"
                        valueLabelFormat={valueLabelFormat}
                        onChange={handleOnChange}
                        onChangeCommitted={handleOnChangeCommitted}
                    />
                    <Button variant="outlined" color="error" onClick={handleReset}>
                        {t("reset")}
                    </Button>
                </Stack>
            </Grid>
        </Grid>
    );
}