import React, { useState, useEffect } from 'react';
import {
    Button,
    Checkbox,
    FormGroup,
    FormControlLabel,
    Typography,
    Paper,
    Box,
    Divider,
    Container,
    Grid,
    CircularProgress,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Alert,
} from '@mui/material';
import { toast } from 'react-toastify';
import { sendRuntimeMessage } from '../../../../core/browers/runtime';
import historyStore from '../../../../core/stores/historyStore';
import userStore, { userStoreCacheInstance } from '../../../../core/stores/userStore';
import { siteStore } from '../../../../core/stores/siteStore';
import snapshotStore from '../../../../core/stores/snapshotStore';
import { FileUpload } from '@mui/icons-material';
import { useTranslationContext } from "../../../Providers/TranslationProvider";


const ImportComponent = () => {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.importAndExport" });

    const { updateSetting } = userStore((state) => ({
        updateSetting: state.updateSetting,
    }));
    const { setSiteConfig } = siteStore((state) => ({
        setSiteConfig: state.setSiteConfig
    }));
    const { addHistory } = historyStore((state) => ({
        addHistory: state.addHistory
    }));
    const { addImportedSnapshot } = snapshotStore((state) => ({
        addImportedSnapshot: state.addImportedSnapshot
    }));

    const [selectedFile, setSelectedFile] = useState(null);
    const [fileData, setFileData] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [selectedOptions, setSelectedOptions] = useState({});
    const [open, setOpen] = useState(false);

    useEffect(() => {
        if (selectedFile) {
            const reader = new FileReader();
            reader.onload = (e) => {
                try {
                    const parsedData = JSON.parse(e.target.result);
                    setFileData(parsedData);
                    const options = {};
                    if (parsedData.preferences && parsedData.preferences.user_settings) options.preferences = true;
                    if (parsedData.quickAccess && parsedData.quickAccess.every(item => item.title && item.favIconUrl && item.url)) options.quickAccess = true;
                    if (parsedData.siteConfig && parsedData.siteConfig.every(item => item.hostname)) options.siteConfig = true;
                    if (parsedData.recentlyTabs && parsedData.recentlyTabs.every(item => item.title && item.url)) options.recentlyTabs = true;
                    if (parsedData.snapshots && parsedData.snapshots.every(item => item.id && item.createdAt && item.windowCount && item.tabCount && item.windows)) options.snapshots = true;
                    setSelectedOptions(options);

                    if (!Object.values(options).some(Boolean)) {
                        toast.error(t('fileError'));
                        return
                    }
                    setOpen(true);
                } catch (error) {
                    console.error('Error parsing JSON:', error);
                    toast.error(t('fileError'));
                }
            };
            reader.readAsText(selectedFile);
        }
    }, [selectedFile]);

    const handleFileChange = (event) => {
        setSelectedFile(event.target.files[0]);
        event.target.value = null;
    };

    const setData = async (data, selectedOptions) => {
        try {
            if (selectedOptions.preferences && data.preferences) {
                const userSettings = data.preferences.user_settings;
                for (const key in userSettings) {
                    await updateSetting(key, userSettings[key]);
                    await sendRuntimeMessage("update_setting", { key, value: userSettings[key] });
                }
                if (data.preferences.background_image_urls?.length > 1) {
                    const cacheInstance = await userStoreCacheInstance();
                    const storedImages = await cacheInstance.get('background_image_urls') || [];
                    const finalImages = Array.from(new Set([...data.preferences.background_image_urls, ...storedImages]));
                    await cacheInstance.set('background_image_urls', finalImages);
                }
            }
        } catch (error) {
            console.error('Error setting preferences:', error);
            throw error;
        }

        try {
            if (selectedOptions.quickAccess && data.quickAccess) {
                await sendRuntimeMessage("addQuickAccess", { items: data.quickAccess });
            }
        } catch (error) {
            console.error('Error setting quick access:', error);
            throw error;
        }

        try {
            if (selectedOptions.siteConfig && data.siteConfig) {
                for (const config of data.siteConfig) {
                    const { hostname } = config;
                    await setSiteConfig(hostname, config);
                    await sendRuntimeMessage('update_site_config', { hostname, config });
                }
            }
        } catch (error) {
            console.error('Error setting site config:', error);
            throw error;
        }

        try {
            if (selectedOptions.recentlyTabs && data.recentlyTabs) {
                for (const item of data.recentlyTabs) {
                    await addHistory(item);
                }
            }
        } catch (error) {
            console.error('Error setting recently tabs:', error);
            throw error;
        }

        if (selectedOptions.snapshots && data.snapshots) {
            for (const snapshot of data.snapshots) {
                try {
                    await addImportedSnapshot(snapshot);
                } catch (error) {
                    continue;

                }
            }
        }
    };

    const handleOptionChange = (event) => {
        setSelectedOptions({ ...selectedOptions, [event.target.name]: event.target.checked });
    };

    const handleImport = async () => {
        if (!fileData) {
            toast.error(t('fileError'));
            return;
        }

        setIsLoading(true);
        try {
            await setData(fileData, selectedOptions);
            toast.success(t('import.importSuccess'));
            setOpen(false);
            // Reset component state after successful import
            setSelectedFile(null);
            setFileData(null);
            setSelectedOptions({});
        } catch (error) {
            console.error('Error importing data:', error);
            toast.error(t('import.importFailed'));
        } finally {
            setIsLoading(false);
        }
    };

    const handleClose = () => {
        setOpen(false);
        setIsLoading(false);
    };

    return (
        <Container maxWidth="sm">
            <Box textAlign="center" mb={3}>
                <Button
                    component="label"
                    color='error'
                    variant="contained"
                    startIcon={<FileUpload />}
                    href="#file-upload"

                >
                    {t('import.label')}
                    <input
                        accept=".json"
                        id="import-file"
                        type="file"

                        onChange={handleFileChange}
                        style={{ display: 'none' }}
                    />
                </Button>
            </Box>
            <Dialog open={open} onClose={(event, reason) => { if (reason !== 'backdropClick') { handleClose() } }}>
                <DialogTitle variant='h3'>{t('import.label')}</DialogTitle>
                <DialogContent>
                    <Typography variant="h4" color="textSecondary" gutterBottom>
                        {t('import.formTips')}
                    </Typography>
                    <Divider sx={{ my: 1 }} />

                    <FormGroup>
                        <Grid container spacing={0}>
                            {fileData?.preferences && <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.preferences}
                                            onChange={handleOptionChange}
                                            name="preferences"
                                            color="error"
                                        />
                                    }
                                    label={`${t('options.preferences')} ( ${Object.keys(fileData.preferences.user_settings || {}).length} )`}
                                />
                                <Alert severity='info'>{t('options.preferencesInfo')}</Alert>
                            </Grid>
                            }
                            {
                                fileData?.quickAccess && <Grid item xs={12}>
                                    <FormControlLabel
                                        control={
                                            <Checkbox
                                                checked={selectedOptions.quickAccess}
                                                onChange={handleOptionChange}
                                                name="quickAccess"
                                                color="error"
                                            />
                                        }
                                        label={`${t('options.quickAccess')} (${Object.keys(fileData.quickAccess || {}).length})`}
                                    />
                                    <Alert severity='warning'>{t('options.quickAccessWarning')}</Alert>

                                </Grid>
                            }
                            {fileData?.siteConfig && <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.siteConfig}
                                            onChange={handleOptionChange}
                                            name="siteConfig"
                                            color="error"
                                        />
                                    }
                                    label={`${t('options.siteConfig')} (${Object.keys(fileData.siteConfig || {}).length})`}
                                />
                                <Alert severity='info'>{t('options.siteConfigInfo')}</Alert>
                            </Grid>
                            }
                            {fileData?.recentlyTabs && <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.recentlyTabs}
                                            onChange={handleOptionChange}
                                            name="recentlyTabs"
                                            color="error"
                                        />
                                    }
                                    label={`${t('options.recentlyTabs')} (${fileData.recentlyTabs ? fileData.recentlyTabs.length : 0})`}
                                />
                            </Grid>
                            }
                            {fileData?.snapshots && <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.snapshots}
                                            onChange={handleOptionChange}
                                            name="snapshots"
                                            color="error"
                                        />
                                    }
                                    label={`${t('options.snapshots')} (${fileData.snapshots ? fileData.snapshots.length : 0})`}
                                />
                            </Grid>
                            }
                        </Grid>
                    </FormGroup>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="error">
                        {t('cancelBtn')}
                    </Button>
                    <Button
                        variant="contained"
                        color="error"
                        onClick={handleImport}
                        startIcon={isLoading ? <CircularProgress size={24} color="inherit" /> : ""}
                        disabled={isLoading || !Object.values(selectedOptions).some(Boolean)}
                    >
                        {isLoading ? t('importing') : t('importBtn')}
                    </Button>
                </DialogActions>
            </Dialog>
        </Container>
    );
};
export default ImportComponent;
