import { Box, Grid, Stack, Typography } from "@mui/material";
import React, { useEffect } from "react";
import SideList from "./SideList";
import SnapshotDetails from "./SnapshotDetails";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import snapshotStore from "../../../../core/stores/snapshotStore";

export default function Snapshots() {

    const { t } = useTranslationContext('options', { keyPrefix: 'snapshots' });
    const summaries = snapshotStore(state => state.summaries);

    useEffect(() => {
        if (summaries.length > 0) {
            snapshotStore.setState({ selectdSnapshotId: summaries[0].id });
        }
    }, []);

    return (

        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("title")}</Typography></Grid>
                <Grid item xs={4}></Grid>

            </Grid>
            <Grid container>
                <Grid item xs={3}>
                    <SideList />
                </Grid>

                <Grid item xs={9}>
                    <SnapshotDetails />
                </Grid>
            </Grid>

        </Box>


    )
}