import React, { useContext } from "react";
import { Box, Divider, Alert, Grid, Stack, Typography, Button, Paper } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import SidebarEnabled from "./SidebarEnabled";
import SidebarPosition from "./SidebarPosition";
import ContentScriptWarning from "./ContentScriptWarning";


export default function TabSidebarOnPages() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.tabSidebarOnPages' });


    return (

        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("title")}</Typography></Grid>
                <Grid item xs={4}></Grid>


            </Grid>
            <Grid item xs={12} sx={{ mt: 2 }}>
                <Alert severity="info">{t('description')}</Alert>
            </Grid>
            <Divider sx={{ my: 2 }} />
            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                <SidebarEnabled />
                <SidebarPosition />
            </Stack>
            <Divider sx={{ my: 2, color: 'red' }}>{t('usageRestrictions')}</Divider>
            <Paper >
                <ContentScriptWarning />
            </Paper>
        </Box>

    )
}