import React from "react";
import { Box, CardMedia, Grid, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import EnableFloatBall from "./EnableFloatBall";


export default function FloatBall() {
    const { t } = useTranslationContext('options', { keyPrefix: 'settings' });
    return (

        <Box>
            <Typography variant="h1">{t('modules.floatBall')}</Typography>
            <Grid container alignItems="space-around" >
                <Grid item xs={4}>
                    <CardMedia component='img' sx={{ width: "100%", padding: "8px" }} image={chrome.runtime.getURL('/images/helps/floatball.jpg')} />
                </Grid>
                <Grid item xs={8} sx={{ padding: "8px" }}>
                    <EnableFloatBall />


                </Grid>
            </Grid>
        </Box>
    )
}