import { colord, extend } from 'colord'
import a11yPlugin from 'colord/plugins/a11y'
import cmykPlugin from 'colord/plugins/cmyk'
import harmoniesPlugin from 'colord/plugins/harmonies'
import hwbPlugin from 'colord/plugins/hwb'
import lchPlugin from 'colord/plugins/lch'
import namesPlugin from 'colord/plugins/names'

extend([a11yPlugin, cmykPlugin, harmoniesPlugin, hwbPlugin, lchPlugin, namesPlugin])

export * from 'colord'


export const getGradientColor = (color, deg = 125) => {
    const color2 = colord(color).desaturate(0.12).lighten(0.12);
    const color1 = color2.rotate(-57).saturate(0.16).lighten(0.03);
    const color3 = color2.rotate(64).saturate(0.39).darken(0.11);
    return `linear-gradient(${deg}deg, ${color1.toHex()} 0%, ${color2.toHex()} 50%, ${color3.toHex()} 100%)`;
}

export const getFontColor = (bgColor) => {
    if (bgColor === 'transparent') {
        return 'inherit';
    }
    return colord(bgColor).isLight() ? "#333" : "#fff";
};

export const getDominantColor = (image) => {
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    canvas.width = image.width;
    canvas.height = image.height;
    ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const data = imageData.data;
    const colorCount = {};
    let dominantColor = '';
    let maxCount = 0;

    for (let i = 0; i < data.length; i += 4) {
        const color = `${data[i]},${data[i + 1]},${data[i + 2]}`;
        colorCount[color] = (colorCount[color] || 0) + 1;
        if (colorCount[color] > maxCount) {
            maxCount = colorCount[color];
            dominantColor = color;
        }
    }

    return `rgb(${dominantColor})`;
};