import React from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Typography, ToggleButton, ToggleButtonGroup } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function ListBottomBorder() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.beautify.listBottomBorder" });

    const { updateSetting, listBottomBorder } = userStore((state) => ({
        updateSetting: state.updateSetting,
        listBottomBorder: state.settings.listBottomBorder
    }));

    const handleListBottomBorderChange = async (e, v) => {
        await updateSetting("listBottomBorder", v);
        await sendRuntimeMessage("update_setting", { key: "listBottomBorder", value: v });
    };

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={listBottomBorder} exclusive>
                    {['none', 'dashed', 'solid'].map((item, index) => (
                        <ToggleButton key={index} value={item} onClick={handleListBottomBorderChange}>
                            <Typography style={{ borderBottom: `1px ${item}` }} variant="body1">{t(item)}</Typography>
                        </ToggleButton>
                    ))}
                </ToggleButtonGroup>
            </Grid>
        </Grid>
    );
}