import React, { useContext } from "react";
import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { sendMessageToAllTabs } from "../../../../core/browers/tabs";



export default function SidebarEnabled() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabSidebarOnPages.sidebarEnabled" });
    const { updateSetting, sidebarEnabled } = userStore((state) => ({
        updateSetting: state.updateSetting,
        sidebarEnabled: state.settings.sidebarEnabled,

    }));


    const handleChange = async (event) => {
        await updateSetting('sidebarEnabled', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "sidebarEnabled", value: event.target.checked })

    }
    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={sidebarEnabled}
                    onChange={handleChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>
    )
}