import React, { useContext } from "react";
import { Box, Divider, Alert, Grid, Stack, Typography, Button } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import EnableHistory from "./EnableHistory";
import ShowHistoryTabs from "./ShowHistoryTabs";
import TabSearchShowHistory from "./TabSearchShowHistory";
import HistoryKeepDays from "./HistoryKeepDays";
import TabAliveSeconds from "./TabAliveSeconds";
import HistoryKeepCount from "./HistoryKeepCount";
import { resetHistorySettings } from "../../../../core/startup";
import HistoryDataSource from "./HistoryDataSource";
import HistorySearchLimitCount from "./HistorySearchLimitCount";



export default function History() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings' });
    const handleResetSettings = () => {
        resetHistorySettings();
    }

    return (

        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("modules.history")}</Typography></Grid>
                <Grid item xs={4}><Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings')}</Button></Grid>

            </Grid>
            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>

                <EnableHistory />
                <HistoryDataSource />
                <ShowHistoryTabs />
                <TabSearchShowHistory />
                <HistorySearchLimitCount />


                <HistoryKeepCount />

                <HistoryKeepDays />
                <TabAliveSeconds />

            </Stack>

        </Box>

    )
}