import React, { useContext } from "react";
import { Grid, ToggleButton, ToggleButtonGroup, Typography } from "@mui/material";

import { userStore } from "../../../../core/stores/userStore";
import { ViewList } from "@mui/icons-material";
import { ViewQuilt } from "@mui/icons-material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function View() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.listView" });


    const { updateSetting, view } = userStore((state) => ({
        updateSetting: state.updateSetting,
        view: state.settings.view

    }));



    const handleviewChange = async (e, v) => {
        if (v) {
            await updateSetting("view", v)
            await sendRuntimeMessage("update_setting", { key: "view", value: v })
        }
    }



    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={view} exclusive>
                    <ToggleButton value="list" onClick={handleviewChange}>
                        <ViewList />
                        <Typography variant="body1">{t("compact")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="visual" onClick={handleviewChange}>
                        <ViewQuilt />
                        <Typography variant="body1">{t("visual")}</Typography>

                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
        </Grid>

    )

}
