import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, RadioGroup, FormControl, FormLabel, Radio, Typography, Grid } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import userStore from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function QuickAccessGroupClickAction() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.quickAccess.quickAccessGroupClickAction' });

    const { updateSetting, pinToQuickAccess, quickAccessGroupClickAction } = userStore((state) => ({
        updateSetting: state.updateSetting,
        pinToQuickAccess: state.settings.pinToQuickAccess,
        quickAccessGroupClickAction: state.settings.quickAccessGroupClickAction,
    }));


    const handleChange = async (event) => {
        await updateSetting('quickAccessGroupClickAction', event.target.value)
        await sendRuntimeMessage("update_setting", { key: "quickAccessGroupClickAction", value: event.target.value })
    }
    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl component="fieldset" disabled={!pinToQuickAccess}>
                    <FormLabel component="legend">{t('label')}</FormLabel>
                    <RadioGroup
                        name="quickAccessGroupClickAction"
                        value={quickAccessGroupClickAction}
                        onChange={handleChange}
                    >
                        <FormControlLabel value="openAllUrls" control={<Radio />} label={t('openAllUrls')} />
                        <FormControlLabel value="expandIcons" control={<Radio />} label={t('expandIcons')} />
                        <FormControlLabel value="expandList" control={<Radio />} label={t('expandList')} />
                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>

    )
}