import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function TabHistoryShowUrl() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.tabHistoryShowUrl" });

    const { updateSetting, tabHistoryEnable, tabHistoryShowUrl } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabHistoryEnable: state.settings.tabHistoryEnable,
        tabHistoryShowUrl: state.settings.tabHistoryShowUrl
    }));



    const handleChange = async (event) => {
        await updateSetting('tabHistoryShowUrl', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "tabHistoryShowUrl", value: event.target.checked })
    }


    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" disabled={!tabHistoryEnable} checked={tabHistoryShowUrl}
                    onChange={handleChange} />}
                    labelPlacement="start"

                />
            </Grid>
        </Grid>

    )

}
