import React, { useContext } from "react";
import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function TabSearchShowHistory() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.showInTabSearchResults" });
    const { updateSetting, enableHistory, tabSearchShowHistory } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,
        tabSearchShowHistory: state.settings.tabSearchShowHistory,

    }));


    const handleTabSearchShowHistoryChange = async (event) => {
        await updateSetting('tabSearchShowHistory', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "tabSearchShowHistory", value: event.target.checked })
    }
    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={enableHistory && tabSearchShowHistory}
                    onChange={handleTabSearchShowHistoryChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>
    )
}