import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function TabPreviewEnable() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.hoverPreview" });

    const { updateSetting, tabPreviewEnable } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabPreviewEnable: state.settings.tabPreviewEnable

    }));


    const handleTabPreviewEnableChange = async (event) => {
        await updateSetting('tabPreviewEnable', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "tabPreviewEnable", value: event.target.checked })
    }



    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={tabPreviewEnable}
                    onChange={handleTabPreviewEnableChange} />}
                    labelPlacement="start"

                />
            </Grid>
        </Grid>

    )

}
