import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { Grid, Typography, ToggleButton, ToggleButtonGroup, Alert } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function ExtensionIconAction() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.extAction" });


    const { updateSetting, clickExtensionIconAction } = userStore((state) => ({
        updateSetting: state.updateSetting,
        clickExtensionIconAction: state.settings.clickExtensionIconAction

    }));

    const handleClickExtensionIconActionChange = async (e, v) => {
        await updateSetting("clickExtensionIconAction", v)
        if (v === "openSidePanel") {
            if ('sidePanel' in chrome) {
                chrome.sidePanel.setPanelBehavior({ openPanelOnActionClick: true }).catch((error) => console.error(error));
            }
            await sendRuntimeMessage('open_sidepanel');
        } else {
            if ("sidePanel" in chrome) {
                chrome.sidePanel?.setPanelBehavior({ openPanelOnActionClick: false }).catch((error) => console.error(error));
            }
            if (v === 'openPopup') {
                chrome.action.setPopup({ popup: "popup.html" })
            }
            await sendRuntimeMessage('open_popup');
        }
    }


    if (!('sidePanel' in chrome)) {
        return null;
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={clickExtensionIconAction} exclusive>
                    <ToggleButton value="openSidePanel" onClick={handleClickExtensionIconActionChange}>
                        <Typography variant="body1">{t("openSidePanel")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="openPopup" onClick={handleClickExtensionIconActionChange}>
                        <Typography variant="body1">{t("openPopup")}</Typography>
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
            <Grid item xs={12} mt={1}>
                <Alert severity="info">{t('SmallWindowAlert')}</Alert>
            </Grid>

        </Grid>
    )
}