import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { Grid, Typography, ToggleButton, ToggleButtonGroup } from "@mui/material";
import { KeyboardArrowRight } from "@mui/icons-material";
import { KeyboardArrowLeft } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
export default function ButtonPosition() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.buttonPosition" });

    const { updateSetting, buttonPosition } = userStore((state) => ({
        updateSetting: state.updateSetting,
        buttonPosition: state.settings.buttonPosition

    }));


    const handlebuttonPositionChange = async (e, v) => {
        await updateSetting("buttonPosition", v)
        await sendRuntimeMessage("update_setting", { key: "buttonPosition", value: v })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <ToggleButtonGroup size="small" value={buttonPosition} exclusive>
                    <ToggleButton value="row-reverse" onClick={handlebuttonPositionChange}>
                        <KeyboardArrowLeft /> <Typography variant="body1">{t("left")}</Typography>
                    </ToggleButton>
                    <ToggleButton value="row" onClick={handlebuttonPositionChange}>
                        <Typography variant="body1">{t("right")}</Typography> <KeyboardArrowRight />
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
        </Grid>

    )
}