import React, { useState, useEffect } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Input, Stack, Typography, Box, Popover, IconButton } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { RgbaStringColorPicker } from "react-colorful";
import ClearIcon from '@mui/icons-material/Clear';
import { siteStore } from "../../../../core/stores/siteStore";

export default function FontColor({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.fontColor" });

    const { fontColor, setSiteConfig } = siteStore((state) => ({
        fontColor: state.sites.find(site => site.hostname === hostname)?.fontColor,
        setSiteConfig: state.setSiteConfig
    }));


    const [anchorEl, setAnchorEl] = useState(null);
    const [localFontColor, setLocalFontColor] = useState(fontColor);

    useEffect(() => {
        setLocalFontColor(fontColor);
    }, [fontColor]);

    const handleInputChange = async (color) => {
        setLocalFontColor(color);
        await setSiteConfig(hostname, { fontColor: color });
        await sendRuntimeMessage('update_site_config', { hostname, config: { fontColor: color } });

    };

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSetBlack = async () => {
        const color = "#000000";
        setLocalFontColor(color);
        await setSiteConfig(hostname, { fontColor: color });
        await sendRuntimeMessage('update_site_config', { hostname, config: { fontColor: color } });
    };

    const handleSetWhite = async () => {
        const color = "#FFFFFF";
        setLocalFontColor(color);
        await setSiteConfig(hostname, { fontColor: color });
        await sendRuntimeMessage('update_site_config', { hostname, config: { fontColor: color } });
    };

    const handleClearColor = async () => {
        const color = "inherit";
        setLocalFontColor(color);
        await setSiteConfig(hostname, { fontColor: null });
        await sendRuntimeMessage('update_site_config', { hostname, config: { fontColor: null } });
    };

    const open = Boolean(anchorEl);
    const id = open ? 'color-picker-popover' : undefined;

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={4} sx={{ cursor: 'pointer' }}>
                <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
                    <Box
                        aria-describedby={id}
                        sx={{
                            width: 24,
                            height: 24,
                            backgroundColor: fontColor,
                            border: '1px solid #000',
                            cursor: 'pointer'
                        }}
                        onClick={handleClick}
                    />
                    <Popover
                        id={id}
                        open={open}
                        anchorEl={anchorEl}
                        onClose={handleClose}
                        anchorOrigin={{
                            vertical: 'bottom',
                            horizontal: 'left',
                        }}
                        slotProps={{
                            paper: {
                                sx: {
                                    padding: 2,
                                    overflow: 'hidden'
                                }
                            }
                        }}
                    >
                        <RgbaStringColorPicker color={localFontColor} onChange={handleInputChange} />
                    </Popover>
                    <Input
                        type="text"
                        value={localFontColor}
                        onChange={(e) => handleInputChange(e.target.value)}
                        autoFocus
                        endAdornment={
                            <>
                                <IconButton onClick={handleSetBlack}>
                                    <Box sx={{ width: 24, height: 24, backgroundColor: "#000000", border: '1px solid #fff' }} />
                                </IconButton>

                                <IconButton onClick={handleSetWhite}>
                                    <Box sx={{ width: 24, height: 24, backgroundColor: "#FFFFFF", border: '1px solid #000' }} />
                                </IconButton>

                                <IconButton onClick={handleClearColor}>
                                    <ClearIcon />
                                </IconButton>
                            </>
                        }
                    />
                </Stack>
            </Grid>
        </Grid>
    );
}