import React from "react";
import { AppBar, Box, Button, Container, Grid, Toolbar, Typography, Alert, List, Divider, ListItemButton, ListItem, Stack } from "@mui/material";

import Sidebar from "./Sidebar";
import PreviewTips from "./alerts/PreviewTips";
import { useEffect } from "react";
import userStore from "../../core/stores/userStore";
import { siteStore } from "../../core/stores/siteStore";
import { NavLink } from "react-router-dom";
import { useTranslationContext } from "../Providers/TranslationProvider";
import ScreenSizeTips from "./alerts/ScreenSizeTips";

export default function Options({ children }) {
    const { t } = useTranslationContext();



    const { updateSetting, } = userStore(state => ({
        updateSetting: state.updateSetting,

    }));

    const { loadSiteConfigs } = siteStore((state) => ({
        loadSiteConfigs: state.loadSiteConfigs
    }));

    useEffect(() => {
        loadSiteConfigs();
        chrome.runtime.onMessage.addListener(handleRuntimeOnMessage);
        return () => {
            chrome.runtime.onMessage.removeListener(handleRuntimeOnMessage);
        }
    }, [])

    const handleRuntimeOnMessage = (message, sender, sendResponse) => {
        const { data } = message;
        switch (message.action) {
            case 'update_setting':
                updateSetting(data.key, data.value);
                sendResponse({ success: true });
                break;
        }
    }



    const rateUrl = navigator.userAgent.includes("Edg") ? `https://microsoftedge.microsoft.com/addons/detail/${chrome.runtime.id}` : `https://chromewebstore.google.com/detail/${chrome.runtime.id}`;

    const extenLinks = [
        { name: "releases", label: t("Changelog", { ns: "translation" }), url: "https://github.com/RabbitPair/colorful_sidepanel_tabs_extension/releases" },
        { name: "feedback", label: t("SubmitSuggestionsOrIssues", { ns: "translation" }), url: "https://github.com/RabbitPair/colorful_sidepanel_tabs_extension/issues/new/choose" },
        { name: "donation", label: t("Donation", { ns: "translation" }), url: "https://ko-fi.com/rabbitpair" },
        { name: "rateExtension", label: t("RateExtension", { ns: "translation" }), url: rateUrl }
    ];

    return (

        <Box sx={{ flexGrow: 1, backgroundImage: "none", minHeight: "100vh", display: "flex", flexDirection: "column" }}>
            <Container maxWidth="lg" sx={{ mt: 4, mb: 4, flex: 1 }}>
                <Grid container spacing={2}>
                    {((parseInt(/Chrome\/(\d+)/.exec(navigator.userAgent)[1], 10) >= 127) || ('sidePanel' in chrome)) && <PreviewTips />}
                    {('sidePanel' in chrome) && <ScreenSizeTips />}
                </Grid>

                <Grid container spacing={2}>
                    <Grid item xs={2}>
                        <Sidebar />
                    </Grid>
                    <Grid item xs={10}>{children}</Grid>
                </Grid>
            </Container>
            <Box component="footer" sx={{ py: 3, px: 2, mt: 'auto' }}>
                <Container maxWidth="lg">
                    <Stack direction="row" spacing={4} justifyContent="center" alignItems="center">
                        {extenLinks.map((link) => (
                            <Button key={link.name} href={link.url} target="_blank" sx={{ textTransform: 'none' }}>
                                <Typography variant="body2" sx={{ fontWeight: 600 }}>{link.label}</Typography>
                            </Button>
                        ))}
                    </Stack>
                </Container>
            </Box>
        </Box>

    );
}