import React, { useState, useMemo } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Typography, Dialog, Box, FormControl, Stack, IconButton, Button, Autocomplete, TextField } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import userStore from "../../../../core/stores/userStore";
import { chromeGroupAvailableColors } from "../../../../utils/colors";
import { RadioButtonChecked, Circle, ArrowRight } from "@mui/icons-material";
import { tab } from "../../../Content/runtime";

export default function AutoJoinGroup({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.autoJoinGroup" });

    const { customGroupNames } = userStore(state => ({
        customGroupNames: state.settings.customGroupNames
    }));

    const { autoJoinGroup, groupName, groupColor, setSiteConfig } = siteStore(state => ({
        autoJoinGroup: state.sites.find(site => site.hostname === hostname)?.autoJoinGroup,
        groupName: state.sites.find(site => site.hostname === hostname)?.groupName,
        groupColor: state.sites.find(site => site.hostname === hostname)?.groupColor,
        setSiteConfig: state.setSiteConfig
    }));

    const [openDialog, setOpenDialog] = useState(false);
    const [newGroupName, setNewGroupName] = useState(groupName);
    const [newGroupColor, setNewGroupColor] = useState(groupColor);

    const handleDialogOpen = () => setOpenDialog(true);
    const handleDialogClose = () => {
        setOpenDialog(false);
        setNewGroupName(groupName);
        setNewGroupColor(groupColor);
    };

    const handleChange = async () => {
        const config = {
            autoJoinGroup: !autoJoinGroup,
            groupName: newGroupName || groupName,
            groupColor: newGroupColor || groupColor
        };
        await setSiteConfig(hostname, config);
        await sendRuntimeMessage('update_site_config', { hostname, config });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        sendRuntimeMessage('add_tabs_to_group', { tabIds: openTabs.map(tab => tab.id), title: newGroupName, color: newGroupColor });
    };

    const handleCreateNewGroup = async () => {
        await handleChange();
        handleDialogClose();
    };

    const groupColorHex = useMemo(() =>
        chromeGroupAvailableColors.find(c => c.color === groupColor)?.hex || 'grey',
        [groupColor]);

    return (
        <Grid container alignItems="center">
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <Button size="small" endIcon={<ArrowRight />} variant="text" color="primary" onClick={handleDialogOpen}>
                    <Typography sx={{ color: groupColorHex }}>{groupName || t('configure')}</Typography>
                </Button>

                <Dialog open={openDialog} onClose={handleDialogClose}>
                    <Box sx={{ p: 2 }}>
                        <FormControl fullWidth variant="outlined">
                            <Autocomplete
                                size="small"
                                id="rename-group-name-input"
                                options={customGroupNames}
                                getOptionLabel={(option) => option.name}
                                value={customGroupNames.find(option => option.name === newGroupName) || { name: newGroupName || "" }}
                                onChange={(_, newValue) => setNewGroupName(newValue?.name || "")}
                                onInputChange={(_, newInputValue) => setNewGroupName(newInputValue)}
                                freeSolo
                                renderInput={(params) => <TextField {...params} placeholder={t("groupName")} />}
                            />
                        </FormControl>
                        <Stack direction="row" sx={{ mt: 2 }}>
                            {chromeGroupAvailableColors.map(colorInfo => (
                                <IconButton
                                    key={colorInfo.color}
                                    onClick={() => setNewGroupColor(colorInfo.color)}
                                >
                                    {newGroupColor === colorInfo.color ? (
                                        <RadioButtonChecked sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                                    ) : (
                                        <Circle sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                                    )}
                                </IconButton>
                            ))}
                        </Stack>
                        <FormControl fullWidth variant="outlined" sx={{ pt: 2 }}>
                            <Button
                                variant="contained"
                                sx={{ backgroundColor: chromeGroupAvailableColors.find(c => c.color === newGroupColor)?.hex || '#eb0022' }}
                                onClick={handleCreateNewGroup}
                                disabled={!newGroupColor || !newGroupName}
                            >
                                {t("save")}
                            </Button>
                        </FormControl>
                    </Box>
                </Dialog>
            </Grid>
        </Grid>
    );
}
