import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, RadioGroup, FormControl, FormLabel, Radio, Typography, Grid } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function GroupSelectBookmarkFolderLevel() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.chromeTabGroupAdvanced.groupSelectBookmarkFolderLevel' });

    const { updateSetting, useBookmarkFolderNameAsGroupName, groupSelectBookmarkFolderLevel } = userStore((state) => ({
        updateSetting: state.updateSetting,
        useBookmarkFolderNameAsGroupName: state.settings.useBookmarkFolderNameAsGroupName,
        groupSelectBookmarkFolderLevel: state.settings.groupSelectBookmarkFolderLevel,
    }));


    const handleChange = async (event) => {
        await updateSetting('groupSelectBookmarkFolderLevel', event.target.value)
        await sendRuntimeMessage("update_setting", { key: "groupSelectBookmarkFolderLevel", value: event.target.value })
    }
    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl component="fieldset" disabled={!useBookmarkFolderNameAsGroupName}>
                    <FormLabel component="legend">{t('label')}</FormLabel>
                    <RadioGroup
                        aria-label="groupSelectBookmarkFolderLevel"
                        name="groupSelectBookmarkFolderLevel"
                        value={groupSelectBookmarkFolderLevel}
                        onChange={handleChange}
                    >
                        <FormControlLabel value="top" control={<Radio />} label={t('top')} />
                        <FormControlLabel value="parent" control={<Radio />} label={t('parent')} />
                        <FormControlLabel value="full" control={<Radio />} label={t('full')} />
                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>

    )
}