import React, { useContext } from "react";
import { Grid, Typography, FormControlLabel, Switch } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function FullScreenHideSidePanel() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.fullScreenHideSidePanel" });


    const { updateSetting, fullScreenHideSidePanel } = userStore((state) => ({
        updateSetting: state.updateSetting,
        fullScreenHideSidePanel: state.settings.fullScreenHideSidePanel

    }));

    const handleFullScreenHideSidePanelChange = async (event) => {
        await updateSetting('fullScreenHideSidePanel', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "fullScreenHideSidePanel", value: event.target.checked })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={fullScreenHideSidePanel}
                    onChange={handleFullScreenHideSidePanelChange} />}
                    labelPlacement="start"

                />
            </Grid>
        </Grid>

    )
}