import React from "react";
import ExportComponent from "./Export";
import ImportComponent from "./Import";
import { Box, Divider, Grid, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


const ImportExport = () => {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.importAndExport" });


    return (
        <Box>
            <Grid container alignItems="center" sx={{ marginBottom: "30px" }}>
                <Grid item xs={8}><Typography variant="h1">{t("title")}</Typography></Grid>
                <Grid item xs={4}></Grid>
            </Grid>

            <Stack direction={"row"} justifyContent="flex-start" alignItems="flex-start" spacing={2} divider={<Divider orientation="vertical" flexItem />}>
                <ExportComponent />
                <ImportComponent />
            </Stack>

        </Box>

    );
};

export default ImportExport;