import React, { useContext, useState } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, TextField, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function TabTitleMulitLines() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.tabTitleMulitLines" });

    const { updateSetting, tabTitleMulitLines } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabTitleMulitLines: state.settings.tabTitleMulitLines
    }));

    const [value, setValue] = useState(tabTitleMulitLines);

    const handleChange = async (event) => {
        const intValue = parseInt(event.target.value, 10);
        setValue(event.target.value);
        await updateSetting('tabTitleMulitLines', isNaN(intValue) ? 1 : intValue);
        await sendRuntimeMessage("update_setting", { key: "tabTitleMulitLines", value: isNaN(intValue) ? 1 : intValue });
    }


    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <TextField
                    value={value}
                    onChange={handleChange}
                    type="text"
                    variant="outlined"
                    fullWidth
                />
            </Grid>
        </Grid>

    )

}
