import React, { useContext } from "react";

import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Typography, Alert } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import userStore from "../../../../core/stores/userStore";
import { Link } from "react-router-dom";



export default function TabSwitchPip({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.tabSwitchPip" });


    const { globalTabSwitchPip } = userStore((state) => ({
        globalTabSwitchPip: state.settings.tabSwitchPip
    }))

    const { tabSwitchPip, setSiteConfig } = siteStore((state) => ({
        tabSwitchPip: state.sites.find(site => site.hostname === hostname)?.tabSwitchPip,
        setSiteConfig: state.setSiteConfig
    }));

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { tabSwitchPip: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { tabSwitchPip: event.target.checked } });
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <FormControlLabel control={<Switch disabled={!globalTabSwitchPip} color="error" checked={globalTabSwitchPip && tabSwitchPip !== false}
                    onChange={handleChange} />}
                />
            </Grid>
            {!globalTabSwitchPip && <Link target="_blank" to={`/pip`}><Alert severity="warning">{t('globaldisabledTips')}</Alert></Link>}
        </Grid>

    )

}
