import React from 'react';
import { useTranslation } from 'react-i18next';
import { Container, Typography, Box, Paper, Card, CardContent, Grid, Avatar, Chip, SvgIcon, Stack, Button } from '@mui/material';
import { LocalActivity } from '@mui/icons-material';
import { Event } from '@mui/icons-material';
import { LocationOn } from '@mui/icons-material';
import PremiumIcon from '../../component/PremiumIcon';
import { History } from '@mui/icons-material';
import { CloudSync } from '@mui/icons-material';
import { PictureInPicture } from '@mui/icons-material';
import { Webhook } from '@mui/icons-material';
import { Widgets } from '@mui/icons-material';
import { AutoAwesome } from '@mui/icons-material';
import { MoreHoriz } from '@mui/icons-material';
import userStore from '../../../core/stores/userStore';

const PurchaseInstructions = () => {
    const { t } = useTranslation();

    const { selectedLanguage } = userStore((state) => ({
        selectedLanguage: state.settings.selectedLanguage,

    }));
    const features = [
        { icon: <SvgIcon><svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M600-120v-120H440v-400h-80v120H80v-320h280v120h240v-120h280v320H600v-120h-80v320h80v-120h280v320H600ZM160-760v160-160Zm520 400v160-160Zm0-400v160-160Zm0 160h120v-160H680v160Zm0 400h120v-160H680v160ZM160-600h120v-160H160v160Z" /></svg></SvgIcon>, label: t('treeTabs') },
        { icon: <History />, label: t('tabHistory') },
        { icon: <CloudSync />, label: t('cloudSync') },
        { icon: <PictureInPicture />, label: t('advancedPictureInPicture') },
        { icon: <Webhook />, label: t('APIAccess') },
        { icon: <Widgets />, label: t('customizeMenu') },
        { icon: <AutoAwesome />, label: t('Automation') },
        { icon: <MoreHoriz />, label: t('More') },
    ];
    const paymentLink = {
        "zh_CN": "https://book.stripe.com/bIY29N1u3cvH0co7su",
        "zh_TW": "https://book.stripe.com/bIY29N1u3cvH0co7su",
        "ja": "https://book.stripe.com/8wM29N5Kjanz2kw6or",
        "en": "https://book.stripe.com/28oeWz4GfdzL9MY145"
    }
    const price = {
        "zh_CN": "¥69.00",
        "zh_TW": "¥69.00",
        ja: "JP¥1,399",
        en: "US$9.99"
    }

    const handlePreOrderClick = () => {

        chrome.tabs.create({ url: paymentLink[selectedLanguage] || paymentLink['en'], active: true });
    };

    return (
        <Container maxWidth="md">
            <Paper elevation={1} >
                <Box sx={{ p: 2 }}>
                    <Box sx={{ mb: 4 }}>
                        <Typography variant="h2" sx={{ py: 2 }}>
                            {t('premiumAccessFeatures')}
                        </Typography>
                        <Typography variant="h3" color="text.secondary">
                            {t('premiumFeaturesPreOrderDescription')}
                        </Typography>
                    </Box>
                    <Box my={4}>
                        <Typography variant="h4" sx={{ py: 2 }}>
                            {t('upcomingFeatures')}
                        </Typography>
                        <Grid container spacing={2}>
                            {features.map((feature, index) => (
                                <Grid item xs={12} sm={6} md={3} key={index}>
                                    <Card>
                                        <CardContent>
                                            <Box display="flex" alignItems="center">
                                                <Avatar sx={{ bgcolor: 'error.main', mr: 2 }}>
                                                    {feature.icon}
                                                </Avatar>
                                                <Typography variant="body1">
                                                    {feature.label}
                                                </Typography>
                                            </Box>
                                        </CardContent>
                                    </Card>
                                </Grid>
                            ))}
                        </Grid>
                    </Box>

                    <Box sx={{ m: 5, maxWidth: 400, margin: 'auto' }}>
                        <Card elevation={3} sx={{ borderRadius: 2, overflow: 'visible', background: 'linear-gradient(135deg, #fff 0%, #eee 100%)' }}>
                            <CardContent>
                                <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
                                    <Stack direction="row" alignItems="center" spacing={1}>
                                        <PremiumIcon />
                                        <Typography variant="h5" component="div" fontWeight="bold" ml={1}>
                                            {t('earlyBirdTicket')}
                                        </Typography>
                                        <Typography sx={{ fontStyle: 'italic', color: "warning.main" }}>
                                            {price[selectedLanguage] || price['en']}
                                        </Typography>
                                    </Stack>
                                    <Chip
                                        label={t('limited')}
                                        color="warning"
                                    />
                                </Box>

                                <Typography variant="h4" paragraph sx={{ fontWeight: 400, color: 'primary.main' }}>
                                    {t('earlyBirdTicketDesc')}
                                </Typography>
                                <Typography variant="body2" paragraph sx={{ color: 'error.main' }} >
                                    {t('afterPurchase')}
                                </Typography>
                                <Box display="flex" justifyContent="center" mt={2}>
                                    <Button
                                        variant="contained"
                                        color="error"
                                        sx={{ padding: '10px 20px', fontWeight: 'bold' }}
                                        onClick={handlePreOrderClick}
                                    >
                                        {t('PreOrderPremiumNow')}
                                    </Button>
                                </Box>

                            </CardContent>
                        </Card>
                    </Box>
                </Box>
            </Paper>
        </Container >
    );
};

export default PurchaseInstructions;
