import React, { useState, useEffect } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, Grid, Switch, TextField, Typography, Paper, FormControlLabel, Box } from "@mui/material";
import { sendRuntimeMessage, sendTabMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import { useParams } from "react-router-dom";
import { ArrowRight } from "@mui/icons-material";
import Markdown from 'react-markdown'
import remarkGfm from 'remark-gfm'

export default function CaptionSetting({ hostname }) {

    const params = useParams();
    hostname = hostname || params.hostname
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.captionSetting" });
    const { captionSetting, captionSelector, captionParentSelector, setSiteConfig } = siteStore((state) => ({
        captionSetting: state.sites.find(site => site.hostname === hostname)?.captionSetting,
        captionSelector: state.sites.find(site => site.hostname === hostname)?.captionSelector || "",
        captionParentSelector: state.sites.find(site => site.hostname === hostname)?.captionParentSelector || "",
        setSiteConfig: state.setSiteConfig
    }));

    const [open, setOpen] = useState(false);
    const [localCaptionSelector, setLocalCaptionSelector] = useState(captionSelector);
    const [localCaptionParentSelector, setLocalCaptionParentSelector] = useState(captionParentSelector);

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { captionSetting: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { captionSetting: event.target.checked } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            sendTabMessage(tab.id, 'update_site_config', { key: 'captionSetting', value: event.target.checked });
        }
    }

    const handleConfigureOpen = () => {
        setOpen(true);
    };

    const handleConfigureClose = () => {
        setOpen(false);
    };

    const handleSave = async () => {
        await setSiteConfig(hostname, { captionSelector: localCaptionSelector, captionParentSelector: localCaptionParentSelector });
        await sendRuntimeMessage('update_site_config', { hostname, config: { captionSelector: localCaptionSelector, captionParentSelector: localCaptionParentSelector } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            sendTabMessage(tab.id, 'update_site_config', { key: 'captionSelector', value: localCaptionSelector });
            sendTabMessage(tab.id, 'update_site_config', { key: 'captionParentSelector', value: localCaptionParentSelector });
        }
        setOpen(false);
    };

    return (
        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <FormControlLabel control={<Switch color="error" checked={captionSetting === true}
                    onChange={handleChange} />}
                />
                {captionSetting && (
                    <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={handleConfigureOpen}>
                        {t('configure')}
                    </Button>
                )}
                <Dialog
                    open={open}
                    onClose={(event, reason) => { if (reason !== 'backdropClick') { handleConfigureClose } }}
                    maxWidth="md"
                    fullWidth
                >
                    <DialogTitle variant="h3">{t("configure")}</DialogTitle>
                    <DialogContent>
                        <Box sx={{ padding: '10px' }}>
                            <Typography variant="h4" gutterBottom>  <Markdown remarkPlugins={[remarkGfm]}>{t("description")}</Markdown ></Typography>
                            <Typography variant="h4" gutterBottom>  <Markdown remarkPlugins={[remarkGfm]}>{t("selectorDesc")}</Markdown ></Typography>
                        </Box>
                        <Paper variant="outlined" style={{ padding: '16px', marginTop: '16px' }}>
                            <Typography variant="h4" gutterBottom>{t("captionSelector")}</Typography>

                            <FormControl fullWidth margin="normal">
                                <TextField
                                    value={localCaptionSelector}
                                    autoComplete="off"
                                    onChange={(e) => setLocalCaptionSelector(e.target.value)}
                                    variant="standard"
                                />
                            </FormControl>
                            <Typography variant="h4" gutterBottom>{t("captionParentSelectorDesc")}</Typography>
                            <FormControl fullWidth margin="normal">
                                <TextField
                                    value={localCaptionParentSelector}
                                    autoComplete="off"
                                    onChange={(e) => setLocalCaptionParentSelector(e.target.value)}
                                    variant="standard"
                                />
                            </FormControl>
                            <Button onClick={handleSave} color="error" variant="contained" style={{ marginTop: '16px' }}>
                                {t("save", { keyPrefix: "settings.site.linkSettings" })}
                            </Button>
                        </Paper>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleConfigureClose} color="error">
                            {t("close", { keyPrefix: "settings.site.linkSettings" })}
                        </Button>
                    </DialogActions>
                </Dialog>
            </Grid>
        </Grid>
    )
}