import React, { useContext } from "react";
import { Box, Divider, Alert, Grid, Stack, Typography, Button } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import Command from "./Command";
import { useRef } from "react";
import { useEffect } from "react";
import { ArrowForward } from "@mui/icons-material";



export default function Shortcuts() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings" });

    const [commands, setCommands] = React.useState([]);
    useEffect(() => {
        chrome.commands.getAll((items) => {
            // console.log("commands", items)
            setCommands(items)
        });
    }, [])

    const handleClick = (event, val) => {
        chrome.tabs.create({ url: "chrome://extensions/shortcuts", active: true });
    }
    return (

        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("modules.shortcuts")}</Typography></Grid>
                <Grid item xs={4}></Grid>

            </Grid>

            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                <Alert severity="error">
                    <Typography variant="body1">{t('shortcuts.alert')}  </Typography>
                    <Button color="error" onClick={handleClick}><ArrowForward color="error" /> {t('shortcuts.goToBrowserSettings')} </Button>
                </Alert>
                {commands.map((command, index) => (
                    <Command key={index} command={command} />
                ))}

            </Stack>

        </Box >

    )
}