import React, { useEffect, useState } from 'react';
import snapshotStore from '../../../../core/stores/snapshotStore';
import { Container, Button, Stack, Checkbox, FormControlLabel } from '@mui/material';
import WindowItem from './WindowItem';
import { formatDateYmdHis } from '../../../../utils/helpers';
import { useTranslationContext } from "../../../Providers/TranslationProvider";
const SnapshotDetails = () => {
    const { t } = useTranslationContext('options', { keyPrefix: 'snapshots' });

    const { selectedTabs, selectedTabsCount, selectdSnapshotId } = snapshotStore((state) => ({
        selectedTabs: state.selectedTabs,
        selectedTabsCount: Object.values(state.selectedTabs).flat().length,
        selectdSnapshotId: state.selectdSnapshotId
    }));

    const [snapshotDetails, setSnapshotDetails] = useState(null);
    const [restoreWindowSize, setRestoreWindowSize] = useState(true);

    useEffect(() => {
        const fetchDetails = async () => {
            if (selectdSnapshotId) {
                const details = await snapshotStore.getState().fetchSnapshot(selectdSnapshotId);
                setSnapshotDetails(details);
            }
        };

        fetchDetails();
    }, [selectdSnapshotId]);

    const handleOpenAllWindows = () => {
        if (snapshotDetails) {
            snapshotDetails.windows.forEach(window => {
                const selectdWindowTabs = selectedTabs[window.id] || [];
                const tabsToOpen = selectdWindowTabs.length > 0 ? selectdWindowTabs.map(tabId => window.tabs.find(tab => tab.id === tabId)) : window.tabs;

                // Open the first tab immediately
                const firstTab = tabsToOpen.shift();
                const createWindowOptions = {
                    url: firstTab.url,
                    ...(restoreWindowSize && { left: window.left, top: window.top, width: window.width, height: window.height })
                };

                chrome.windows.create(createWindowOptions, (newWindow) => {
                    const groupTabs = {};

                    // Open the remaining tabs with a delay to avoid performance issues
                    tabsToOpen.forEach((tab, index) => {
                        setTimeout(() => {
                            chrome.tabs.create({ windowId: newWindow.id, url: tab.url, active: false }, (createdTab) => {
                                if (tab.groupId > 0) {
                                    if (!groupTabs[tab.groupId]) {
                                        groupTabs[tab.groupId] = [];
                                    }
                                    groupTabs[tab.groupId].push(createdTab.id);
                                }
                            });
                        }, index * 250); // Adjust the delay as needed
                    });

                    // Set groups after all tabs are created, only if there are no selected tabs
                    if (selectdWindowTabs.length === 0) {
                        setTimeout(() => {
                            Object.keys(groupTabs).forEach(groupId => {
                                if (groupId && window.tabGroups) {
                                    const group = window.tabGroups.find(group => group.id === parseInt(groupId));
                                    if (group) {
                                        chrome.tabs.group({ tabIds: groupTabs[groupId], createProperties: { windowId: newWindow.id } }, (newGroupId) => {
                                            chrome.tabGroups?.update(newGroupId, { color: group.color, title: group.title });
                                        });
                                    }
                                }
                            });
                        }, tabsToOpen.length * 250 + 500); // Adjust the delay as needed
                    }
                });
            });
        }
    };

    const handleOpenSelectedInCurrentWindow = () => {
        if (snapshotDetails) {
            snapshotDetails.windows.forEach(window => {
                const selectdWindowTabs = selectedTabs[window.id] || [];
                if (selectdWindowTabs.length > 0) {
                    const tabsToOpen = selectdWindowTabs.map(tabId => window.tabs.find(tab => tab.id === tabId));
                    tabsToOpen.forEach((tab, index) => {
                        setTimeout(() => {
                            chrome.tabs.create({ url: tab.url, active: false }, (createdTab) => {

                            });
                        }, index * 250); // Adjust the delay as needed to avoid performance issues
                    });
                }
            });
        }
    };

    const handleExportSnapshot = () => {
        if (snapshotDetails) {
            const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(snapshotDetails));
            const downloadAnchorNode = document.createElement('a');
            const formattedDate = formatDateYmdHis(snapshotDetails.createdAt);
            downloadAnchorNode.setAttribute("href", dataStr);
            downloadAnchorNode.setAttribute("download", `snapshot_${formattedDate}.json`);
            document.body.appendChild(downloadAnchorNode);
            downloadAnchorNode.click();
            downloadAnchorNode.remove();
        }
    };

    const handleCheckboxChange = (event) => {
        setRestoreWindowSize(event.target.checked);
    };

    if (!selectdSnapshotId) {
        return null;
    }

    return (
        <Container>
            <Stack direction="row" justifyContent="flex-end" sx={{ marginBottom: 2 }} spacing={2}>
                <FormControlLabel
                    control={<Checkbox checked={restoreWindowSize} onChange={handleCheckboxChange} />}
                    label={t('restoreWindowSize')}
                />
                <Button variant="outlined" color="error" onClick={handleExportSnapshot}>{t('exportSnapshot')}</Button>
                <Button variant="outlined" color="error" onClick={handleOpenAllWindows}>
                    {selectedTabsCount > 0 ? `${t('openSelectedInNewWindow')} (${selectedTabsCount})` : t('openAllWindows')}
                </Button>
                {selectedTabsCount > 0 && (
                    <Button variant="outlined" color="error" onClick={handleOpenSelectedInCurrentWindow}>
                        {t('openSelectedInCurrentWindow')} ({selectedTabsCount})
                    </Button>
                )}
            </Stack>
            {snapshotDetails?.windows.map((window, windowIndex) => (
                <WindowItem
                    key={windowIndex}
                    window={window}
                    windowIndex={windowIndex}
                    restoreWindowSize={restoreWindowSize}
                />
            ))}
        </Container>
    );
};

export default SnapshotDetails;
