import React, { useContext } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, Radio, RadioGroup, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function ChromeTabGroupsCollapse() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.chromeTabGroupAdvanced.tabGroupCollapseBehavior' });

    const { updateSetting, tabGroupCollapse } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabGroupCollapse: state.settings.tabGroupCollapse
    }));

    const handleChange = async (event) => {
        await updateSetting('tabGroupCollapse', event.target.value)
        await sendRuntimeMessage("update_setting", { key: "tabGroupCollapse", value: event.target.value })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl>
                    {/* <FormLabel id="demo-row-radio-buttons-group-label">Gender</FormLabel> */}
                    <RadioGroup
                        // row
                        name="gender"
                        value={tabGroupCollapse}
                        onChange={handleChange}
                    >
                        <FormControlLabel value="collapse_others" control={<Radio />} label={t('option_collapse_others')} />
                        <FormControlLabel value="disable" control={<Radio />} label={t('option_disable')} />
                        <FormControlLabel value="auto" control={<Radio />} label={t('option_auto')} />

                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>

    )

}