import React, { useState } from 'react';
import {
    Button,
    Checkbox,
    FormGroup,
    FormControlLabel,
    Typography,
    Paper,
    Box,
    Divider,
    Container,
    Grid,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Alert,
} from '@mui/material';
import { toast } from 'react-toastify';
import DownloadIcon from '@mui/icons-material/Download';
import { userPublicLocalStorage } from '../../../../core/user';
import { FileDownload } from '@mui/icons-material';
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStoreCacheInstance } from '../../../../core/stores/userStore';
import { snapshotCacheInstance } from '../../../../core/stores/snapshotStore';
import { localCacheQuickAccessInstance } from '../../../../core/stores/tabStore';
import { localCacheSiteInstance } from '../../../../core/stores/siteStore';
import { localCacheHistoriesInstance } from '../../../../core/stores/historyStore';

// Utility function to get data based on the selected options
const getData = async (selectedOptions) => {
    const data = {};
    if (selectedOptions.preferences) {
        data.preferences = await new Promise((resolve) => {
            chrome.storage.local.get(null, (result) => {
                resolve(result);
            });
        });
        const cacheInstance = await userStoreCacheInstance();
        const backgroundImageUrl = await cacheInstance.get("backgroundImageUrl");
        data.preferences.user_settings.backgroundImageUrl = backgroundImageUrl;
        const background_image_urls = await cacheInstance.get('background_image_urls') || [];
        data.preferences.background_image_urls = background_image_urls;
    }
    if (selectedOptions.quickAccess) {
        const quickAccessInstance = await localCacheQuickAccessInstance();
        data.quickAccess = await quickAccessInstance.get("quickAccess");
    }
    if (selectedOptions.siteConfig) {
        const siteConfigInstance = await localCacheSiteInstance();
        data.siteConfig = await siteConfigInstance.all();
    }
    if (selectedOptions.recentlyTabs) {
        const recentlyTabsInstance = await localCacheHistoriesInstance();
        data.recentlyTabs = await recentlyTabsInstance.all();
    }
    if (selectedOptions.snapshots) {
        const snapshotInstance = await snapshotCacheInstance();
        const keys = await snapshotInstance.keys();
        const snapshotKeys = keys.filter(key => key.startsWith('snapshot_detail_'));
        data.snapshots = await Promise.all(snapshotKeys.map(key => snapshotInstance.get(key)));
    }
    return data;
};

// Export Component
const ExportComponent = () => {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.importAndExport" });

    const [open, setOpen] = useState(false);
    const [selectedOptions, setSelectedOptions] = useState({
        preferences: true,
        quickAccess: true,
        siteConfig: true,
        recentlyTabs: true,
        snapshots: true,
    });

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleOptionChange = (event) => {
        setSelectedOptions({ ...selectedOptions, [event.target.name]: event.target.checked });
    };

    const handleExport = async () => {
        try {
            const data = await getData(selectedOptions);
            const jsonString = JSON.stringify(data, null, 2);
            const blob = new Blob([jsonString], { type: 'application/json' });
            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            const exportTime = new Date().toLocaleString('sv-SE', { hour12: false }).replace(/[:]/g, '').replace(/[ ]/g, '_');
            link.download = `vertiTab_extension_export_${exportTime}.json`;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            URL.revokeObjectURL(url);

            handleClose();
        } catch (error) {
            console.error('Error exporting data:', error);
            toast.error(t('export.exportFailed'));
        }
    };

    return (
        <Container maxWidth="sm">
            <Box textAlign="center">
                <Button
                    variant="contained"
                    color="error"
                    onClick={handleClickOpen}
                    startIcon={<FileDownload />}
                    style={{ padding: '8px 24px' }}
                >
                    {t('export.label')}
                </Button>
            </Box>
            <Dialog open={open} onClose={(event, reason) => { if (reason !== 'backdropClick') { setOpen(false) } }}>
                <DialogTitle variant='h3'>{t('export.label')}</DialogTitle>
                <DialogContent>
                    <DialogContentText variant="h4" color="textSecondary" gutterBottom>
                        {t('export.formTips')}
                    </DialogContentText>
                    <Divider sx={{ my: 1 }} />
                    <FormGroup  >
                        <Grid container spacing={0}>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.preferences}
                                            onChange={handleOptionChange}
                                            name="preferences"
                                            color="error"
                                        />
                                    }
                                    label={t('options.preferences')}
                                />
                                <Alert severity='info'>{t('options.preferencesInfo')}</Alert>

                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.quickAccess}
                                            onChange={handleOptionChange}
                                            name="quickAccess"
                                            color="error"
                                        />
                                    }
                                    label={t('options.quickAccess')}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.siteConfig}
                                            onChange={handleOptionChange}
                                            name="siteConfig"
                                            color="error"
                                        />
                                    }
                                    label={t('options.siteConfig')}
                                />
                                <Alert severity='info'>{t('options.siteConfigInfo')}</Alert>

                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.recentlyTabs}
                                            onChange={handleOptionChange}
                                            name="recentlyTabs"
                                            color="error"
                                        />
                                    }
                                    label={t('options.recentlyTabs')}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={selectedOptions.snapshots}
                                            onChange={handleOptionChange}
                                            name="snapshots"
                                            color="error"
                                        />
                                    }
                                    label={t('options.snapshots')}
                                />
                            </Grid>
                        </Grid>
                    </FormGroup>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="error">
                        {t('cancelBtn')}
                    </Button>
                    <Button
                        onClick={handleExport}
                        color="error"
                        variant="contained"
                        disabled={!Object.values(selectedOptions).some(Boolean)}
                    >
                        {t('exportBtn')}
                    </Button>
                </DialogActions>
            </Dialog>
        </Container>
    );
};

export default ExportComponent;