import React, { useState } from "react";
import userStore from "../../../../core/stores/userStore";
import { Add, Edit, Delete } from "@mui/icons-material";
import { Box, List, ListItem, ListItemText, IconButton, TextField, Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, Typography } from "@mui/material";
import { Check } from "@mui/icons-material";
import { DndContext, closestCenter, useSensor, useSensors, MouseSensor, TouchSensor } from "@dnd-kit/core";
import { arrayMove, rectSortingStrategy, SortableContext, sortableKeyboardCoordinates, verticalListSortingStrategy } from "@dnd-kit/sortable";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function CustomGroupNames() {


    const { t } = useTranslationContext('options', { keyPrefix: 'settings.chromeTabGroupAdvanced.customGroupNames' });

    const { updateSetting, customGroupNames } = userStore((state) => ({
        updateSetting: state.updateSetting,
        customGroupNames: state.settings.customGroupNames
    }));

    const [groupNames, setGroupNames] = useState(customGroupNames);
    const [newGroupName, setNewGroupName] = useState("");
    const [editIndex, setEditIndex] = useState(null);
    const [editGroupName, setEditGroupName] = useState("");
    const [open, setOpen] = useState(false);
    const [deleteIndex, setDeleteIndex] = useState(null);
    const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);

    const handleAddGroup = () => {
        if (newGroupName.trim() !== "") {
            const updatedGroupNames = [...groupNames, { id: groupNames.length, name: newGroupName, index: groupNames.length }];
            setGroupNames(updatedGroupNames);
            updateSetting('customGroupNames', updatedGroupNames);
            setNewGroupName("");
        }
    };

    const handleEditGroup = (index) => {
        setEditIndex(index);
        setEditGroupName(groupNames[index].name);
    };

    const handleSaveEdit = () => {
        const updatedGroupNames = groupNames.map((group, index) =>
            index === editIndex ? { ...group, name: editGroupName } : group
        );
        setGroupNames(updatedGroupNames);
        updateSetting('customGroupNames', updatedGroupNames);
        setEditIndex(null);
        setEditGroupName("");
    };

    const handleDeleteGroup = (index) => {
        setDeleteIndex(index);
        setDeleteDialogOpen(true);
    };

    const confirmDeleteGroup = () => {
        const updatedGroupNames = groupNames.filter((_, i) => i !== deleteIndex);
        setGroupNames(updatedGroupNames);
        updateSetting('customGroupNames', updatedGroupNames);
        setDeleteDialogOpen(false);
        setDeleteIndex(null);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleDeleteDialogClose = () => {
        setDeleteDialogOpen(false);
        setDeleteIndex(null);
    };

    const handleDragEnd = (event) => {
        const { active, over } = event;

        if (!active || !over) { return; }

        if (active.id !== over.id) {
            setGroupNames((items) => {
                const oldIndex = items.findIndex((item) => item.id === active.id);
                const newIndex = items.findIndex((item) => item.id === over.id);

                const updatedGroupNames = arrayMove(items, oldIndex, newIndex);
                updateSetting('customGroupNames', updatedGroupNames);
                return updatedGroupNames;
            });
        }
    };

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    function SortableItem(props) {
        const { attributes, listeners, setNodeRef, transform, transition } = useSortable({ id: props.id });

        const style = {
            transform: CSS.Transform.toString(transform),
            transition,
        };

        return (
            <Grid item xs={12} sm={6} md={4} lg={3} sx={{ border: "1px solid #eee" }} ref={setNodeRef} style={style} {...attributes} {...listeners}>
                <ListItem>
                    {editIndex === props.index ? (
                        <TextField
                            size="small"
                            value={editGroupName}
                            onChange={(e) => setEditGroupName(e.target.value)}
                            inputProps={{
                                sx: { padding: "4px" }
                            }}
                        />
                    ) : (
                        <ListItemText sx={{ cursor: "move" }} primary={props.name} />
                    )}
                    {editIndex === props.index ? (
                        <IconButton sx={{ padding: "4px" }} onClick={() => handleSaveEdit(props.index)}>
                            <Check />
                        </IconButton>
                    ) : (
                        <IconButton sx={{ padding: "4px" }} onClick={() => handleEditGroup(props.index)}>
                            <Edit />
                        </IconButton>
                    )}
                    <IconButton sx={{ padding: "4px" }} onClick={() => handleDeleteGroup(props.index)}>
                        <Delete />
                    </IconButton>
                </ListItem>
            </Grid>
        );
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <Box>
                    <Button color="error" size="small" variant="contained" onClick={handleClickOpen}>
                        {t('ManageGroupNames')}
                    </Button>
                    <Dialog maxWidth="md" open={open} onClose={(event, reason) => { if (reason !== 'backdropClick') { handleClose } }}>
                        <DialogTitle variant="h3">{t('CustomGroupNames')}</DialogTitle>
                        <DialogContent>
                            <DndContext collisionDetection={closestCenter} onDragEnd={handleDragEnd} sensors={sensors}>
                                <SortableContext items={groupNames} strategy={rectSortingStrategy}>
                                    <Grid container>
                                        {groupNames.map((group, index) => (
                                            <SortableItem key={group.id} id={group.id} index={index} name={group.name} />
                                        ))}
                                    </Grid>
                                </SortableContext>
                            </DndContext>
                            <Box sx={{ mt: 2 }}>
                                <TextField
                                    size="small"
                                    value={newGroupName}
                                    onChange={(e) => setNewGroupName(e.target.value)}
                                    placeholder={t('AddNewName')}
                                    inputProps={{
                                        sx: { padding: "8px" }
                                    }}
                                />
                                <IconButton onClick={handleAddGroup}>
                                    <Add />
                                </IconButton>
                            </Box>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose}>{t('Close')}</Button>
                        </DialogActions>
                    </Dialog>
                    <Dialog open={deleteDialogOpen} onClose={handleDeleteDialogClose}>
                        <DialogTitle variant="h4">{t('ConfirmDelete')}</DialogTitle>
                        <DialogContent>
                            {t('ConfirmDeleteMsg')}
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleDeleteDialogClose}>{t('Cancel')}</Button>
                            <Button onClick={confirmDeleteGroup} color="secondary">{t('Delete')}</Button>
                        </DialogActions>
                    </Dialog>
                </Box>
            </Grid>
        </Grid>

    )

}