import React, { useContext } from "react";

import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { FormControlLabel, Grid, Switch, Tooltip, Typography, Alert } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import userStore from "../../../../core/stores/userStore";
import { Link } from "react-router-dom";



export default function ReadingProgressBar({ hostname }) {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.readingProgressBar" });


    const { globalReadingProgressBar } = userStore((state) => ({
        globalReadingProgressBar: state.settings.readingProgressBar && state.settings.enableProgressBar
    }))


    const { readingProgressBar, setSiteConfig } = siteStore((state) => ({
        readingProgressBar: state.sites.find(site => site.hostname === hostname)?.readingProgressBar,
        setSiteConfig: state.setSiteConfig
    }));

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { readingProgressBar: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { readingProgressBar: event.target.checked } });

    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>

                <FormControlLabel control={<Switch color="error" disabled={!globalReadingProgressBar} checked={globalReadingProgressBar && readingProgressBar !== false}
                    onChange={handleChange} />}
                />

            </Grid>
            {!globalReadingProgressBar && <Link target="_blank" to={`/progressBar`}><Alert severity="warning">{t('globaldisabledTips')}</Alert></Link>}
        </Grid>

    )

}
