import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { FormControl, Grid, InputAdornment, OutlinedInput, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function TabPreviewWaitSeconds() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.previewDelay" });


    const { updateSetting, tabPreviewEnable, tabPreviewWaitSeconds } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds

    }));

    const handleTabPreviewWaitSecondsChange = async (event) => {
        await updateSetting('tabPreviewWaitSeconds', event.target.value)
        await sendRuntimeMessage("update_setting", { key: "tabPreviewWaitSeconds", value: (event.target.value) })
    }


    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '100px' }} variant="outlined">
                    <OutlinedInput
                        disabled={!tabPreviewEnable}
                        value={tabPreviewWaitSeconds}
                        type="text"
                        endAdornment={<InputAdornment position="end">{t('seconds')}</InputAdornment>}
                        aria-describedby=""
                        onChange={handleTabPreviewWaitSecondsChange}
                    />
                </FormControl>
            </Grid>
        </Grid>
    )

}
