import React from "react";

import Language from './Language';
import Theme from './Theme';
import Rtl from './Rtl';

import SearchBoxPosition from "./SearchBoxPosition";
import ExtensionIconAction from "./ExtensionIconAction";
import ButtonPosition from "./ButtonPosition";
import ShowToolbar from "./ShowToolbar";
import ShowBottomNav from "./ShowBottomNav";
import { Box, Button, Divider, Grid, Stack, Typography } from "@mui/material";
import FullScreenHideSidePanel from "./FullScreenHideSidePanel";
import Sidepanel from "./Sidepanel";
import { resetAppearanceSettings } from "../../../../core/startup";
import { memo } from "react";
import AlwaysShowCloseButton from "./AlwaysShowCloseButton";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import ShowTabNav from "./ShowTabNav";


export default memo(function Appearance() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings" });

    const handleResetSettings = async () => {
        await resetAppearanceSettings();

    }


    return (

        <Box>
            <Grid container alignItems="center" mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("modules.appearance")}</Typography></Grid>
                <Grid item xs={4}><Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings',)}</Button></Grid>
                {/* <Grid item xs={2}><Button color="error" onClick={handleResetAllSettings}>{t('Reset all settings')}</Button></Grid> */}
            </Grid>

            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>

                <Language />
                <Theme />
                <Rtl />

                {('sidePanel' in chrome) && <ExtensionIconAction />}
                {('sidePanel' in chrome) && !navigator.userAgent.includes("Edg") && <Sidepanel />}

                <SearchBoxPosition />
                <ShowToolbar />
                <ShowTabNav />
                <ShowBottomNav />
                <ButtonPosition />
                <AlwaysShowCloseButton />
                <FullScreenHideSidePanel />
            </Stack>

        </Box>

    )


})