import React, { useContext } from "react";
import { Box, Card, CardContent, CardMedia, Divider, FormControlLabel, Grid, Paper, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { sendMessageToAllTabs } from "../../../../core/browers/tabs";


export default function PlayingProgressBar() {
    const { t } = useTranslationContext('options', { keyPrefix: 'settings.progressBar.playingProgressBar' });

    const { enableProgressBar, playingProgressBar, updateSetting } = userStore((state) => ({
        enableProgressBar: state.settings.enableProgressBar,
        playingProgressBar: state.settings.playingProgressBar,
        updateSetting: state.updateSetting
    }));

    const handleChanged = async (event) => {
        await updateSetting('playingProgressBar', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "playingProgressBar", value: event.target.checked })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel disabled={!enableProgressBar} control={<Switch color="error" checked={enableProgressBar && playingProgressBar}
                    onChange={handleChanged} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>

    )
}