import React, { useState, useEffect } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Grid, Typography, FormControlLabel, Switch, Button, Dialog, DialogActions, DialogContent, DialogTitle, Box, Paper, IconButton, ListItem, Stack, Divider, TextField, Card } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { DndContext, closestCenter, MouseSensor, TouchSensor, useSensor, useSensors } from '@dnd-kit/core';
import { arrayMove, SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import DeleteIcon from '@mui/icons-material/Delete';
import { ArrowRight } from "@mui/icons-material";
import { Close } from "@mui/icons-material";
import { Add } from "@mui/icons-material";
import { toast } from "react-toastify";
import WithPermission from "../../../component/WithPermission";

export default function ShowTabNav() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.tabNav" });

    const { updateSetting, showTabNavs, tabNavOptions } = userStore((state) => ({
        updateSetting: state.updateSetting,
        showTabNavs: state.settings.showTabNavs,
        tabNavOptions: state.settings.tabNavOptions
    }));

    const [open, setOpen] = useState(false);

    const handleShowTabNavChange = async (event) => {
        await updateSetting('showTabNavs', event.target.checked);
        await sendRuntimeMessage("update_setting", { key: "showTabNavs", value: event.target.checked });
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleDragEnd = async (event) => {
        const { active, over } = event;
        if (!active?.id || !over?.id || active.id === over.id) return;
        const oldIndex = tabNavOptions.navs.findIndex(item => item.id === active.id);
        const newIndex = tabNavOptions.navs.findIndex(item => item.id === over.id);
        const newNavs = arrayMove(tabNavOptions.navs, oldIndex, newIndex).map((item, index) => ({ ...item, sort: index + 1 }));
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });

    };

    const handleEnable = async (id) => {
        const newNavs = tabNavOptions.navs.map((item) =>
            item.id === id ? { ...item, enable: true } : item
        );
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });
    };

    const handleSwitchLabel = async (id) => {
        const newNavs = tabNavOptions.navs.map((item) =>
            item.id === id ? { ...item, showLabel: !item.showLabel } : item
        );
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });
    };

    const handleSwitchCount = async (id) => {
        const newNavs = tabNavOptions.navs.map((item) =>
            item.id === id ? { ...item, showCount: !item.showCount } : item
        );
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });
    };

    const handleDaysChange = async (id, days) => {
        const newNavs = tabNavOptions.navs.map((item) =>
            item.id === id ? { ...item, days: days } : item
        );
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });
    };

    const handleDelete = async (id) => {

        const curNav = tabNavOptions.navs.find((item) => item.id === id);
        if (curNav.name === 'version') {
            const { isOnToolbar } = await chrome.action.getUserSettings();
            if (!isOnToolbar) {
                toast(t('pinToToolbarReminder'));
                return;
            }
        }

        const newNavs = tabNavOptions.navs.map((item) =>
            item.id === id ? { ...item, enable: false } : item
        );
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });
    };

    const SortableItem = (props) => {
        const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({ id: props.id });
        const style = {
            transform: CSS.Transform.toString(transform),
            transition,
            zIndex: isDragging ? 1000 : 'auto',
            opacity: isDragging ? 0.8 : 1,
            display: 'flex',
            alignItems: 'center',
            position: 'relative'
        };
        return (
            <Card
                sx={{ border: "1px solid #e0e0e0", borderRadius: 2, p: 2, display: 'flex', alignItems: 'center', cursor: 'move', boxShadow: 3 }}
                ref={setNodeRef}
                style={style}
                {...attributes}
                {...listeners}
                component="div"
            >
                <IconButton
                    size="small"
                    onClick={() => handleDelete(props.id)}
                    sx={{ position: 'absolute', top: 0, right: 0 }}
                >
                    <Close color="error" fontSize="small" />
                </IconButton>
                <Stack direction="column" spacing={2} useFlexGap sx={{ padding: '5px' }} >
                    {props.permission ? <WithPermission permissionName={props.permission}><Typography
                        variant="h4"
                        color="error"
                        size="small"
                        sx={{ cursor: 'move' }}
                    >
                        {t(props.name)}
                    </Typography></WithPermission> : <Typography
                        variant="h4"
                        color="error"
                        size="small"
                        sx={{ cursor: 'move' }}
                    >
                        {t(props.name)}
                    </Typography>}

                    {props.showCount !== undefined && (
                        <FormControlLabel
                            control={
                                <Switch
                                    size="small"
                                    color="error"
                                    checked={props.showCount}
                                    onChange={() => handleSwitchCount(props.id)}
                                    name='showCount'
                                />
                            }
                            label={t('showCount')}
                        />
                    )}
                    {props.days !== undefined && (
                        <TextField
                            size="small"
                            color="error"
                            type="text"
                            value={props.days}
                            onChange={(e) => handleDaysChange(props.id, e.target.value)}
                            label={t('days')}
                        />
                    )}
                </Stack>
            </Card>
        );
    };

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    return (
        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={2}>
                <FormControlLabel control={<Switch color="error" checked={showTabNavs}
                    onChange={handleShowTabNavChange} />}
                    labelPlacement="start"
                />
            </Grid>
            {showTabNavs &&
                <Grid item xs={2}>
                    <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={handleOpen}>
                        {t('configure')}
                    </Button>
                </Grid>
            }
            <Dialog fullWidth={true} maxWidth="lg" open={open}
                onClose={(event, reason) => { if (reason !== 'backdropClick') { handleClose } }}
            >
                <DialogTitle>{t("configureTabNav")}</DialogTitle>
                <DialogContent>
                    <Box mb={3}>
                        <Typography variant="h3" gutterBottom>{t("enabledNavs")}</Typography>
                        <Paper variant="outlined" style={{ padding: '16px', marginBottom: '16px' }}>
                            <DndContext
                                onDragEnd={handleDragEnd}
                                sensors={sensors}
                            >
                                <SortableContext
                                    items={tabNavOptions.navs.filter(option => option.enable).map(option => option.id).sort((a, b) => a.sort - b.sort)}
                                // strategy={verticalListSortingStrategy}
                                >
                                    <Box display="flex" flexWrap="wrap" gap={1}>
                                        {tabNavOptions.navs.filter(option => option.enable).map((option) => (
                                            <SortableItem key={option.id} id={option.id} name={option.name} showLabel={option.showLabel} showCount={option.showCount} days={option.days} permission={option.permission} />
                                        ))}
                                    </Box>
                                </SortableContext>
                            </DndContext>
                        </Paper>
                    </Box>
                    <Box mb={3}>
                        <Typography variant="h3" gutterBottom>{t("disabledNavs")}</Typography>
                        <Paper variant="outlined" style={{ padding: '16px' }}>
                            <Box display="grid" gridTemplateColumns="repeat(auto-fill, minmax(200px, 1fr))" gap={1}>
                                {tabNavOptions.navs.filter(option => !option.enable).sort((a, b) => a.id - b.id).map((option) => (

                                    option.permission ?
                                        // <WithPermission key={`wp-${option.id}`} permissionName={option.permission}>
                                        <Button key={option.id} endIcon={<Add />} variant="outlined" size="small" onClick={() => handleEnable(option.id)}>
                                            {t(option.name)}
                                        </Button>
                                        // </WithPermission> 
                                        : <Button key={option.id} endIcon={<Add />} variant="outlined" size="small" onClick={() => handleEnable(option.id)}>
                                            {t(option.name)}
                                        </Button>


                                ))}
                            </Box>
                        </Paper>
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="error">
                        {t("close")}
                    </Button>
                </DialogActions>
            </Dialog >
        </Grid >
    );
}