import React from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Grid, Typography, FormControlLabel, Switch } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { getGradientColor } from "../../../../utils/colord";
import Slider from '@mui/material/Slider';

export default function BackgroundGradient() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.beautify.backgroundGradient" });

    const { updateSetting, backgroundGradient, backgroundGradientDeg, backgroundColor } = userStore((state) => ({
        updateSetting: state.updateSetting,
        backgroundGradient: state.settings.backgroundGradient,
        backgroundGradientDeg: state.settings.backgroundGradientDeg,
        backgroundColor: state.settings.backgroundColor,
    }));

    const handleChange = async (event) => {
        const isChecked = event.target.checked;
        await updateSetting('backgroundGradient', isChecked);
        await sendRuntimeMessage("update_setting", { key: "backgroundGradient", value: isChecked });
        const finalColor = isChecked ? getGradientColor(backgroundColor, backgroundGradientDeg) : backgroundColor;
        await updateSetting("background", finalColor);
        await sendRuntimeMessage("update_setting", { key: "background", value: finalColor });
    }

    const handleAngleChange = async (event, newValue) => {
        await updateSetting('backgroundGradientDeg', newValue);
    }

    const handleAngleChangeCommitted = async (event, newValue) => {
        await updateSetting('backgroundGradientDeg', newValue);
        await sendRuntimeMessage("update_setting", { key: "backgroundGradientDeg", value: newValue });
        if (backgroundGradient) {
            const finalColor = getGradientColor(backgroundColor, newValue);
            await updateSetting("background", finalColor);
            await sendRuntimeMessage("update_setting", { key: "background", value: finalColor });
        }
    }

    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")}</Typography>
            </Grid>
            <Grid item xs={1}>
                <FormControlLabel control={<Switch color="error" checked={backgroundGradient}
                    onChange={handleChange} />}
                    labelPlacement="start"
                />
            </Grid>
            <Grid item xs={3}>
                {backgroundGradient && (
                    <Slider
                        min={0}
                        max={360}
                        step={1}
                        onChange={handleAngleChange}
                        onChangeCommitted={handleAngleChangeCommitted}
                        size="small"
                        sx={{ width: "calc(100% - 60px)", marginLeft: "10px" }}
                        value={backgroundGradientDeg}
                        color="error"
                        valueLabelDisplay="auto"
                        valueLabelFormat={(value) => `${value}°`}
                    />
                )}
            </Grid>
        </Grid>
    )
}