import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import Markdown from 'react-markdown'
import remarkGfm from 'remark-gfm'

export default function TabSwitchPipBackground() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.picture_in_picture.tabSwitchPipBackground' });

    const { updateSetting, tabSwitchPipBackground } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabSwitchPipBackground: state.settings.tabSwitchPipBackground,
    }));


    const handleChange = async (event) => {
        await updateSetting('tabSwitchPipBackground', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "tabSwitchPipBackground", value: event.target.checked })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'><Markdown remarkPlugins={[remarkGfm]}>{t("description")}</Markdown> </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={tabSwitchPipBackground}
                    onChange={handleChange} />}
                    labelPlacement="end"
                />
            </Grid>
        </Grid>

    )

}