import React, { useContext } from "react";
import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function ShowHistoryTabs() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.history.showInSidePanel" });
    const { updateSetting, enableHistory, historyDataSource, showHistoryTabs, tabNavOptions } = userStore((state) => ({
        updateSetting: state.updateSetting,
        enableHistory: state.settings.enableHistory,
        historyDataSource: state.settings.historyDataSource,
        showHistoryTabs: state.settings.showHistoryTabs,
        tabNavOptions: state.settings.tabNavOptions,

    }));


    const handleChange = async (event) => {


        await updateSetting('showHistoryTabs', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "showHistoryTabs", value: event.target.checked })

        //Deprecated

        const newNavs = tabNavOptions.navs.map((item) =>
            item.name === (historyDataSource === 'recentTabs' ? "RecentlyTabs" : 'history') ? { ...item, enable: true } : item
        );
        const newOptions = { ...tabNavOptions, navs: newNavs };
        await updateSetting('tabNavOptions', newOptions);
        await sendRuntimeMessage("update_setting", { key: "tabNavOptions", value: newOptions });
        await updateSetting('showTabNavs', true);
        await sendRuntimeMessage("update_setting", { key: "showTabNavs", value: true });

        if (!event.target.checked) {
            await updateSetting('activeTabIndex', 1)
            await sendRuntimeMessage("update_setting", { key: "activeTabIndex", value: 1 })
        }
    }
    return (
        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={enableHistory && showHistoryTabs}
                    onChange={handleChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>
    )
}