import React, { useContext } from "react";
import { Box, Divider, FormControl, FormControlLabel, Grid, InputAdornment, OutlinedInput, Stack, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";



export default function DiscardInactiveTabsMinutes() {
    const { t } = useTranslationContext('options', { keyPrefix: 'settings.performance.discardInactiveTabsMinutes' });
    const { updateSetting, saveMemoryAction, discardInactiveTabsMinutes } = userStore((state) => ({
        updateSetting: state.updateSetting,
        saveMemoryAction: state.settings.saveMemoryAction,
        discardInactiveTabsMinutes: state.settings.discardInactiveTabsMinutes
    }));


    const handleChange = async (event) => {
        await updateSetting('discardInactiveTabsMinutes', parseInt(event.target.value))
        await sendRuntimeMessage("update_setting", { key: "discardInactiveTabsMinutes", value: parseInt(event.target.value) })
    }

    return (

        <Grid container alignItems="center">
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body2" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControl sx={{ m: 1, width: '200px' }} variant="outlined">
                    <OutlinedInput
                        disabled={saveMemoryAction === "disabled"}
                        value={discardInactiveTabsMinutes}
                        inputProps={{ itemType: 'number', max: 10000, min: 1, step: 1 }}
                        endAdornment={<InputAdornment position="end">{t('minutes')}</InputAdornment>}
                        aria-describedby="outlined-weight-helper-text"
                        onChange={handleChange}
                    />
                </FormControl>
            </Grid>
        </Grid>

    )
}