import React from 'react';
import { Container, Typography, Box, Grid, Stack } from '@mui/material';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import PurchaseInstructions from './PurchaseInstructions';
import PremiumIcon from '../../component/PremiumIcon';


const Premium = () => {

    const { t } = useTranslationContext();

    return (
        <Container>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("premiumEdition", { ns: "translation" })}</Typography></Grid>
                <Grid item xs={4}>
                    {/* <Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings')}</Button> */}
                </Grid>

            </Grid>
            <Stack spacing={2}>
                <PurchaseInstructions />
            </Stack>
        </Container>
    );
};

export default Premium;
