import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";


export default function Rtl() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.rtl" });

    const { updateSetting, rtl } = userStore((state) => ({
        updateSetting: state.updateSetting,
        rtl: state.settings.rtl,

    }));


    const handleRTLChange = async (event) => {
        await updateSetting("rtl", event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "rtl", value: event.target.checked })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={rtl}
                    onChange={handleRTLChange} />}
                    labelPlacement="start"
                />
            </Grid>
        </Grid>

    )
}