import React from 'react';
import { Alert, AlertTitle, Box, Typography } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';

const ContentScriptWarning = () => {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabSidebarOnPages.contentScriptWarning" });

    const createLink = (text) => {
        const urlRegex = /(https?:\/\/[^\s]+)/g;
        return text.split(urlRegex).map((part, index) =>
            urlRegex.test(part) ? <a key={index} href={part} target="_blank" rel="noopener noreferrer">{part}</a> : part
        );
    };

    return (
        <Alert severity="warning">
            <AlertTitle>{createLink(t('warningTitle'))}</AlertTitle>
            {createLink(t('warningMessage'))}
            <Box mt={2}>
                <ul>
                    <li><Typography>{createLink(t('webStore'))}</Typography></li>
                    <li><Typography>{createLink(t('internalPages'))}</Typography></li>
                    <li><Typography>{createLink(t('fileUrls'))}</Typography></li>
                    <li><Typography>{createLink(t('extensionsPages'))}</Typography></li>
                    <li><Typography>{createLink(t('protectedPages'))}</Typography></li>
                    <li><Typography>{createLink(t('newTabPage'))}</Typography></li>
                    <li><Typography>{createLink(t('pdfViewer'))}</Typography></li>
                </ul>
            </Box>
        </Alert>
    );
};

export default ContentScriptWarning;
