import React, { useState, useEffect } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { FormControlLabel, Grid, Switch, Typography, Dialog, DialogActions, DialogContent, DialogTitle, Button, Checkbox, List, ListItem, ListItemText, Box, Tooltip } from "@mui/material";
import { sendRuntimeMessage, sendTabMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import { useParams } from "react-router-dom";
import { clearCookiesfromUrl } from "../../../../core/browers/cookies";
import { cleanHistoriesByHostname } from "../../../../core/stores/historyStore";
import SettingsIcon from '@mui/icons-material/Settings';
import InfoIcon from '@mui/icons-material/Info';
import { ArrowRight } from "@mui/icons-material";

export default function Incognito({ hostname }) {
    const params = useParams();
    hostname = hostname || params.hostname;
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.incognito" });
    const { incognito, incognitoOptions, setSiteConfig } = siteStore((state) => ({
        incognito: state.sites.find(site => site.hostname === hostname)?.incognito,
        incognitoOptions: state.sites.find(site => site.hostname === hostname)?.incognitoOptions,
        setSiteConfig: state.setSiteConfig
    }));

    const [openDialog, setOpenDialog] = useState(false);
    const [cleanOptions, setCleanOptions] = useState({
        cache: incognitoOptions?.cache ?? true,
        cacheStorage: incognitoOptions?.cacheStorage ?? true,
        cookies: incognitoOptions?.cookies ?? false,
        skipAccountCookies: incognitoOptions?.skipAccountCookies ?? true,
        history: incognitoOptions?.history ?? true,
        indexedDB: incognitoOptions?.indexedDB ?? true,
        localStorage: incognitoOptions?.localStorage ?? true,
        serviceWorkers: incognitoOptions?.serviceWorkers ?? true,
        webSQL: incognitoOptions?.webSQL ?? true
    });


    const handleChange = async (event) => {
        await updateIncognitoSetting(event.target.checked);
    }

    const handleConfirm = async () => {
        setOpenDialog(false);
        await updateIncognitoSetting(true, cleanOptions);
    }

    const handleCancel = () => {
        setOpenDialog(false);
    }

    const updateIncognitoSetting = async (value, options = cleanOptions) => {
        await setSiteConfig(hostname, { incognito: value, incognitoOptions: options });
        await sendRuntimeMessage('update_site_config', { hostname, config: { incognito: value, incognitoOptions: options } });
        if (value) {
            const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
            if (openTabs.length === 0) return;
            for (const tab of openTabs) {
                await sendTabMessage(tab.id, 'clear_browsing_data', { options });
                if (options.cookies) await clearCookiesfromUrl(tab.url, options.skipAccountCookies);
                if (options.history) await cleanHistoriesByHostname(hostname);
            }
        }
    }

    const handleCleanOptionChange = (option) => {
        setCleanOptions(prev => ({ ...prev, [option]: !prev[option] }));
    }

    const handleSelectAll = () => {
        const allSelected = Object.values(cleanOptions).every(Boolean);
        setCleanOptions(Object.fromEntries(Object.keys(cleanOptions).map(key => [key, !allSelected])));
    }

    return (
        <>
            <Grid container alignItems="center">
                <Grid item xs={12} md={8}>
                    <Typography variant="h3" mb={1}>{t("label")}</Typography>
                    <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
                </Grid>
                <Grid item xs={12} md={4}>
                    <Box display="flex" alignItems="center">
                        <FormControlLabel
                            control={
                                <Switch
                                    color="error"
                                    checked={incognito === true}
                                    onChange={handleChange}
                                />
                            }
                        />
                        {incognito && (
                            <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={() => setOpenDialog(true)}>
                                {t('configure')}
                            </Button>

                        )}
                    </Box>
                </Grid>
            </Grid>

            <Dialog
                open={openDialog}
                onClose={handleCancel}
                maxWidth="md"
                fullWidth
            >
                <DialogTitle>{t("configure")}</DialogTitle>
                <DialogContent>
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                        {t("configureDescription")}
                    </Typography>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <List>
                                {Object.entries(cleanOptions).slice(0, Math.ceil(Object.entries(cleanOptions).length / 2)).map(([option, checked]) => (
                                    <ListItem key={option} dense button onClick={() => handleCleanOptionChange(option)}>
                                        <Checkbox checked={checked} />
                                        <ListItemText primary={t(option)} />
                                        {option === 'cookies' && (
                                            <Tooltip title={t("cookieWarning")}>
                                                <InfoIcon color="info" />
                                            </Tooltip>
                                        )}
                                    </ListItem>
                                ))}
                            </List>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <List>
                                {Object.entries(cleanOptions).slice(Math.ceil(Object.entries(cleanOptions).length / 2)).map(([option, checked]) => (
                                    <ListItem key={option} dense button onClick={() => handleCleanOptionChange(option)}>
                                        <Checkbox checked={checked} />
                                        <ListItemText primary={t(option)} />
                                        {option === 'cookies' && (
                                            <Tooltip title={t("cookieWarning")}>
                                                <InfoIcon color="info" />
                                            </Tooltip>
                                        )}
                                    </ListItem>
                                ))}
                            </List>
                        </Grid>
                    </Grid>
                    <Box mt={2}>
                        <FormControlLabel
                            control={<Checkbox checked={Object.values(cleanOptions).every(Boolean)} onChange={handleSelectAll} />}
                            label={t("selectAll")}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCancel}>{t("cancel")}</Button>
                    <Button onClick={handleConfirm} color="primary" variant="contained">{t("confirm")}</Button>
                </DialogActions>
            </Dialog>
        </>
    )
}
