import React from "react";
import { Box, Button, Divider, Grid, Stack, Typography } from "@mui/material";
import AutoCreateOrJoinSameDomainGroup from "./AutoCreateOrJoinSameDomainGroup";
import GroupFilterOutPinnedTabs from "./GroupFilterOutPinnedTabs";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import UseBookmarkFolderNameAsGroupName from "./UseBookmarkFolderNameAsGroupName";
import GroupSelectBookmarkFolderLevel from "./GroupSelectBookmarkFolderLevel";
import CustomGroupNames from "./CustomGroupNames";
import CustomGroupRemember from "./CustomGroupRemember";
import { resetTabGroupsSettings } from "../../../../core/startup";
import { toast } from "react-toastify";
import AutoActivateTabGrouping from "./AutoActivateTabGrouping";
import AutoActivateTabGroupingName from "./AutoActivateTabGroupingName";
import ChromeTabGroupsCollapse from "./ChromeTabGroupsCollapse";


export default function ChromeTabGroup() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.chromeTabGroupAdvanced' });

    const handleResetSettings = async () => {
        await resetTabGroupsSettings();
    };


    return (
        <Box>
            <Grid container mb={5}>
                <Grid item xs={8}><Typography variant="h1">{t("label")}</Typography></Grid>
                <Grid item xs={4}>
                    <Button color="error" onClick={handleResetSettings}>{t('resetCurrentPageSettings', { keyPrefix: "settings" })}</Button>
                </Grid>

            </Grid>
            <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <CustomGroupNames />
                    <Divider sx={{ my: 1 }} />
                    <CustomGroupRemember />
                </Box>
                <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <AutoActivateTabGrouping />
                    <Divider sx={{ my: 1 }} />
                    <AutoActivateTabGroupingName />
                </Box>
                <Box sx={{ padding: "10px", border: "1px solid #eee", borderRadius: "10px" }}>
                    <UseBookmarkFolderNameAsGroupName />
                    <Divider sx={{ my: 1 }} />
                    <GroupSelectBookmarkFolderLevel />
                </Box>

                <AutoCreateOrJoinSameDomainGroup />
                <GroupFilterOutPinnedTabs />
                <ChromeTabGroupsCollapse />

            </Stack>

        </Box>
    )

}