import React, { useContext } from "react";
import { Box, Divider, FormControlLabel, Grid, Switch, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { userStore } from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import Markdown from 'react-markdown'
import remarkGfm from 'remark-gfm'

export default function SubtitleDetection() {

    const { t } = useTranslationContext('options', { keyPrefix: 'settings.picture_in_picture.subtitleDetection' });

    const { updateSetting, advancedPipEnable, subtitleDetection } = userStore((state) => ({
        updateSetting: state.updateSetting,
        advancedPipEnable: state.settings.advancedPipEnable,
        subtitleDetection: state.settings.subtitleDetection,
    }));


    const handleChange = async (event) => {
        await updateSetting('subtitleDetection', event.target.checked)
        await sendRuntimeMessage("update_setting", { key: "subtitleDetection", value: event.target.checked })
    }

    return (

        <Grid container alignItems="center" >
            <Grid item xs={8} sx={{ paddingRight: "8px" }}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>
                    <Markdown remarkPlugins={[remarkGfm]}>{t("description")}</Markdown >
                </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" disabled={!advancedPipEnable} checked={subtitleDetection}
                    onChange={handleChange} />}
                    labelPlacement="end"
                />
            </Grid>
        </Grid>

    )

}