import React from "react";

import { Autocomplete, Grid, TextField, Typography } from "@mui/material";
import { supportLanguages } from "../../../../utils/i18n";
import { useTranslationContext } from "../../../Providers/TranslationProvider";




export default function Language() {

    const { t, language, changeLanguage } = useTranslationContext({ keyPrefix: "settings.appearance.language" });


    // const { updateSetting, selectedLanguage } = userStore((state) => ({
    //     updateSetting: state.updateSetting,
    //     selectedLanguage: state.settings.selectedLanguage,
    // }));

    // console.log("Language", selectedLanguage)

    const selectedValues = () => supportLanguages.find((v) => language.indexOf(v.lang) > -1);

    const handleChangeSelectedLanguage = async (e, v) => {
        if (v && v.lang) {
            changeLanguage(v.lang)
        }
    }


    return (
        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1} >{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}><Autocomplete
                options={supportLanguages}
                getOptionKey={(option) => option?.lang}
                defaultValue={selectedValues}
                // getOptionLabel={(option) => option.label}
                onChange={handleChangeSelectedLanguage}
                renderInput={(params) => <TextField {...params} />}
            /></Grid>
        </Grid>
    )

}