import React, { useContext } from "react";
import { userStore } from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { FormControlLabel, Grid, Switch, Typography, Alert } from "@mui/material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { toast } from "react-toastify";



export default function TabPreviewImage() {
    const { t } = useTranslationContext("options", { keyPrefix: "settings.tabs.previewWithThumbnail" });
    const { updateSetting, tabPreviewEnable, tabPreviewImage } = userStore((state) => ({
        updateSetting: state.updateSetting,
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewImage: state.settings.tabPreviewImage

    }));


    const handleTabPreviewImageChange = async (event) => {

        const val = event.target.checked
        if (val) {
            chrome.permissions.contains({ origins: ["<all_urls>"] }, async (granted) => {
                if (!granted) {
                    chrome.permissions.request({ origins: ["<all_urls>"] }, async (granted) => {
                        if (!granted) {
                            await updateSetting("tabPreviewImage", false)
                            await sendRuntimeMessage("update_setting", { key: "tabPreviewImage", value: false });
                            toast.error(t("deniedMessage"));
                        } else {
                            await updateSetting("tabPreviewImage", true)
                            await sendRuntimeMessage("update_setting", { key: "tabPreviewImage", value: true });
                        }
                    })
                } else {
                    await updateSetting("tabPreviewImage", val)
                    await sendRuntimeMessage("update_setting", { key: "tabPreviewImage", value: val })
                }
            })
        } else {
            await updateSetting('tabPreviewImage', event.target.checked)
            await sendRuntimeMessage("update_setting", { key: "tabPreviewImage", value: event.target.checked })
        }


    }



    return (

        <Grid container alignItems="center" >
            <Grid item xs={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={4}>
                <FormControlLabel control={<Switch color="error" checked={tabPreviewImage && tabPreviewEnable}
                    onChange={handleTabPreviewImageChange} />}
                    labelPlacement="start"

                />
            </Grid>
            <Grid item xs={12}>
                <Alert severity="warning">
                    <Typography component="p">{t('alert')} </Typography>
                </Alert>
            </Grid>
        </Grid>

    )

}
